import { NumberComponent, SymbolComponent } from "./symbolComponent";
import * as Libs from "Libs/";
import { MathDAD } from "Libs/";
import { INumberObject } from "GameObjects";

export class SubtractionComponent extends SymbolComponent
{
    public static readonly componentName = "SymbolSubtractionComponet";
    public static readonly inheritedComponentNames = [SymbolComponent];

    protected _symbolName = "Subtraction";

    public priority: number = 1;

    public calc(left: MathDAD.XNumberValue, right: MathDAD.XNumberValue | undefined): { numberValue: number, isX: boolean }
    {
        let numberValue;
        let isX;
        if (right)
        {
            numberValue = left.numberValue - right.numberValue;
            isX = left.isX || right.isX;
        }
        else
        {
            numberValue = 0 - left.numberValue;
            isX = left.isX;
        }

        return { numberValue, isX };
    }

    public getSymbolsNumbers(numberValues: Libs.ValueIndex<INumberObject>[], symbolIndex: number):
        { right?: Libs.ValueIndex<INumberObject>, left?: Libs.ValueIndex<INumberObject> }
    {
        let returnValue = super.getSymbolsNumbers(numberValues, symbolIndex);

        if (returnValue.left && returnValue.left.value.isX &&
            returnValue.right && returnValue.right.value.numberValue !== 0)
        {
            returnValue.left = undefined;
        }

        return returnValue;
    }

    public isCalcValid(left: MathDAD.XNumberValue, right: MathDAD.XNumberValue): boolean
    {
        return true;
    }
}


