export class CallbackCounter
{
    private amount = 0;
    private _amountToReach: number;
    public get amountToReach() { return this._amountToReach; }
    public set amountToReach(value: number)
    {
        this._amountToReach = value;
        if (this._amountToReach < 1)
            this.callback.call(this.context);
    }

    public CountUp()
    {
        this.amount += 1;
        if (this.amountToReach === this.amount)
        {
            this.callback.call(this.context);
        }
    }

    constructor(amountToReach: number, private callback: Function, private context?: any)
    {
        this._amountToReach = amountToReach;
        if (this._amountToReach < 1)
            this.callback.call(this.context);
    }
}


