export function removeFromArray<T = any>(array: T[], objectToRemove: T): boolean
{
    return templateRemoveFromArray.call(array, objectToRemove);
}

export function exitsInArray<T = any>(array: T[], objectToFind: T): boolean
{
    return templateExitsInArray.call(array, objectToFind);
}

export function cloneArray<T = any>(array: T[]): T[]
{
    return templateCloneArray.call(array);
}

export function findMinElement<T = any>(array: T[], numberFunc: (obj: T) => number, preferNewer = false): T | undefined
{
    if (array.length > 0)
    {
        let minIndex = 0;
        let minValue = numberFunc(array[0]);
        for (let index = 1; index < array.length; index++)
        {
            let newValue = numberFunc(array[index]);
            if (minValue > newValue || (preferNewer && minValue === newValue))
            {
                minIndex = index;
                minValue = newValue;
            }
        }

        return array[minIndex];
    }
}

export function findMaxElement<T = any>(array: T[], numberFunc: (obj: T) => number, preferNewer = false): T | undefined
{
    if (array.length > 0)
    {
        let maxIndex = 0;
        let maxValue = numberFunc(array[0]);
        for (let index = 1; index < array.length; index++)
        {
            let newValue = numberFunc(array[index]);
            if (maxValue < newValue || (preferNewer && maxValue === newValue))
            {
                maxIndex = index;
                maxValue = newValue;
            }
        }

        return array[maxIndex];
    }
}

function templateRemoveFromArray<T>(this: T[], objectToRemove: T): boolean
{
    let index = this.indexOf(objectToRemove);
    if (index !== -1)
    {
        this.splice(index, 1);
        return true;
    }

    return false;
}

function templateExitsInArray<T>(this: T[], objectToFind: T): boolean
{
    return this.indexOf(objectToFind) !== -1;
}

function templateCloneArray<T = any>(this: T[]): T[]
{
    return this.slice(0);
}

export function improveArray<T = any>(): ImprovedArray<T>;
export function improveArray<T = any>(arrayLength: number): ImprovedArray<T>;
export function improveArray<T = any>(array: Array<T>): ImprovedArray<T>;
export function improveArray<T = any>(...items: T[]): ImprovedArray<T>
{
    let newArray: ImprovedArray<T>;
    if (arguments.length === 1)
    {
        if (isImproved(arguments[0]))
            return (arguments[0]);
        let argType = typeof arguments[0];
        if (argType === "object")
        {
            if (arguments[0].constructor === Array)
            {


                newArray = arguments[0];
            }
            else
            {
                newArray = new Array(arguments[0]) as ImprovedArray;
            }

        }
        else
        {
            newArray = new Array(arguments[0]) as ImprovedArray;
        }
    }
    else
    {
        newArray = [...items] as ImprovedArray;
    }

    newArray.remove = templateRemoveFromArray;
    newArray.has = templateExitsInArray;
    newArray.clone = templateCloneArray;

    return newArray;
}

export function isImproved<T>(array: Array<T>): boolean
{
    return arguments[0].remove === templateRemoveFromArray;
}

export function isModifiedByOthers<T>(array: Array<T>): boolean
{
    return (arguments[0].remove && !isImproved(arguments[0]));
}


export interface ImprovedArray<T = any> extends Array<T>
{
    remove(objectToRemove: T): boolean;
    has(objectToFind: T): boolean;
    clone(): T[];
}

export function stringArrayCompare(arrayOne: string[], arrayTwo: string[]): boolean
{
    if (arrayOne.length !== arrayTwo.length)
        return false;

    arrayOne.sort();
    arrayTwo.sort();

    for (let index = 0; index < arrayOne.length; index++)
    {
        if (arrayOne[index] !== arrayTwo[index])
        {
            return false;
        }
    }

    return true;
}



