import { game } from "main";

export function setToCurrentDrag(sprite: Phaser.Sprite, atPosition?: { x, y })
{
    let { activePointer } = game.input;
    let { x, y } = atPosition || activePointer;
    sprite.position.setTo(x, y);
    activePointer.swapTarget(sprite.input);
    sprite.input.startDrag(activePointer);
}


