import * as Libs from "Libs/";
import
{
    IComponentAbleSprite, IPhaserSpriteBehaviorConstructor,
    PhaserSpriteBehaviorWithEvents, InterfaceComponentAbleType,
    StateComponent
} from "./types";
import { PhaserSpriteBehavior } from "./phaserSpriteBehavior";
import { StateComponentClass } from "./stateComponent";

type SpriteConstructor = Constructor<Phaser.Sprite>;
type ComponentAbleSpriteConstructor = SpriteConstructor & Constructor<IComponentAbleSprite>;

interface IPhaserSpriteBehaviorDirectory
{
    [index: string]: PhaserSpriteBehaviorWithEvents[];
}

const componentAbleSpriteClassCacheMap = new Libs.MapProxy<Constructor<Phaser.Sprite>, ComponentAbleSpriteConstructor>();

export function makeComponentAbleSprite<T extends Constructor<Phaser.Sprite>>(base: T): T & Constructor<IComponentAbleSprite>
{
    if (componentAbleSpriteClassCacheMap.has(base))
    {
        return componentAbleSpriteClassCacheMap.get(base) as (T & Constructor<IComponentAbleSprite>);
    }

    let newComponentAble = class extends base implements IComponentAbleSprite
    {
        private _startQueue: Array<PhaserSpriteBehaviorWithEvents>;
        private _updateArray: Array<PhaserSpriteBehaviorWithEvents>;
        private _phaserComponents: IPhaserSpriteBehaviorDirectory;

        public events: Phaser.Events & { onUpdate: Phaser.Signal };

        constructor(...args: any[])
        {
            super(...args);
            this._startQueue = new Array<PhaserSpriteBehavior>();
            this._updateArray = new Array<PhaserSpriteBehavior>();
            this._phaserComponents = {};
            this.events.onUpdate = new Phaser.Signal();
            this.events.onDestroy.addOnce(this.onComponentAbleDestroy, this);
        }

        public addComponent<T extends PhaserSpriteBehavior>(
            componentClassOrNames: IPhaserSpriteBehaviorConstructor<T> | string | string[],
            component: PhaserSpriteBehaviorWithEvents | any, ...args: any[]): T
        {
            let newComponent: PhaserSpriteBehaviorWithEvents & T;
            if (Libs.isArray(componentClassOrNames))
            {
                newComponent = component;
                for (let name of componentClassOrNames)
                {
                    if (this._phaserComponents[name])
                    {
                        this._phaserComponents[name].push(newComponent);
                    }
                    else
                    {
                        this._phaserComponents[name] = [newComponent];
                    }
                }
            }
            else if (Libs.isString(componentClassOrNames))
            {
                newComponent = component;
                if (this._phaserComponents[componentClassOrNames])
                {
                    this._phaserComponents[componentClassOrNames].push(newComponent);
                }
                else
                {
                    this._phaserComponents[componentClassOrNames] = [newComponent];
                }
            }
            else
            {
                args.unshift(component);
                newComponent = new componentClassOrNames(this, ...args);

                let componentNames = getComponentNames(componentClassOrNames);

                for (let name of componentNames)
                {
                    if (this._phaserComponents[name])
                    {
                        this._phaserComponents[name].push(newComponent);
                    }
                    else
                    {
                        this._phaserComponents[name] = [newComponent];
                    }
                }

            }

            this._startQueue.push(newComponent);
            this._updateArray.push(newComponent);
            return newComponent;
        }


        public createStateComponent(
            componentIdentifier: IPhaserSpriteBehaviorConstructor<any> |
                InterfaceComponentAbleType<any> | string | string[]): any | false
        {
            let newComponent;
            if (Libs.isArray(componentIdentifier))
            {
                newComponent = new StateComponentClass(this);
                for (let name of componentIdentifier)
                {
                    if (this._phaserComponents[name])
                    {
                        this._phaserComponents[name].push(newComponent);
                    }
                    else
                    {
                        this._phaserComponents[name] = [newComponent];
                    }
                }
            }
            else if (Libs.isString(componentIdentifier))
            {
                newComponent = new StateComponentClass(this);
                if (this._phaserComponents[componentIdentifier])
                {
                    this._phaserComponents[componentIdentifier].push(newComponent);
                }
                else
                {
                    this._phaserComponents[componentIdentifier] = [newComponent];
                }
            }
            else
            {
                newComponent = new StateComponentClass(this);
                let componentNames = getComponentNames(componentIdentifier);

                for (let name of componentNames)
                {
                    if (this._phaserComponents[name])
                    {
                        this._phaserComponents[name].push(newComponent);
                    }
                    else
                    {
                        this._phaserComponents[name] = [newComponent];
                    }
                }
            }
            this._updateArray.push(newComponent);
            return newComponent;
        }

        public getAllComponent(
            componentIdentifier: IPhaserSpriteBehaviorConstructor<any> |
                InterfaceComponentAbleType<any> | string): any[] | false
        {
            if (typeof componentIdentifier === "string")
            {
                if (this._phaserComponents[componentIdentifier])
                {
                    let oldArray = this._phaserComponents[componentIdentifier];
                    let newArray = Array(oldArray.length);

                    let i = newArray.length;

                    while (i--)
                    {
                        if (oldArray[i] instanceof StateComponentClass)
                            newArray[i] = (oldArray[i] as StateComponent<any>).currentComponent;
                        else
                            newArray[i] = oldArray[i];
                    }
                }
            }
            else
            {
                if (this._phaserComponents[componentIdentifier.componentName])
                {
                    let oldArray = this._phaserComponents[componentIdentifier.componentName];
                    let newArray = Array(oldArray.length);

                    let i = newArray.length;

                    while (i--)
                    {
                        if (oldArray[i] instanceof StateComponentClass)
                            newArray[i] = (oldArray[i] as StateComponent<any>).currentComponent;
                        else
                            newArray[i] = oldArray[i];
                    }
                }
            }
            return false;
        }

        public changeComponentState(
            componentIdentifier: IPhaserSpriteBehaviorConstructor<any> |
                InterfaceComponentAbleType<any> | string,
            componentState: any): any | false
        {
            if (typeof componentIdentifier === "string")
            {
                let comps = this._phaserComponents[componentIdentifier];
                if (comps && comps.length > 0)
                {
                    let comp = comps[0];
                    if (comp instanceof StateComponentClass)
                    {
                        return comp.SetState(componentState);
                    }
                }
            }
            else
            {
                let comps = this._phaserComponents[componentIdentifier.componentName];
                if (comps && comps.length > 0)
                {
                    let comp = comps[0];
                    if (comp instanceof StateComponentClass)
                    {
                        return comp.SetState(componentState);
                    }
                }
            }
            return false;
        }

        public getComponent(
            componentIdentifier: IPhaserSpriteBehaviorConstructor<any> |
                InterfaceComponentAbleType<any> | string): any | false
        {
            if (typeof componentIdentifier === "string")
            {
                let comps = this._phaserComponents[componentIdentifier];
                if (comps && comps.length > 0)
                {
                    let comp = comps[0];
                    if (comp instanceof StateComponentClass)
                    {
                        return comp.currentComponent;
                    }
                    return comp;
                }
            }
            else
            {
                let comps = this._phaserComponents[componentIdentifier.componentName];
                if (comps && comps.length > 0)
                {
                    let comp = comps[0];
                    if (comp instanceof StateComponentClass)
                    {
                        return comp.currentComponent;
                    }
                    return comp;
                }
            }
            return false;
        }

        public update()
        {
            this._startQueue.forEach((c) =>
            {
                if (c.start)
                    c.start();
            });

            this._startQueue.length = 0;

            this._updateArray.forEach((c) =>
            {
                if (c.update)
                    c.update();

            });

            this.events.onUpdate.dispatch();
            super.update();
        }

        protected onComponentAbleDestroy()
        {
            this._updateArray.forEach((c) =>
            {
                if (c.onRemove)
                    c.onRemove();
            });

            this._updateArray.length = 0;
            this._startQueue.length = 0;

            delete this._updateArray;
            delete this._startQueue;
            for (let componentName in this._phaserComponents)
            {
                this._phaserComponents[componentName].length = 0;
            }
            delete this._phaserComponents;
        }

    };

    componentAbleSpriteClassCacheMap.set(base, newComponentAble);

    return newComponentAble;
}

function getComponentNames(componentClass: IPhaserSpriteBehaviorConstructor | InterfaceComponentAbleType): string[]
{
    let backlog: (IPhaserSpriteBehaviorConstructor | InterfaceComponentAbleType)[] = [componentClass];
    let toDoList: (IPhaserSpriteBehaviorConstructor | InterfaceComponentAbleType)[] = [componentClass];
    let names: string[] = [];
    for (let newComponentClass = toDoList.pop(); newComponentClass; newComponentClass = toDoList.pop())
    {
        if (!Libs.exitsInArray(names, newComponentClass.componentName))
        {
            names.push(newComponentClass.componentName);
        }

        if (newComponentClass.inheritedComponentNames === undefined)
            continue;

        for (let containComponentClass of newComponentClass.inheritedComponentNames)
        {
            if (!Libs.exitsInArray(backlog, containComponentClass))
            {
                backlog.push(containComponentClass);
                toDoList.push(containComponentClass);
            }
        }
    }

    return names;
}


