import
{
    DragAbleSymbolObject, INumberObject, WinSymbolObject, ISymbolObject,
    DropAreaObject, ParenthesisPair, CompositeObject
} from "GameObjects/";
import { IComponentAbleSprite } from "Components";
import { SpriteArray } from "Libs/Phaser/";
import * as Libs from "Libs/";
import { game } from "main";
import * as Comp from "Components";


export type BeginAnimationFunc = (callback: Function, animationArgs: BaseAnimationArgs) => void;

export type ProcessBeginAnimationFunc = (callback: Function, animationArgs: ProcessBeginArgs) => void;

export type ProcessEndAnimationFunc = (callback: Function, animationArgs: ProcessEndArgs) => void;

export type ParenthesisDoneAnimation = (callback: Function, animationArgs: ProcessEndArgs) => void;

export type LoseAnimationFunc = (callback: Function, animationArgs: ProcessEndArgs) => void;

export type WinAnimationFunc = (callback: Function, animationArgs: ProcessEndArgs) => void;

export type SplitAnimationFunc = (callback: Function, animationArgs: SplitArgs) => void;

export type LoseEquationAnimationFunc = (callback: Function, animationArgs: BaseAnimationArgs) => void;

export type EdgeCaseRemoveZeroAnimationFunc = (callback: Function, animationArgs: EdgeCaseRemoveZeroArgs) => void;

export type EdgeCaseSpawnSymbolAnimationFunc = (callback: Function, animationArgs: EdgeCaseSpawnSymbolArgs) => void;

export let beginAnimationFunc: BeginAnimationFunc;
export let processBeginAnimationFunc: ProcessBeginAnimationFunc;
export let processEndAnimationFunc: ProcessEndAnimationFunc;
export let loseAnimationFunc: LoseAnimationFunc;
export let winAnimationFunc: WinAnimationFunc;
export let parenthesisDoneAnimationFunc: ParenthesisDoneAnimation;
export let splitAnimationFunc: SplitAnimationFunc;
export let loseEquationAnimationFunc: LoseEquationAnimationFunc;
export let edgeCaseRemoveZeroAnima: EdgeCaseRemoveZeroAnimationFunc;
export let edgeCaseSpawnSymbolAnima: EdgeCaseSpawnSymbolAnimationFunc;


function defaultBeginAnimationFunc(callback: Function, animationArgs: BaseAnimationArgs): void
{
    let { dropAreas } = animationArgs;

    dropAreas.forEach((dropArea) =>
    {
        let tween = dropArea.game.add.tween(dropArea).
            to({ alpha: 0 }, 1000, Phaser.Easing.Exponential.Out, true);
        tween.onComplete.addOnce(animationFin);
    });

    let count = 0;
    function animationFin()
    {
        count++;
        if (count < dropAreas.length)
            return;
        game.time.events.add(300, callback);
    }
}
beginAnimationFunc = defaultBeginAnimationFunc;

function defaultProcessBeginAnimationFunc(callback: Function, animationArgs: ProcessBeginArgs): void
{
    let { currentSymbol, right, left } = animationArgs;

    let { x, y } = currentSymbol.value;
    let count = 0;
    game.add.tween(currentSymbol.value).
        to({ alpha: 0 }, 600, Phaser.Easing.Exponential.Out, true).
        onComplete.addOnce(animationFin);
    if (right)
        game.add.tween(right.value).
            to({ alpha: 0, x, y }, 600, Phaser.Easing.Exponential.Out, true).
            onComplete.addOnce(animationFin);
    else
        count++;

    if (left)
        game.add.tween(left.value).
            to({ alpha: 0, x, y }, 600, Phaser.Easing.Exponential.Out, true).
            onComplete.addOnce(animationFin);
    else
        count++;



    function animationFin()
    {
        count++;
        if (count < 3)
            return;
        callback();
    }
}
processBeginAnimationFunc = defaultProcessBeginAnimationFunc;

function defaultProcessEndAnimationFunc(callback: Function, animationArgs: ProcessEndArgs): void
{
    let { newNumber } = animationArgs;

    newNumber.value.alpha = 0;

    game.add.tween(newNumber.value).
        to({ alpha: 1 }, 200, Phaser.Easing.Exponential.Out, true).
        onComplete.addOnce(animationFin);

    function animationFin()
    {
        game.time.events.add(700, callback);
    }
}
processEndAnimationFunc = defaultProcessEndAnimationFunc;

function defaultLoseAnimationFunc(callback: Function, animationArgs: ProcessEndArgs)
{
    let { newNumber, winSymbol, dropAreas } = animationArgs;

    let originalWinSymbolPosition = winSymbol.position.clone();
    let originalWinSymbolTint = winSymbol.tint;
    let originalNewNumberTint = newNumber.value.tint;

    let tintColor = 15073280;

    let centerX = game.world.centerX;
    let moveTween = game.add.tween(newNumber.value).
        to({ x: centerX }, 600, Phaser.Easing.Elastic.Out);

    let moveTweenNumberValue = game.add.tween(winSymbol).to(
        { left: newNumber.value.right - 50 }, 600, Phaser.Easing.Exponential.In);

    let tintTween = Libs.Phaser.tweenTint([newNumber.value, winSymbol], winSymbol.tint, tintColor, 500);

    tintTween.onComplete.addOnce(secondAnimationStage);

    moveTween.chain(moveTweenNumberValue, tintTween).start();

    function secondAnimationStage()
    {
        let callBackCounter = new Libs.CallbackCounter(2, thirdAnimationStage);

        let moveBackTween = game.add.tween(winSymbol).
            to(originalWinSymbolPosition, 600, Phaser.Easing.Exponential.In);

        let numberFadeOut = game.add.tween(newNumber.value).
            to({ alpha: 0 }, 400);

        moveBackTween.start().onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
        numberFadeOut.start().onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
    }

    function thirdAnimationStage()
    {
        let callBackCounter = new Libs.CallbackCounter(dropAreas.length + 1, animationFin);

        newNumber.value.tint = originalNewNumberTint;

        Libs.Phaser.tweenTint([winSymbol], winSymbol.tint, originalWinSymbolTint, 1000).
            start().onComplete.addOnce(callBackCounter.CountUp, callBackCounter);

        dropAreas.forEach((dropArea) =>
        {
            let tween = dropArea.game.add.tween(dropArea).
                to({ alpha: 1 }, 1000, Phaser.Easing.Linear.None, true);
            tween.onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
        });
    }

    function animationFin()
    {
        game.time.events.add(300, callback);
    }
}

loseAnimationFunc = defaultLoseAnimationFunc;


function defaultWinAnimationFunc(callback: Function, animationArgs: ProcessEndArgs)
{
    let { newNumber, winSymbol } = animationArgs;


    let tintColor = 58880;

    let centerX = game.world.centerX;
    let moveTween = game.add.tween(newNumber.value).
        to({ x: centerX }, 600, Phaser.Easing.Elastic.Out);

    let moveTweenNumberValue = game.add.tween(winSymbol).to(
        { left: newNumber.value.right - 50 }, 600, Phaser.Easing.Exponential.In);

    let tintTween = Libs.Phaser.tweenTint([newNumber.value, winSymbol], winSymbol.tint, tintColor, 500);

    let scaleTween = game.add.tween(newNumber.value.scale).
        to({ x: 2, y: 2 }, 600, Phaser.Easing.Exponential.InOut, false, 30).yoyo(true);

    scaleTween.onComplete.addOnce(animationFin);
    moveTween.chain(moveTweenNumberValue, tintTween, scaleTween).start();

    function animationFin()
    {
        game.time.events.add(300, callback);
    }
}

winAnimationFunc = defaultWinAnimationFunc;

function defaultParenthesisDoneAnimationFunc(callback: Function, animationArgs: ProcessEndArgs)
{
    if (animationArgs.currentParenthesisPair)
    {
        let { parenthesisLeft, parenthesisRight } = animationArgs.currentParenthesisPair;
        let callBackCounter = new Libs.CallbackCounter(2, callback);

        game.add.tween(parenthesisLeft).to({ alpha: 0 }, 333, Phaser.Easing.Exponential.In).start().
            onComplete.addOnce(callBackCounter.CountUp, callBackCounter);

        game.add.tween(parenthesisRight).to({ alpha: 0 }, 333, Phaser.Easing.Exponential.In).start().
            onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
    }
    else
    {
        callback();
    }
}

parenthesisDoneAnimationFunc = defaultParenthesisDoneAnimationFunc;

function defaultSplitAnimationFunc(callback: Function, animationArgs: SplitArgs)
{
    let { compositesAndPartsIndex } = animationArgs;
    compositesAndPartsIndex.forEach(element =>
    {
        let { compositeObject, numberObject, symbolObject } = element.value;
        numberObject.position.copyFrom(compositeObject.position);
        symbolObject.position.copyFrom(compositeObject.position);
    });
    callback();
}

splitAnimationFunc = defaultSplitAnimationFunc;


function defaultLoseEquationAnimationFunc(callback: Function, animationArgs: BaseAnimationArgs)
{
    let { dropAreas, otherNumberValues, otherSymbolValues } = animationArgs;
    let objectsInDropArea: IComponentAbleSprite[] = [];
    for (let currentDropArea of dropAreas)
    {
        if (currentDropArea.objectInArea)
        {
            objectsInDropArea.push(currentDropArea.objectInArea);
        }
    }

    let callBackCounter = new Libs.CallbackCounter(
        otherNumberValues.length + otherSymbolValues.length + dropAreas.length, callback);

    for (let currentNumberValue of otherNumberValues)
    {
        if (!Libs.exitsInArray(objectsInDropArea, currentNumberValue.value))
        {
            let tween = game.add.tween(currentNumberValue.value).
                to({ alpha: 0 }, 1000, Phaser.Easing.Linear.None, true);
            tween.onComplete.addOnce(callback);
            tween.start();
        }
        else
        {
            callBackCounter.CountUp();
        }
    }

    for (let currentSymbolValue of otherSymbolValues)
    {
        if (!Libs.exitsInArray(objectsInDropArea, currentSymbolValue.value))
        {
            let tween = game.add.tween(currentSymbolValue.value).
                to({ alpha: 0 }, 1000, Phaser.Easing.Linear.None, true);
            tween.onComplete.addOnce(callback);
            tween.start();
        }
        else
        {
            callBackCounter.CountUp();
        }
    }

    for (let dropArea of dropAreas)
    {
        if (dropArea.objectInArea)
        {
            if (dropArea.objectInArea.getComponent(Comp.DragAndDropComponent))
            {
                let tween = game.add.tween(dropArea).
                    to({ alpha: 1 }, 1000, Phaser.Easing.Linear.None, true);
                tween.onComplete.addOnce(callback);
                tween.start();

                continue;
            }
        }

        callBackCounter.CountUp();
    }
}

loseEquationAnimationFunc = defaultLoseEquationAnimationFunc;


function defaultEdgeCaseRemoveZero(callback: Function, animationArgs: EdgeCaseRemoveZeroArgs)
{
    let { numberObjectZero } = animationArgs;

    game.add.tween(numberObjectZero).
        to({ alpha: 0 }, 450, Phaser.Easing.Exponential.InOut, true).
        onComplete.addOnce(callback);
}

edgeCaseRemoveZeroAnima = defaultEdgeCaseRemoveZero;

function defaultEdgeCaseSpawnSymbolAnima(callback: Function, animationArgs: EdgeCaseSpawnSymbolArgs)
{
    let { otherNumberValues, rightSideStartIndex, newSymbolObject } = animationArgs;

    if (!rightSideStartIndex)
    {
        callback();
        return;
    }

    let rightSideNumber = otherNumberValues.filter(i => i.index < (rightSideStartIndex as number));

    let midPoint = Libs.Phaser.findMidPoint(rightSideNumber[0].value, rightSideNumber[1].value);

    newSymbolObject.position.setTo(midPoint.x, midPoint.y);
    newSymbolObject.alpha = 0;

    game.add.tween(newSymbolObject).
        to({ alpha: 1 }, 450, Phaser.Easing.Exponential.InOut, true).
        onComplete.addOnce(callback);
}


edgeCaseSpawnSymbolAnima = defaultEdgeCaseSpawnSymbolAnima;


export function setBeginAnimationFunc(newAnimationFunc?: BeginAnimationFunc)
{
    if (newAnimationFunc)
    {
        beginAnimationFunc = newAnimationFunc;
    }
    else
    {
        beginAnimationFunc = defaultBeginAnimationFunc;
    }

}
export function setProcessBeginAnimationFunc(newAnimationFunc?: ProcessBeginAnimationFunc)
{
    if (newAnimationFunc)
    {
        processBeginAnimationFunc = newAnimationFunc;
    }
    else
    {
        processBeginAnimationFunc = defaultProcessBeginAnimationFunc;
    }

}

export function setProcessEndAnimationFunc(newAnimationFunc?: ProcessEndAnimationFunc)
{
    if (newAnimationFunc)
    {
        processEndAnimationFunc = newAnimationFunc;
    }
    else
    {
        processEndAnimationFunc = defaultProcessEndAnimationFunc;
    }

}

export function setLoseAnimationFunc(newAnimationFunc?: LoseAnimationFunc)
{
    if (newAnimationFunc)
    {
        loseAnimationFunc = newAnimationFunc;
    }
    else
    {
        loseAnimationFunc = defaultLoseAnimationFunc;
    }
}

export function setWinAnimationFunc(newAnimationFunc?: WinAnimationFunc)
{
    if (newAnimationFunc)
    {
        winAnimationFunc = newAnimationFunc;
    }
    else
    {
        winAnimationFunc = defaultWinAnimationFunc;
    }
}

export function setParenthesisDoneAnimationFunc(newAnimationFunc?: ParenthesisDoneAnimation)
{
    if (newAnimationFunc)
    {
        parenthesisDoneAnimationFunc = newAnimationFunc;
    }
    else
    {
        parenthesisDoneAnimationFunc = defaultParenthesisDoneAnimationFunc;
    }
}

export function setSplitAnimationFunc(newAnimationFunc?: SplitAnimationFunc)
{
    if (newAnimationFunc)
    {
        splitAnimationFunc = newAnimationFunc;
    }
    else
    {
        splitAnimationFunc = defaultSplitAnimationFunc;
    }
}

export function setLoseEquationAnimationFunc(newAnimationFunc?: LoseEquationAnimationFunc)
{
    if (newAnimationFunc)
    {
        loseEquationAnimationFunc = newAnimationFunc;
    }
    else
    {
        loseEquationAnimationFunc = defaultLoseEquationAnimationFunc;
    }
}

export function setEdgeCaseRemoveZeroAnimationFunc(newAnimationFunc?: EdgeCaseRemoveZeroAnimationFunc)
{
    if (newAnimationFunc)
    {
        edgeCaseRemoveZeroAnima = newAnimationFunc;
    }
    else
    {
        edgeCaseRemoveZeroAnima = defaultEdgeCaseRemoveZero;
    }
}

export interface BaseAnimationArgs
{
    otherNumberValues: Libs.ValueIndex<INumberObject>[];
    otherSymbolValues: Libs.ValueIndex<ISymbolObject>[];
    otherParenthesisPair: ParenthesisPair[];
    rightSideStartIndex: number | undefined;
    dropAreas: DropAreaObject[];
    winSymbol: WinSymbolObject;
}

export interface ProcessBeginArgs extends BaseAnimationArgs
{
    currentSymbol: Libs.ValueIndex<ISymbolObject>;
    right?: Libs.ValueIndex<INumberObject>;
    left?: Libs.ValueIndex<INumberObject>;
    currentParenthesisPair: ParenthesisPair | null;
}

export interface ProcessEndArgs extends BaseAnimationArgs
{
    newNumber: Libs.ValueIndex<INumberObject>;
    currentParenthesisPair: ParenthesisPair | null;
}

export type CompositeAndParts = { compositeObject: CompositeObject, numberObject: INumberObject, symbolObject: ISymbolObject };

export interface SplitArgs
{
    compositesAndPartsIndex: Libs.ValueIndex<CompositeAndParts>[];
}

export interface EdgeCaseRemoveZeroArgs extends BaseAnimationArgs
{
    numberObjectZero: INumberObject;
}

export interface EdgeCaseSpawnSymbolArgs extends BaseAnimationArgs
{
    newSymbolObject: ISymbolObject;
}


