export let MapProxy: MapProxyConstructor;
if (Map)
{
    class MapProxyTrue<K = any, V = any> implements MapProxy
    {
        private _map: Map<K, V>;

        public clear(): void
        {
            this._map.clear();
        }

        public delete(key: K): boolean
        {
            return this._map.delete(key);
        }

        public forEach(callbackfn: (value: V, index: K, map: MapProxy<K, V>) => void, thisArg?: any): void
        {
            this._map.forEach(callbackfn);
        }

        public get(key: K): V | undefined
        {
            return this._map.get(key);
        }

        public has(key: K): boolean
        {
            return this._map.has(key);
        }

        public set(key: K, value?: V): MapProxy<K, V>
        {
            this._map.set(key, value);
            return this;
        }

        public get size(): number
        {
            return this._map.size;
        }

        constructor()
        {
            this._map = new Map<K, V>();
        }
    }

    MapProxy = MapProxyTrue;
}
else
{
    class MapProxyFalse<K = any, V = any> implements MapProxy
    {

        private _seekIndex: K[];
        private _seekValues: (V | undefined)[];

        public clear(): void
        {
            this._seekIndex.length = 0;
            this._seekValues.length = 0;
        }

        public delete(key: K): boolean
        {
            let index = this._seekIndex.indexOf(key);
            if (index !== -1)
            {
                this._seekIndex.splice(index, 1);
                this._seekValues.splice(index, 1);
                return true;
            }
            else
            {
                return false;
            }
        }

        public forEach(callbackfn: (value: V, index: K, map: MapProxy<K, V>) => void, thisArg?: any): void
        {
            for (let index = 0; index < this._seekIndex.length; index++)
            {
                callbackfn.call(thisArg, this._seekValues[index], this._seekIndex[index]);
            }
        }

        public get(key: K): V | undefined
        {
            let index = this._seekIndex.indexOf(key);
            if (index !== -1)
            {
                return this._seekValues[index];
            }
            else
            {
                return undefined;
            }
        }

        public has(key: K): boolean
        {
            return this._seekIndex.indexOf(key) !== -1;
        }

        public set(key: K, value?: V): MapProxyFalse<K, V>
        {
            let index = this._seekIndex.indexOf(key);
            if (index !== -1)
            {
                this._seekIndex.push(key);
                this._seekValues.push(value);
            }
            else
            {
                this._seekValues[index] = value;
            }

            return this;
        }

        public get size(): number
        {
            return this._seekIndex.length;
        }

        constructor()
        {
            this._seekIndex = [];
            this._seekValues = [];
        }
    }
    MapProxy = MapProxyFalse;
}

export interface MapProxy<K = any, V = any>
{
    clear(): void;
    delete(key: K): boolean;
    forEach(callbackfn: (value: V, index: K, map: MapProxy<K, V>) => void, thisArg?: any): void;
    get(key: K): V |undefined;
    has(key: K): boolean;
    set(key: K, value?: V): MapProxy<K, V>;
    readonly size: number;
}

export interface MapProxyConstructor
{
    new <K, V>(): MapProxy<K, V>;
    readonly prototype: MapProxy<any, any>;
}


