import { SymbolText, DadSpawnComponent } from "Components";
import { CompositeObject } from "./compositeObject";
import * as Libs from "Libs";
import { numberOverlapLayer, dropAreaOverlapLayer } from "GameObjects";
import { MainState } from "States/mainState";

export class SpawnCompositeObject extends CompositeObject
{
    private _dadSpawnComponent: DadSpawnComponent;
    public get dadSpawnComponent() { return this._dadSpawnComponent; }

    constructor(game: Phaser.Game, x: number, y: number, symbolText: SymbolText,
        numberValue: number, symbolGroup: Phaser.Group, numberGroup: Phaser.Group)
    {
        super(game, x, y, symbolText, numberValue, symbolGroup, numberGroup);
        this._dadSpawnComponent = this.addComponent(DadSpawnComponent, [numberOverlapLayer],
            [dropAreaOverlapLayer]);
    }
}


