export class BackButtonGameSelect extends Phaser.Sprite
{
    constructor(game: Phaser.Game, x: number, y: number, key: string)
    {
        super(game, x, y, key, 0);

        this.inputEnabled = true;

        this.events.onInputOver.add(this._onInputOver, this);
        this.events.onInputOut.add(this._onInputOut, this);
        this.events.onInputDown.add(this._onInputDown, this);
    }

    private _onInputOver(): void
    {
        this.tint = 0xd3d3d3;
    }

    private _onInputOut(): void
    {
        this.tint = 0xFFFFFF;
    }

    private _onInputDown(): void
    {
        this.game.load.baseURL = "";
        this.game.state.start("gameSelect");
    }
}


