import { ComponentAbleSprite } from "Components";
import { TreeParticleObject } from "GameObjects/Particles";
import { game } from "main";

export const mathPlantVariants = ["mathplant_1", "mathplant_2", "mathplant_3", "mathplant_4"];

export class MathPlant extends Phaser.Sprite {

    public static readonly doodadName = "mathPlant";

    public treeGrowParticle: Phaser.Particles.Arcade.Emitter;

    public static loadAssets(game: Phaser.Game) {
        // the assets to load for this actor like
        game.load.spritesheet(mathPlantVariants[0], "images/mathman/World1/tree1.png", 299, 250, 13);
        game.load.spritesheet(mathPlantVariants[1], "images/mathman/World1/tree2.png", 299, 250, 13);
        game.load.spritesheet(mathPlantVariants[2], "images/mathman/World1/tree3.png", 299, 250, 13);
        game.load.spritesheet(mathPlantVariants[3], "images/mathman/World1/tree4.png", 299, 250, 13);
        TreeParticleObject.loadAssets(game);
    }

   public growPlant(growAmount: number, callback?: Function) {

            let targetFrame = growAmount + this.animations.frame;
            let tween;

            this.animations.frame = this.animations.frame + 1;

            if (this.animations.frame > 0 && this.animations.frame < 3){
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.03, y: this.scale.y + 0.13 }, 200, Phaser.Easing.Circular.Out, true, undefined, 0, true);
            }

            else if (this.animations.frame === 3){
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.13, y: this.scale.y + 0.03 }, 200, Phaser.Easing.Bounce.Out, true, undefined, 0, true);
            }

            else if (this.animations.frame === 4) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.1, y: this.scale.y + 0.03 }, 100, Phaser.Easing.Bounce.Out, true, undefined, 0, true);

                this.treeGrowParticle.x = this.position.x + 30;
                this.treeGrowParticle.y = this.position.y - 60;
                // this.treeGrowParticle.start(true, 2200, 200, 5);

                this.treeGrowParticle.x = this.position.x - 30;
                this.treeGrowParticle.y = this.position.y - 60;
                // this.treeGrowParticle.start(true, 2200, 200, 5);

            }
            else if (this.animations.frame === 5) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0, y: this.scale.y + 0.08 }, 100, Phaser.Easing.Bounce.Out, true, undefined, 0, true);
            }

            else if (this.animations.frame === 6) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x - 0.06, y: this.scale.y - 0.02 }, 100, Phaser.Easing.Bounce.Out, true, undefined, 0, true);
            }


            else if (this.animations.frame === 7) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.06, y: this.scale.y + 0.06 }, 100, Phaser.Easing.Bounce.Out, true, undefined, 0, true);
            }

            else if (this.animations.frame === 8) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.01, y: this.scale.y + 0.06 }, 100, Phaser.Easing.Bounce.Out, true, undefined, 0, true);

                this.treeGrowParticle.x = this.position.x + 30;
                this.treeGrowParticle.y = this.position.y - 120;
                // this.treeGrowParticle.start(true, 3200, 200, 5);

                this.treeGrowParticle.x = this.position.x - 30;
                this.treeGrowParticle.y = this.position.y - 120;
                // this.treeGrowParticle.start(true, 3200, 200, 5);
            }

            else if (this.animations.frame === 9) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.05, y: this.scale.y + 0.01 }, 100, Phaser.Easing.Bounce.Out, true, undefined, 0, true);
            }

            else if (this.animations.frame === 10) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.05, y: this.scale.y + 0.01 }, 100, Phaser.Easing.Bounce.Out, true, undefined, 0, true);

                this.treeGrowParticle.x = this.position.x + 50;
                this.treeGrowParticle.y = this.position.y - 120;
                // this.treeGrowParticle.start(true, 3200, 200, 2);

                this.treeGrowParticle.x = this.position.x - 50;
                this.treeGrowParticle.y = this.position.y - 120;
                // this.treeGrowParticle.start(true, 3200, 200, 2);

                this.treeGrowParticle.x = this.position.x + 40;
                this.treeGrowParticle.y = this.position.y - 80;
                // this.treeGrowParticle.start(true, 3200, 200, 2);

                this.treeGrowParticle.x = this.position.x - 40;
                this.treeGrowParticle.y = this.position.y - 80;
                // this.treeGrowParticle.start(true, 3200, 200, 2);
            }

            else if (this.animations.frame === 11) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.05, y: this.scale.y + 0.01 }, 200, Phaser.Easing.Bounce.Out, true, undefined, 0, true);

                this.treeGrowParticle.x = this.position.x + 50;
                this.treeGrowParticle.y = this.position.y - 130;
                // this.treeGrowParticle.start(true, 3200, 200, 4);

                this.treeGrowParticle.x = this.position.x - 50;
                this.treeGrowParticle.y = this.position.y - 130;
                // this.treeGrowParticle.start(true, 3200, 200, 4);

                this.treeGrowParticle.x = this.position.x + 60;
                this.treeGrowParticle.y = this.position.y - 80;
                // this.treeGrowParticle.start(true, 3200, 200, 4);

                this.treeGrowParticle.x = this.position.x - 60;
                this.treeGrowParticle.y = this.position.y - 80;
                // this.treeGrowParticle.start(true, 3200, 200, 4);
            }

            else if (this.animations.frame === 12) {
                tween = game.add.tween(this.scale).to({ x: this.scale.x + 0.06, y: this.scale.y + 0.0 }, 200, Phaser.Easing.Bounce.Out, true, undefined, 0, true);

                this.treeGrowParticle.x = this.position.x + 50;
                this.treeGrowParticle.y = this.position.y - 130;
                // this.treeGrowParticle.start(true, 3200, 200, 8);

                this.treeGrowParticle.x = this.position.x - 50;
                this.treeGrowParticle.y = this.position.y - 130;
                // this.treeGrowParticle.start(true, 3200, 200, 8);

                this.treeGrowParticle.x = this.position.x + 60;
                this.treeGrowParticle.y = this.position.y - 80;
                // this.treeGrowParticle.start(true, 3200, 200, 8);

                this.treeGrowParticle.x = this.position.x - 60;
                this.treeGrowParticle.y = this.position.y - 80;
                // this.treeGrowParticle.start(true, 3200, 200, 8);

                this.treeGrowParticle.x = this.position.x;
                this.treeGrowParticle.y = this.position.y - 140;
                // this.treeGrowParticle.start(true, 3200, 200, 8);
            }

        if (this.animations.frame < targetFrame) {
            game.time.events.add(500, this.growPlant.bind(this, growAmount - 1, callback), this);
        }

        else {
            if (callback)
                callback();

            return;
        }

    }

    constructor(game: Phaser.Game, x: number, y: number, key: number)
    {
        super(game, x, y, mathPlantVariants[key]);
        this.anchor.set(0.5, 1);
        this.treeGrowParticle = new TreeParticleObject(game, 0, 0);
    }

    public setProgress(progress: number)
    {
        this.animations.frame = progress;
    }
}




