import { DropAreaObject } from "GameObjects";

export type SimplePosition = { x: number, y: number };
export type TweenFunction = (spriteToMove: Phaser.Sprite,
    endpoint: SimplePosition) => Phaser.Tween;


export type DropInAreaTweenFunk = (spriteToMove: Phaser.Sprite,
    endDropArea: DropAreaObject) => Phaser.Tween;

function defaultDropInAreaTween(spriteToMove: Phaser.Sprite,
    endDropArea: DropAreaObject): Phaser.Tween
{
    let { x, y } = endDropArea.position;

    return spriteToMove.game.add.tween(spriteToMove).
        to({ x, y },
        333, Phaser.Easing.Exponential.Out);
}

function defaultSwitchHomePointTween(spriteToMove: Phaser.Sprite,
    endpoint: SimplePosition): Phaser.Tween
{
    return spriteToMove.game.add.tween(spriteToMove).
        to(endpoint, 333, Phaser.Easing.Exponential.Out);
}

function defaultGoHomeTween(spriteToMove: Phaser.Sprite,
    endpoint: SimplePosition): Phaser.Tween
{
    return spriteToMove.game.add.tween(spriteToMove).
        to(endpoint, 333, Phaser.Easing.Exponential.Out);
}

export let switchHomePointTween = defaultSwitchHomePointTween;

export function setSwitchHomePointTween(tweenFunction?: TweenFunction)
{
    if (tweenFunction)
    {
        switchHomePointTween = tweenFunction;
    }
    else
    {
        switchHomePointTween = defaultSwitchHomePointTween;
    }
}

export let dropInAreaTween = defaultDropInAreaTween;

export function setDropInAreaTween(tweenFunction?: DropInAreaTweenFunk)
{
    if (tweenFunction)
    {
        dropInAreaTween = tweenFunction;
    }
    else
    {
        dropInAreaTween = defaultDropInAreaTween;
    }
}

export let goHomeTween = defaultGoHomeTween;


export function setGoHomeTween(tweenFunction?: TweenFunction)
{
    if (tweenFunction)
    {
        goHomeTween = tweenFunction;
    }
    else
    {
        goHomeTween = defaultGoHomeTween;
    }
}


