import { IComponentAbleSprite, PhaserSpriteBehavior } from "Components/PhaserComponent/";
import { SymbolComponent } from "Components/SymbolComponents/symbolComponent";
import { NumberComponent } from "Components/numberComponent";
import * as Libs from "Libs/";
import { INumberObject } from "GameObjects";
import { MathDAD } from "Libs/";


export class ExponentiationComponent extends SymbolComponent
{
    public static readonly componentName = "ExponentiationComponent";
    public static readonly inheritedComponentNames = [SymbolComponent];
    public priority = 3;

    protected _symbolName = "exponentiation";

    public calc(left: MathDAD.XNumberValue): { numberValue: number, isX: boolean }
    {
        let numberValue = left.numberValue ** this.exponentiationValue;
        let isX = left.isX;

        return { numberValue, isX };
    }

    public getSymbolsNumbers(numberValues: Libs.ValueIndex<INumberObject>[], symbolIndex: number):
        { right?: Libs.ValueIndex<INumberObject>, left?: Libs.ValueIndex<INumberObject> }
    {
        let returnValues: { right?, left?} = {};

        returnValues.left = Libs.
            findMaxElement(numberValues.filter(vi => vi.index < symbolIndex),
            i => i.index);

        return returnValues;
    }

    public checkNext(next: IComponentAbleSprite | undefined | null): boolean
    {
        if (!next)
            return true;

        if (next.getComponent(SymbolComponent))
        {
            return true;
        }

        if (next.getComponent("CompositeComponent"))
            return true;


        return false;
    }

    constructor(sprite: IComponentAbleSprite,
        public exponentiationValue = 2)
    {
        super(sprite);
    }
}


