let definedModules: ObjectDictionary<any> = Object.create(null);

export function defineRequire(path: string, moduleObj: any)
{
    if (!window.__Game__DEBUG)
        return;

    window["require"] = window["require"] || myRequire;

    let splitPath = path.split("/").filter(s => s !== "");
    let index = 0;
    for (; index < splitPath.length; index++)
    {
        if (splitPath[index] === "code")
        {
            break;
        }
    }

    let newPath = "";
    for (; index < splitPath.length; index++)
    {
        if (index + 1 < splitPath.length)
            newPath += splitPath[index].toLowerCase() + "/";
        else
            newPath += splitPath[index].toLowerCase();
    }
    definedModules[newPath] = moduleObj;
}

function myRequire(path: string): any
{
    let splitPath = path.split("/").filter(s => s !== "");
    let index = 0;
    for (; index < splitPath.length; index++)
    {
        if (splitPath[index] === "code")
        {
            break;
        }
    }

    let newPath = "";
    for (; index < splitPath.length; index++)
    {
        if (index + 1 < splitPath.length)
            newPath += splitPath[index].toLowerCase() + "/";
        else
            newPath += splitPath[index].toLowerCase();
    }
    // console.log(newPath);
    return definedModules[newPath];
}




