import { IComponentAbleSprite, PhaserSpriteBehavior } from "Components";
import { events as stateEvents } from "States/Events/mainStateEvent";

export namespace RectangleOverlapManger
{
    export interface IRectangleOverlapItem
    {
        sprite: IComponentAbleSprite;
        getOverlapRectangle(): Phaser.Rectangle | PIXI.Rectangle;
        receiveOverlapEvent?: boolean;
        onOverlapEnter?(others: IComponentAbleSprite[]);
        onOverlapStay?(others: IComponentAbleSprite[]);
        onOverlapExit?(others: IComponentAbleSprite[]);
    }

    interface IRectangleOverlapEntry
    {
        item: IRectangleOverlapItem;
        intersections: IRectangleOverlapEntry[];
        overlapLayers: string[];
    }

    let layers: { [layer: string]: IRectangleOverlapEntry[] } = {};

    let overlapRectangleEntries: IRectangleOverlapEntry[] = [];

    export function clearLayers()
    {
        for (let layer in layers)
        {
            layers[layer].length = 0;
        }

        overlapRectangleEntries.length = 0;
    }

    export function addItem(overlapItem: IRectangleOverlapItem, layer: string | string[], overlapLayers: string[] = [])
    {
        let entry: IRectangleOverlapEntry = { item: overlapItem, intersections: [], overlapLayers: overlapLayers };

        if (typeof layer === "string")
        {
            if (!layers[layer])
            {
                layers[layer] = new Array<IRectangleOverlapEntry>();
            }

            layers[layer].push(entry);
        }
        else
        {
            layer.forEach((layerItem) =>
            {
                if (!layers[layerItem])
                {
                    layers[layerItem] = new Array<IRectangleOverlapEntry>();
                }

                layers[layerItem].push(entry);
            });
        }


        overlapRectangleEntries.push(entry);
    }

    export function removeItem(overlapItem: IRectangleOverlapItem)
    {
        for (let layer in layers)
        {
            let indexes: number[] = [];

            let currentLayer = layers[layer];

            for (let index = 0; index < currentLayer.length; index++)
            {
                if (currentLayer[index].item === overlapItem)
                {
                    indexes.push(index);
                }
            }

            if (indexes.length > 0)
            {
                indexes.forEach(i => layers[layer].splice(i, 0));
            }

        }

    }

    function update()
    {
        for (let overlapItem of overlapRectangleEntries)
        {
            if (overlapItem.item.receiveOverlapEvent)
            {
                let oldIntersections = overlapItem.intersections;
                let newIntersections: IRectangleOverlapEntry[] = [];

                for (let overlapLayer of overlapItem.overlapLayers)
                {
                    if (overlapLayer in layers)
                    {
                        newIntersections.push(...filterIntersections(layers[overlapLayer], overlapItem));
                    }
                }

                overlapStay(overlapItem, newIntersections);

                overlapExit(overlapItem, oldIntersections, newIntersections);

                overlapEnter(overlapItem, oldIntersections, newIntersections);

                overlapItem.intersections = newIntersections;

            }
            else
            {
                if (overlapItem.intersections.length > 0)
                    overlapItem.intersections.length = 0;
            }
        }
    }

    function shutDown()
    {
        clearLayers();
    }

    function overlapExit(overlapItem: IRectangleOverlapEntry,
        oldIntersections: IRectangleOverlapEntry[], newIntersections: IRectangleOverlapEntry[] = [])
    {
        if (!overlapItem.item.onOverlapExit || oldIntersections.length <= 0)
            return;

        let others = new Array<IComponentAbleSprite>();
        for (let oldIntersection of oldIntersections)
        {
            if (newIntersections.indexOf(oldIntersection) === -1)
            {
                others.push(oldIntersection.item.sprite);
            }
        }
        if (others.length > 0)
            overlapItem.item.onOverlapExit(others);
    }

    function overlapEnter(overlapItem: IRectangleOverlapEntry,
        oldIntersections: IRectangleOverlapEntry[], newIntersections: IRectangleOverlapEntry[])
    {
        if (overlapItem.item.onOverlapEnter)
        {
            let others = new Array<IComponentAbleSprite>();
            for (let newIntersection of newIntersections)
            {
                if (oldIntersections.indexOf(newIntersection) === -1)
                {
                    others.push(newIntersection.item.sprite);
                }
            }
            if (others.length > 0)
                overlapItem.item.onOverlapEnter(others);
        }
    }

    function overlapStay(overlapItem: IRectangleOverlapEntry, newIntersections: IRectangleOverlapEntry[])
    {
        if (overlapItem.item.onOverlapStay)
        {
            let others = new Array<IComponentAbleSprite>(newIntersections.length);
            for (let index = 0; index < newIntersections.length; index++)
            {
                others[index] = newIntersections[index].item.sprite;
            }
            if (others.length > 0)
                overlapItem.item.onOverlapStay(others);
        }
    }

    function filterIntersections(intersections: IRectangleOverlapEntry[],
        overlapItem: IRectangleOverlapEntry): IRectangleOverlapEntry[]
    {
        return intersections.filter(i => i !== overlapItem &&
            Phaser.Rectangle.
                intersects(overlapItem.item.getOverlapRectangle() as any,
                i.item.getOverlapRectangle() as any));
    }

    stateEvents.onUpdate.add(update);
    stateEvents.onShutdown.add(shutDown);
}


