import { GameCreator, currentDoodadLayouts } from "GameCreator/gameCreator";
import { NumberManager } from "Managers/";
import { addDoodadClass } from "./doodadLayoutCreator";
import { MainState } from "States/mainState";

export class SettingsCreator
{
    private _state: MainState;
    public get state() { return this.state; }

    public add: {
        homePosition(x: number, y: number): NumberManager.HomePosition;
    };

    public custom:
    {
        doodad(name: string, doodadClass: new (...args: any[]) => Phaser.Sprite);
    };

    public change:
    {
        doodadsLayout(...layoutNames: string[]);
    };

    public set:
    {
        GameSize(width: number, height: number);
    };

    public load: Phaser.Loader;

    private _addHomePosition(x: number, y: number): NumberManager.HomePosition
    {
        return NumberManager.createHomePosition(x, y);
    }

    private _changeDoodadsLayout(...layoutNames: string[])
    {
        currentDoodadLayouts.length = layoutNames.length;
        for (let index = 0; index < layoutNames.length; index++)
        {
            currentDoodadLayouts[index] = layoutNames[index];
        }
    }

    constructor(state: MainState)
    {
        this._state = state;

        this.add = {
            homePosition: this._addHomePosition.bind(this)
        };
        this.custom = {
            doodad: addDoodadClass
        };
        this.change = {
            doodadsLayout: this._changeDoodadsLayout
        };
        this.set = {
            GameSize: state.scale.setGameSize.bind(state.scale)
        };

        this.load = state.load;
    }
}


