import { IComponentAbleSprite, PhaserSpriteBehavior, InheritableComponentType } from "Components/PhaserComponent/";
import { INumberObject, ISymbolObject } from "GameObjects";
import { SymbolText } from "Components/SymbolComponents/symbolComponent";
import { ResetAbleComponent, ResetBaseArgs } from "Components/resetAbleComponent";
import { ICheckNextAble, ICheckNextAbleType } from "Components/iCheckNextAble";


export class CompositeComponent extends
    PhaserSpriteBehavior implements ICheckNextAble
{
    public static readonly componentName = "CompositeComponent";
    public static readonly inheritedComponentNames: InheritableComponentType[] = [ICheckNextAbleType];

    public events: { onSymbolTextNumberChange: Phaser.Signal };

    private _numberObject: INumberObject;
    private _symbolObject: ISymbolObject;
    private _isMerged: boolean = true;

    public get numberObject() { return this._numberObject; }
    public get symbolObject() { return this._symbolObject; }
    public get isMerged() { return this._isMerged; }

    private _numberValue: number;
    private _symbolText: SymbolText;

    protected start()
    {
        let comp = this.sprite.getComponent(ResetAbleComponent);
        if (comp)
            comp.events.onReset.add(this.onReset, this);
    }

    protected onReset(resetArgs: CompositeResetArg)
    {
        let { symbolText, numberValue } = resetArgs;

        if (symbolText)
            this._symbolText = symbolText;

        if (numberValue)
            this._numberValue = numberValue;

        this.events.onSymbolTextNumberChange.
            dispatch(this._symbolText, this._numberValue);
    }

    public get symbolText(): SymbolText
    {
        return this._symbolText;
    }

    public set symbolText(value: SymbolText)
    {
        if (this._symbolText !== value)
        {
            this._symbolText = value;
            this.events.onSymbolTextNumberChange.
                dispatch(this._symbolText, this._numberValue);
        }
    }

    public get numberValue(): number
    {
        return this._numberValue;
    }

    public set numberValue(value: number)
    {
        if (this._numberValue !== value)
        {
            this._numberValue = value;
            this.events.onSymbolTextNumberChange.
                dispatch(this._symbolText, this._numberValue);
        }
    }

    public split(): { numberObject: INumberObject, symbolObject: ISymbolObject }
    {
        this._numberObject.exists = true;
        this._numberObject.numberValue = this._numberValue;
        this._symbolObject.exists = true;
        this._isMerged = false;

        this._numberObject.alpha = 1;
        this._symbolObject.alpha = 1;

        if (this._symbolObject.changeSymbol)
            this._symbolObject.changeSymbol(this._symbolText);

        return { numberObject: this._numberObject, symbolObject: this._symbolObject };
    }

    public merge()
    {
        this._symbolObject.exists = false;
        this._numberObject.exists = false;

        this.sprite.exists = true;
        this.sprite.alpha = 1;
        this._isMerged = true;
    }

    public checkNext(next: IComponentAbleSprite | null | undefined): boolean
    {
        this._numberObject.numberValue = this._numberValue;
        return this._numberObject.numberComponent.checkNext(next);
    }

    constructor(sprite: IComponentAbleSprite, symbolText: SymbolText,
        symbolObject: ISymbolObject, numberValue: number, numberObject: INumberObject)
    {
        super(sprite);
        this.events = { onSymbolTextNumberChange: new Phaser.Signal };
        this._numberObject = numberObject;
        this._symbolObject = symbolObject;
        this._symbolText = symbolText;
        this._numberValue = numberValue;
        this._numberObject.exists = false;
        this._symbolObject.exists = false;
    }
}


export interface CompositeResetArg extends ResetBaseArgs
{
    symbolText?: SymbolText;
    numberValue?: number;
}


