import
{
    NewLitUpComponent, ObjectInAreaStatusEnum, DropAreaComponent,
    makeComponentAbleSprite, IComponentAbleSprite, ComponentAbleSprite,
    DADHomeComponent, MathOwnedByComponent, IMathOwner, DragAndDropComponent
} from "Components";
import { assetLoadMethod, StateEnum } from "assetsLoader";
import { IResetAbleSprite } from "GameObjects";

const defaultDropAreaObject = {
    tint: 16777215,
    alpha: 1,
    scale: { x: 1, y: 1 }
};


export const dropAreaOverlapLayer = "DropSquareLayer";

export class DropAreaObject extends ComponentAbleSprite
    implements IResetAbleSprite
{

    private _litUp: NewLitUpComponent;
    private _isFixed: boolean = false;
    private _ownedByComponent: MathOwnedByComponent;

    public get ownedBy() { return this._ownedByComponent.ownedBy; }
    public set ownedBy(value: IMathOwner | null)
    {
        this._ownedByComponent.ownedBy = value;
    }

    public readonly dropArea: DropAreaComponent;
    public dropAreaIndex: number = -1;

    public get full()
    {
        return this.dropArea.getObjectInAreaStatus === ObjectInAreaStatusEnum.inArea;
    }

    public get isFixed()
    {
        return this._isFixed;
    }

    public get objectInArea()
    {
        return this.dropArea.getObjectInArea;
    }

    public addObjectFixed(compSprite: IComponentAbleSprite)
    {
        let comp = compSprite.getComponent(DADHomeComponent);
        if (comp)
        {
            comp.SetDragAble(false);
        }
        compSprite.position.copyFrom(this.position);
        this._isFixed = true;
        this.dropArea.SetObjectInArea(compSprite, ObjectInAreaStatusEnum.inArea);
    }

    public resetSprite(x: number, y: number): DropAreaObject
    {
        let { alpha, scale, tint } = defaultDropAreaObject;
        this.alpha = alpha;
        this.scale.set(scale.x, scale.y);
        this.tint = tint;
        this.position.set(x, y);
        this.dropArea.SetObjectInArea(null, ObjectInAreaStatusEnum.noObject);
        return this;
    }

    public dragAbleInArea(): boolean
    {
        let objectInArea = this.objectInArea;
        if (objectInArea && objectInArea.getComponent(DragAndDropComponent))
        {
            return true;
        }

        return false;
    }

    constructor(game: Phaser.Game, x: number, y: number, key: string = "dropSquare")
    {
        super(game, x, y, key);
        this.anchor.set(0.5, 0.5);
        this.scale.setTo(0.5, 0.5);
        this.dropArea = this.addComponent(DropAreaComponent, dropAreaOverlapLayer);
        this._litUp = this.addComponent(NewLitUpComponent, "dropSquareLit", "dropSquare");
        this.dropArea.events.onObjectStatusChange
            .add(this._eventOnObjectStatusChange, this);
        this._ownedByComponent = this.addComponent(MathOwnedByComponent);
    }

    private _eventOnObjectStatusChange(objectInArea: IComponentAbleSprite,
        newStatus: ObjectInAreaStatusEnum, oldStatus: ObjectInAreaStatusEnum)
    {
        if (newStatus === ObjectInAreaStatusEnum.reservedArea)
        {
            this._litUp.LitUp();
        }
        else
        {
            this._litUp.LitDown();
        }
    }

    @assetLoadMethod(StateEnum.Main, "dropArea")
    private static _loadAssets(game: Phaser.Game)
    {
        game.load.image("dropSquare", "images/DropSquare.png");
        game.load.image("dropSquareLit", "images/DropSquareLit.png");
    }
}




