import * as Libs from "Libs/";



interface ILevelSelectInfo
{
    gameBeginPlayed: GameTypes;
    amountOfLevels: number;
    backButtonSprite: UrlAndKey;
    loadScreenSprite: UrlAndKey;
    levelSelectBackgroundSprite: UrlAndKey;
    buttonBackgroundSprite: UrlAndKey;
    buttonBackgroundGreySprite: UrlAndKey;
    buttonArrowSprite: UrlAndKey;
}

export type UrlAndKey = { key: string, spriteUrl: string };

export let levelSelectInfo: ILevelSelectInfo = {
    gameBeginPlayed: "order",
    amountOfLevels: 40,
    backButtonSprite: { key: "backButtonKey", spriteUrl: "images/backButton.png" },
    loadScreenSprite: { key: "loadScreenKey", spriteUrl: "images/percentageLoadScreen.jpg" },
    levelSelectBackgroundSprite: { key: "levelSelectBackgroundKey", spriteUrl: "images/lvlbg.png" },
    buttonBackgroundSprite: { key: "buttonBackgroundKey", spriteUrl: "images/buttonBackground.png" },
    buttonBackgroundGreySprite: { key: "buttonBackgroundGreyKey", spriteUrl: "images/buttonBackgroundGrey.png" },
    buttonArrowSprite: { key: "buttonArrowKey", spriteUrl: "images/btnArrowTest.png" }
};


