import { game } from "main";
import { ResetBaseArgs, ResetAbleComponent } from "Components/resetAbleComponent";
import { improveArray, ImprovedArray } from "../../Common/Containers/";
import { IComponentAbleSprite } from "Components/PhaserComponent/";


export function makeSpriteArray<T extends IComponentAbleSprite>(
    state: Phaser.State, spriteClass: Constructor<T>, defaultGroup?: Phaser.Group): SpriteArray<T>
{
    let newArray = improveArray<T>() as SpriteArray<T>;
    newArray.spriteClass = spriteClass;
    newArray.state = state;
    newArray.defaultGroup = defaultGroup;
    newArray.create = spriteArrayCreate;
    newArray.restElseCreate = spriteArrayRestElseCreate;
    return newArray;
}

export interface SpriteArray<T extends IComponentAbleSprite = IComponentAbleSprite> extends ImprovedArray<T>
{
    spriteClass: Constructor<T>;
    state: Phaser.State;
    defaultGroup?: Phaser.Group;
    onCreate?: (sprite: T) => T;
    defaultCreateArgs?: any[];
    defaultResetArgs?: ResetBaseArgs;

    restElseCreate(group?, restArgs?: ResetBaseArgs, ...args: any[]);
    create(group?: Phaser.Group, ...args: any[]): T;
}

function spriteArrayCreate<T extends IComponentAbleSprite>(this: SpriteArray<T>, group = this.defaultGroup, ...args: any[]): T
{
    if (this.defaultCreateArgs !== undefined && args.length < this.defaultCreateArgs.length)
    {
        args.push(...this.defaultCreateArgs.slice(args.length));
    }

    let newSprite = new this.spriteClass(this.state.game, ...args);

    if (group)
        group.add(newSprite);
    else
        this.state.add.existing(newSprite);

    if (this.onCreate)
    {
        newSprite = this.onCreate(newSprite);
    }

    this.push(newSprite);
    return newSprite;
}

function spriteArrayGetExists<T extends IComponentAbleSprite>(this: SpriteArray<T>,
    exists = true, createIfNull = false, group?: Phaser.Group, ...args: any[]): T | void
{
    for (let index = 0; index < this.length; index++)
    {
        if (this[index].exists === exists)
        {
            return this[index];
        }
    }

    if (createIfNull && group)
        return this.create(group, ...args);
}

function spriteArrayRestElseCreate<T extends IComponentAbleSprite>(this: SpriteArray<T>,
    group = this.defaultGroup, restArgs?: ResetBaseArgs, ...args: any[]): T
{
    if (!restArgs)
        restArgs = this.defaultResetArgs || {};
    else if (this.defaultResetArgs)
    {
        for (let key in this.defaultResetArgs)
        {
            if (restArgs[key] === undefined)
            {
                restArgs[key] = this.defaultResetArgs[key];
            }
        }
    }

    for (let objT of this)
    {
        if (objT.exists)
            continue;

        let comp = objT.getComponent(ResetAbleComponent);

        if (comp)
        {
            comp.resetObject(restArgs);
        }

        if (this.onCreate)
            return this.onCreate(objT);

        return objT;
    }

    return this.create(group, ...args);
}


