/// <reference path="../../scriptJsTypings/jsonEditor.d.ts" />
/// <reference path="../../json/global.d.ts" />

import { MapProxy } from "Libs/";

let onceCheck;
let givePath;
let jsonEditor: JSONEditor;

type JsonCallbackObject = { callback: (json: any) => void, context?: any };

let jsonLoadQueue: JsonCallbackObject[];
let jsonUpdateMap: MapProxy<object, JsonCallbackObject> = new MapProxy<Function, JsonCallbackObject>();

let updateTimer: number;
let autoUpdateDelay: number = 1000;
let isAutoUpdating: boolean = true;

/**
 * contains all data from the global.json
 */
export let jsonGlobalObject: globalJson.RootObject;

/**
 * Adds function to callback when the json is loaded
 * or if the json is already loaded callback immediately
 * @param callback the function to callback with context and the jsonGlobalObject using the prefix
 * @param context the context of the callback is called in optional
 */
export function addOnJsonLoad(callback: (json: any) => void, context?: any)
{
    if (!jsonGlobalObject)
    {
        if (jsonLoadQueue)
        {

            jsonLoadQueue.push({ callback, context });
        }
        else
        {
            jsonLoadQueue = [{ callback, context }];
        }
    }
    else
    {
        runJsonCallbackObject({ callback, context });
    }
}

/**
 * Adds function to callback when the json is update by the editor
 * @param objectToMap the object the callback will be map to, use it to remove the callback
 * @param callback the function to callback with context and the jsonGlobalObject using the prefix
 * @param context the context of the callback is called in optional
 */
export function addOnJsonUpdate(objectToMap: object, callback: (json: globalJson.RootObject) => void, context?: any): void
{
        return;
}

export function removeOnJsonUpdate(objectToMap: object): boolean
{
    return jsonUpdateMap.delete(objectToMap);
}

export function loadJson(path: string)
{
    if (onceCheck)
        return;

    onceCheck = true;
    givePath = path;
    getJSON(path, onGetJson);
}


function onGetJson(xhrStatus: number, response?: any)
{
    if (xhrStatus === 200)
    {
        if (typeof response === "string")
        {
            response = JSON.parse(response);
        }
        jsonGlobalObject = response;

        while (jsonLoadQueue && jsonLoadQueue.length > 0)
        {
            let element = jsonLoadQueue.pop();
            if (element)
                runJsonCallbackObject(element);
        }

        if (window.__Game__DEBUG)
        {
            if (response.editorJson.active)
            {
                createJsonEditorControlDiv();
                createJsonEditor(response);
            }
        }
        else
        {
            delete jsonGlobalObject.editorJson;
            deepFreeze(jsonGlobalObject);
        }
    }
}

function onJsonEditorChange()
{
    if (!isAutoUpdating)
        return;

    if (autoUpdateDelay <= 0)
    {
        updateJson();
        return;
    }


    if (updateTimer <= 0)
    {
        updateTimer = setTimeout(updateJson, autoUpdateDelay);
    }
    else
    {
        clearTimeout(updateTimer);
        updateTimer = setTimeout(updateJson, autoUpdateDelay);
    }
}

function updateJson()
{
    console.log("json update");
    jsonGlobalObject = <globalJson.RootObject>jsonEditor.get();
    deepFreeze(jsonGlobalObject);
    jsonUpdateMap.forEach(c => runJsonCallbackObject(c));
    updateTimer = 0;
}

function deepFreeze(obj)
{
    // Retrieve the property names defined on obj
    let propNames = Object.getOwnPropertyNames(obj);

    // Freeze properties before freezing self
    propNames.forEach(function (name)
    {
        let prop = obj[name];

        // Freeze prop if it is an object
        if (typeof prop === "object" && prop !== null)
            deepFreeze(prop);
    });

    // Freeze self (no-op if already frozen)
    return Object.freeze(obj);
}

function getJSON(url: string, callback: (xhrStatus: number, response?: any) => void)
{
    let xhr = new XMLHttpRequest();
    xhr.open("GET", url, true);
    xhr.responseType = "json";
    xhr.onload = function ()
    {
        let status = xhr.status;
        if (status === 200)
        {
            callback(status, xhr.response);
        }
        else
        {
            callback(status);
        }
    };
    xhr.send();
}

function runJsonCallbackObject(jsonCallbackObject: JsonCallbackObject)
{
    jsonCallbackObject.callback.call(jsonCallbackObject.context, jsonGlobalObject);
}

function createJsonEditorControlDiv()
{
    let jsonEditorControlDiv = document.getElementById("jsonEditorControl");

    if (jsonEditorControlDiv)
    {
        jsonEditorControlDiv.appendChild(document.createTextNode("auto Update:   "));

        let checkBoxAU = <HTMLInputElement>document.createElement("input");
        checkBoxAU.setAttribute("type", "checkbox");
        checkBoxAU.checked = isAutoUpdating;
        checkBoxAU.addEventListener("change", e => isAutoUpdating = checkBoxAU.checked);
        jsonEditorControlDiv.appendChild(checkBoxAU);

        jsonEditorControlDiv.appendChild(document.createTextNode("auto update delay:  "));

        let numberFieldAU = <HTMLInputElement>document.createElement("input");
        numberFieldAU.setAttribute("type", "number");
        numberFieldAU.setAttribute("value", autoUpdateDelay.toString());
        numberFieldAU.addEventListener("change", e => autoUpdateDelay = Number(numberFieldAU.value));
        jsonEditorControlDiv.appendChild(numberFieldAU);

        let updateButton = <HTMLButtonElement>document.createElement("button");
        updateButton.style.margin = "5px";
        updateButton.appendChild(document.createTextNode("update Json"));
        updateButton.onclick = updateJson;
        jsonEditorControlDiv.appendChild(updateButton);
    }
}

function createJsonEditor(gJson: globalJson.RootObject)
{
    let element = document.getElementById("jsonEditor");
    if (element && gJson.editorJson)
    {
        element.style.height = (gJson.editorJson.height.toString() + "px");
        delete gJson.editorJson;
        jsonEditor = new JSONEditor(element,
            {
                name: "globalSettings",
                onChange: onJsonEditorChange, mode: "form", modes: ["form", "tree", "view", "code", "text"]
            }, gJson);
    }
}


