import { ComponentAbleText, ExponentiationComponent } from "Components";
import { ISymbolObject } from "../iSymbolObject";
import { textStyleConfig } from "fonts";

export class ExponentiationObject extends
    ComponentAbleText implements ISymbolObject
{
    private _symbolStateComponent: ExponentiationComponent;
    public get symbolComponent() { return this._symbolStateComponent; }

    constructor(game: Phaser.Game, x: number, y: number, exponentiationValue = 2)
    {
        super(game, x, y, exponentiationValue.toString(), textStyleConfig);
        this._symbolStateComponent = this.addComponent(ExponentiationComponent, exponentiationValue);
        this.anchor.setTo(0.5);
    }
}


