import
{
    makeComponentAbleSprite, IComponentAbleSprite,
    InterfaceComponentAbleType, StartingAreaComponent
} from "Components/";
import { events as stateEvents } from "States/Events/mainStateEvent";
import { MainState } from "States/mainState";

export namespace NumberManager
{
    let possibleHomePositions: HomePosition[] = [];
    let numberObjectComponents: INumberMangerComponent[] = [];

    let numberStartingArea: IComponentAbleSprite;
    let numberStartingAreaComponent: StartingAreaComponent;

    let startingAreaRectangle: { width, height, x, y };

    export function createHomePosition(x: number, y: number): HomePosition
    {
        let homePosition = new HomePosition(x, y, possibleHomePositions.length);
        possibleHomePositions.push(homePosition);
        return homePosition;
    }

    export function registerNumberObjectComponent(component: INumberMangerComponent):
        HomePosition
    {
        let homeNumber = numberObjectComponents.push(component) - 1;
        return possibleHomePositions[homeNumber];
    }

    export function registerNumberObjectComponentAt(component: INumberMangerComponent, index: number):
        HomePosition
    {
        numberObjectComponents[index] = component;
        return possibleHomePositions[index];
    }

    export function removeNumberObjectComponent(component: INumberMangerComponent)
    {
        let index = numberObjectComponents.indexOf(component);
        if (index !== -1)
        {
            numberObjectComponents.splice(index, 1);

            numberObjectComponents.forEach((item, index) =>
            {
                if (item.getCurrentHomeNumber !== index)
                {
                    let { x, y } = possibleHomePositions[index];
                    item.changeHomeNumber(index, x, y);
                }
            });
        }
    }

    function numberObjectLeft(others: IComponentAbleSprite[])
    {
        let removedItems: INumberMangerComponent[] = new Array();

        others.forEach((item, index) =>
        {
            let componet = item.getComponent(INumberMangerComponentType);
            if (componet && componet.getUseHomePoint)
            {
                removedItems.push(componet);
                numberObjectComponents.splice(componet.getCurrentHomeNumber, 1);
            }
        });

        if (removedItems.length > 0)
        {
            numberObjectComponents.forEach((item, index) =>
            {
                if (item.getCurrentHomeNumber !== index)
                {
                    let { x, y } = possibleHomePositions[index];
                    item.changeHomeNumber(index, x, y);
                }
            });

            removedItems.forEach((item) =>
            {
                let newHomeNumber = numberObjectComponents.push(item) - 1;

                if (item.getCurrentHomeNumber !== newHomeNumber)
                {
                    let { x, y } = possibleHomePositions[newHomeNumber];
                    item.changeHomeNumber(newHomeNumber, x, y);
                }

            });
        }
    }

    function create(state: MainState)
    {
        numberStartingArea = new (makeComponentAbleSprite(Phaser.Sprite))
            (state.game, state.game.width / 2, state.game.height / 2);
        state.add.existing(numberStartingArea);
        numberStartingAreaComponent = numberStartingArea.addComponent(StartingAreaComponent);
        numberStartingAreaComponent.events.numberObjectExit.add(numberObjectLeft);
    }

    function managerShutdown(state: MainState)
    {
        numberObjectComponents.length = 0;
    }

    stateEvents.onCreate.add(create);
    stateEvents.onShutdown.add(managerShutdown);

    export interface INumberMangerComponent
    {
        getUseHomePoint: boolean;
        getCurrentHomeNumber: number;
        changeHomeNumber(newNumber: number, newPositionX: number, newPositionY, move?: boolean): void;
    }

    export const INumberMangerComponentType: InterfaceComponentAbleType<INumberMangerComponent> = {
        componentName: "INumberMangerComponet"
    };

    export class HomePosition
    {
        public x: number;
        public y: number;
        private _index: number;
        public get index(): number { return this._index; }

        constructor(x: number, y: number, index: number)
        {
            this.x = x;
            this.y = y;
            this._index = index;
        }
    }
}


