import { textStyleConfig } from "fonts";
import { IResetAbleSprite } from "../iResetAble";
import
{
    NumberComponent, makeComponentAbleSprite,
    IComponentAbleSprite, ComponentAbleText
} from "Components";
import { INumberObject } from "./iNumberObject";
import * as Libs from "Libs/";

const numberObjectDefaults = {
    tint: 16777215,
    alpha: 1,
    scale: { x: 1, y: 1 }
};

export const numberOverlapLayer = "NumberLayer";
export class NumberObject extends
    ComponentAbleText implements IResetAbleSprite, INumberObject
{
    public readonly numberComponent: NumberComponent;

    public get numberValue()
    {
        return this.numberComponent.numberRepresenting;
    }
    public set numberValue(value: number)
    {
        this.numberComponent.numberRepresenting = value;
    }

    public get isX() { return this.numberComponent.isX; }
    public set isX(value) { this.numberComponent.isX = value; }

    public get divisionStyling() { return this.numberComponent.divisionStyling; }
    public set divisionStyling(value) { this.numberComponent.divisionStyling = value; }

    public restNumberObject(x: number, y: number, numberValue: number)
    {
        this.position.set(x, y);
        this.numberComponent.numberRepresenting = numberValue;
        this.exists = true;
    }

    public resetSprite(x: number, y: number, numberValue: number,
        style: Phaser.PhaserTextStyle = textStyleConfig): NumberObject
    {
        let { alpha, scale, tint } = numberObjectDefaults;

        this.restNumberObject(x, y, numberValue);
        this.setStyle(style);
        this.exists = true;
        this.tint = tint;
        this.alpha = alpha;
        this.scale.set(scale.x, scale.y);

        return this;
    }

    protected onNumberChange(value: number, isX: boolean, divisionStyling: boolean)
    {
        this.text = Libs.MathDAD.numberToString(value, isX, divisionStyling);
    }

    constructor(game: Phaser.Game, x: number, y: number, numberValue: number,
        style: Phaser.PhaserTextStyle = textStyleConfig)
    {
        super(game, x, y, numberValue.toString(), style);
        this.numberComponent = this.addComponent(NumberComponent, numberValue);
        this.numberComponent.events.onNumberChange.add(this.onNumberChange, this);
        this.anchor.set(0.5);
        this.onNumberChange(numberValue, false, false);
    }
}


