import { GameCreator, currentDoodadLayouts } from "GameCreator/gameCreator";
import { MainState } from "States/mainState";
import
{
    DropAreaObject, DragAbleNumberObject,
    DragAbleSymbolObject, WinSymbolObject, WinSymbolText,
    NumberObject, SymbolObject, ParenthesisPair,
    ExponentiationObject, RootObject, CompositeObject, CompositeSpawner
} from "GameObjects/";
import { SymbolText } from "Components/";
import { NumberManager } from "Managers/";


export class LevelCreator
{
    public get level() { return this._level; }
    public get state() { return this._state; }
    private _level: number;
    private _state: MainState;

    public create: {
        dropArea(x: number, y: number): DropAreaObject;
        number(value: number, startingPoint?: NumberManager.HomePosition,
            isX?: boolean, divisionStyling?: boolean): DragAbleNumberObject;
        symbol(value: SymbolText): DragAbleSymbolObject;
        winCheck(value: string, numberValue: number | undefined, x: number, y: number): WinSymbolObject;
        parenthesisPair(startBeforeDropArea: DropAreaObject, endAfterDropArea: DropAreaObject,
            executionPriority: number): ParenthesisPair;
        fixed: {
            number(value: number, dropArea?: DropAreaObject,
                isX?: boolean, divisionStyling?: boolean): DragAbleNumberObject;
            symbol(value: SymbolText, dropArea?: DropAreaObject): DragAbleSymbolObject;
        };
        complexSymbol: {
            exponentiation(dropArea?: DropAreaObject, exponentiationValue?: number): ExponentiationObject;
            root(dropArea?: DropAreaObject, rootValue?: number): RootObject;
        };
        composite(symbolText: SymbolText, numberValue: number, dropArea?: DropAreaObject): CompositeObject;
        compositeSpawner(symbolText: SymbolText, numberValue: number, x: number, y: number): CompositeSpawner;
    };

    public change: {
        doodadsLayout(...layoutNames: string[]);
    };

    public set: {
        secondStageStartDropArea(dropArea?: DropAreaObject): DropAreaObject;
    };

    private _createDropArea(x: number, y: number): DropAreaObject
    {
        return this._state.dropAreaObjects.create(undefined, x, y);
    }

    private _createNumber(value: number, startingPoint?: NumberManager.HomePosition,
        isX?: boolean, divisionStyling?: boolean): DragAbleNumberObject
    {
        let newObj = this._state.dragAbleNumberObjects.create(undefined, 0, 0, value, startingPoint);

        if (divisionStyling)
            newObj.divisionStyling = divisionStyling;

        if (isX)
            newObj.isX = isX;

        return newObj;
    }

    private _createFixedNumber(value: number, dropArea?: DropAreaObject,
        isX?: boolean, divisionStyling?: boolean): NumberObject
    {
        let newNumber = this._state.fixedNumberObjects.create(undefined, 0, 0, value);
        if (dropArea)
            dropArea.addObjectFixed(newNumber);

        if (divisionStyling)
            newNumber.divisionStyling = divisionStyling;

        if (isX)
            newNumber.isX = isX;

        return newNumber;
    }

    private _createFixedSymbol(value: SymbolText, dropArea?: DropAreaObject): SymbolObject
    {
        let newSymbol = this._state.fixedSymbolObjects.create(undefined, 0, 0, value);
        if (dropArea)
            dropArea.addObjectFixed(newSymbol);

        return newSymbol;
    }

    private _createSymbol(value: SymbolText, startingPoint?: NumberManager.HomePosition): DragAbleSymbolObject
    {
        return this._state.dragAbleSymbolObjects.create(undefined, 0, 0, value, startingPoint);
    }

    private _createWinSymbol(value: WinSymbolText, numberValue: number = 0, x: number, y: number): WinSymbolObject
    {
        return this._state.winSymbolObjects.create(undefined, x, y, value, numberValue);
    }


    private _createParenthesisPair(startBeforeDropArea: DropAreaObject, endAfterDropArea: DropAreaObject,
        executionPriority: number): ParenthesisPair
    {
        return this._state.parenthesisPairObjects.create(startBeforeDropArea,
            endAfterDropArea, executionPriority);
    }

    private _createExponentiation(dropArea?: DropAreaObject, exponentiationValue?: number): ExponentiationObject
    {
        let newExponentiation = this._state.exponentiationSymbolObjects.create(undefined, 0, 0, exponentiationValue);
        if (dropArea)
        {
            dropArea.addObjectFixed(newExponentiation);
        }
        return newExponentiation;
    }

    private _createCompositeSpawner(symbolText: SymbolText, numberValue: number, x: number, y: number): CompositeSpawner
    {
        return this._state.spawnerCompositeObjects.create(undefined, x, y, symbolText, numberValue);
    }

    private _createRoot(dropArea?: DropAreaObject, rootValue?: number): RootObject
    {
        let newRoot = this._state.rootSymbolObjects.create(undefined, 0, 0, rootValue);
        if (dropArea)
        {
            dropArea.addObjectFixed(newRoot);
        }
        return newRoot;
    }
    private _createComposite(symbolText: SymbolText, numberValue: number, dropArea?: DropAreaObject): CompositeObject
    {
        let newCompositeObject = this._state.compositeObjects.create(undefined, 0, 0, symbolText, numberValue);
        if (dropArea)
        {
            dropArea.addObjectFixed(newCompositeObject);
        }
        return newCompositeObject;
    }

    private _changeDoodadsLayout(...layoutNames: string[])
    {
        currentDoodadLayouts.length = layoutNames.length;
        for (let index = 0; index < layoutNames.length; index++)
        {
            currentDoodadLayouts[index] = layoutNames[index];
        }
    }

    private _setSecondStageStartDropArea(dropArea: DropAreaObject | null = null)
    {
        this._state.secondStageStartDropArea = dropArea;
    }


    constructor(level: number, state: MainState)
    {
        state.secondStageStartDropArea = null;
        this._level = level;
        this._state = state;
        let self = this;
        this.create = {
            dropArea: this._createDropArea.bind(this),
            symbol: this._createSymbol.bind(this),
            number: this._createNumber.bind(this),
            winCheck: this._createWinSymbol.bind(this),
            parenthesisPair: this._createParenthesisPair.bind(this),
            fixed: {
                number: self._createFixedNumber.bind(self),
                symbol: self._createFixedSymbol.bind(self)
            },
            complexSymbol: {
                root: self._createRoot.bind(self),
                exponentiation: self._createExponentiation.bind(self)
            },
            composite: this._createComposite.bind(this),
            compositeSpawner: this._createCompositeSpawner.bind(this)
        };
        this.change = {
            doodadsLayout: this._changeDoodadsLayout.bind(this)
        };
        this.set = {
            secondStageStartDropArea: this._setSecondStageStartDropArea.bind(this)
        };
    }
}



