import { TreeParticleObject } from "GameObjects/Particles";
import { game } from "main";

export const millSpriteKeys = ["millHouse1", "millHouse2"];
export const wingsSpriteKeys = ["millWings1", "millWings2"];


export class MillObject extends Phaser.Sprite
{

    public static readonly doodadName = "Mill";

    public rotationSpeed: number;
    private rotationMod = 0;
    private wings: Phaser.Sprite;

    private shakeTween: Phaser.Tween;
    private startAngle;
    private shakeAngle = 7;

    public static loadAssets(game: Phaser.Game)
    {
        game.load.image(millSpriteKeys[0], "images/mathman/World2/mill/mill1.png");
        game.load.image(millSpriteKeys[1], "images/mathman/World2/mill/mill2.png");

        game.load.image(wingsSpriteKeys[0], "images/mathman/World2/mill/wings1.png");
        game.load.image(wingsSpriteKeys[1], "images/mathman/World2/mill/wings2.png");

    }

    constructor(game: Phaser.Game, x: number, y: number, key: number, rotationSpeed?: number, scaleX?: number, scaleY?: number)
    {
        super(game, x, y, millSpriteKeys[key]);
        this.anchor.set(0.5, 1);
        this.wings = this.createWings(key);
        this.setBasePosition(0, 0 - this.height / 1.2);
        this.startAngle = this.angle;
        this.inputEnabled = true;
        this.events.onInputDown.add(this.millShake, this);


        if (scaleX)
            {
                this.scale.multiply(scaleX, 1);
            }

        if (scaleY)
            {
                this.scale.multiply(1, scaleY);
            }


        if (rotationSpeed)
            this.rotationSpeed = rotationSpeed;
        else
            this.rotationSpeed = 0.2;
    }

    private createWings(key)
    {

        let base = new Phaser.Sprite(game, 0, 0, wingsSpriteKeys[key]);
        this.addChild(base);
        base.anchor.set(0.5, 0.5);
        return base;
    }

    public millShake()
    {
        if (this.shakeTween)
            this.shakeTween.stop;
        this.angle = this.startAngle;
        this.shakeTween = game.add.tween(this).to({angle: this.shakeAngle}, 120, Phaser.Easing.Circular.Out, true, 0, undefined, true);
        this.rotationMod += 50;
    }
    public setBasePosition(x, y)
    {
        this.wings.position.set(x, y);
    }
    public update()
    {
        this.wings.angle += (this.rotationSpeed * this.rotationMod) + this.rotationSpeed;
        this.rotationMod *= 0.90;
    }
}


