import
{
    ComponentAbleText, SymbolText,
    CompositeComponent, ResetAbleComponent
} from "Components";
import * as Libs from "Libs";
import { NumberObject } from "./NumberObject/";
import { SymbolObject } from "./SymbolObject/";
import { textStyleConfig } from "fonts";

export class CompositeObject extends ComponentAbleText
{
    private _compositeComponent: CompositeComponent;
    private _resetComponent: ResetAbleComponent;
    public get resetComponent() { return this.resetComponent; }
    public get compositeComponent() { return this._compositeComponent; }

    public split()
    {
        return this._compositeComponent.split();
    }

    protected onNumberSymbolValueChange(symbolText: SymbolText, value: number)
    {
        this.text = Libs.MathDAD.symbolTextToString(symbolText) + " " + Libs.MathDAD.numberToString(value, false);
    }

    constructor(game: Phaser.Game, x: number, y: number,
        symbolText: SymbolText, numberValue: number,
        symbolGroup: Phaser.Group, numberGroup: Phaser.Group)
    {
        super(game, x, y, "", textStyleConfig);
        let numberObject = makeNumberObject(game, x, y, numberValue, numberGroup);
        let symbolObject = makeSymbolObject(game, x, y, symbolText, symbolGroup);
        this._compositeComponent = this.addComponent(CompositeComponent,
            symbolText, symbolObject, numberValue, numberObject);
        this._compositeComponent.events.
            onSymbolTextNumberChange.add(this.onNumberSymbolValueChange, this);
        this.onNumberSymbolValueChange(symbolText, numberValue);
        this._resetComponent = this.addComponent(ResetAbleComponent);
        this.anchor.setTo(0.5);
    }
}


function makeNumberObject(game: Phaser.Game, x: number,
    y: number, numberValue: number, group: Phaser.Group): NumberObject
{
    let newNumberObject = new NumberObject(game, x, y, numberValue);
    group.add(newNumberObject);
    return newNumberObject;
}

function makeSymbolObject(game: Phaser.Game, x: number,
    y: number, symbolText: SymbolText, group: Phaser.Group): SymbolObject
{
    let newSymbolObject = new SymbolObject(game, x, y, symbolText);
    group.add(newSymbolObject);
    return newSymbolObject;
}


