import
{
    IComponentAbleSprite, DADHomeComponent,
    SymbolText, startingObjectLayer,
    ComponentAbleText
} from "Components";
import { SymbolObject } from "./symbolObject";
import { dropAreaOverlapLayer } from "../dropAreaObject";
import { NumberManager } from "Managers/";
import { textStyleConfig } from "fonts";

export const symbolOverlapLayer = "SymbolObject";

export class DragAbleSymbolObject extends SymbolObject
{
    public get dADHomeComponent() { return this._dADHomeComponent; }
    private _dADHomeComponent: DADHomeComponent;

    public get homePosition(): NumberManager.HomePosition | undefined
    {
        if (this._dADHomeComponent)
        {
            return this._dADHomeComponent.currentHomePosition;
        }
        return undefined;
    }

    public set homePosition(value: NumberManager.HomePosition | undefined)
    {
        if (this._dADHomeComponent && value)
            this._dADHomeComponent.currentHomePosition = value;
    }

    public resetSprite(x: number, y: number,
        text: SymbolText, configObj: { style?: Phaser.PhaserTextStyle } = {}): SymbolObject
    {
        super.resetSprite(x, y, text, configObj);
        this._dADHomeComponent.moveHome();
        return this;
    }

    constructor(game: Phaser.Game, x: number, y: number, text: SymbolText, configObj: { style?: Phaser.PhaserTextStyle } = {})
    {
        super(game, x, y, text, { style: configObj.style || textStyleConfig });

        this.changeSymbol(text);

        this._dADHomeComponent = this.addComponent(DADHomeComponent, [symbolOverlapLayer, startingObjectLayer], [dropAreaOverlapLayer]);

        this.anchor.set(0.5);
    }
}


