import { game } from "main";

export class ClickCloudClutter extends Phaser.Particles.Arcade.Emitter
{
    public static readonly sprDotKey = "spr_dot";
    public static readonly sprDot2Key = "spr_dot_2";
    public static readonly audioDirtHitKey = "dirt_Hit";

    private _dirtSound: Phaser.Sound;

    constructor(game: Phaser.Game, x: number, y: number)
    {
        super(game, x, y);
        this._dirtSound = game.add.audio("dirt_Hit");
        this.makeParticles(["spr_dot_2"]);
        this.minParticleScale = 0.9;
        this.maxParticleScale = 0.4;
    }

    public PlayParticle(x: number, y: number, distanceMod: number)
    {
        this.x = x;
        this.y = y;

        this.setAlpha(0.5 * distanceMod / 50 + 0.4, 0, 550, Phaser.Easing.Exponential.apply, false);
        this.maxParticleScale = distanceMod;
        this.minParticleScale = distanceMod;

        this.maxParticleSpeed.setTo(160, 0);
        this.minParticleSpeed.setTo(-160);
        this.particleDrag.setTo(150 + (200 * distanceMod));

        this._dirtSound.volume = distanceMod;
        this._dirtSound.volume = this._dirtSound.volume / 2;

        this._dirtSound.play();
        this.start(true, 550, 200, 4);
    }


    public static loadAssets(game: Phaser.Game)
    {
        game.load.image(ClickCloudClutter.sprDotKey, "images/Dot.png");
        game.load.image(ClickCloudClutter.sprDot2Key, "images/Dot_2.png");
        game.load.audio(ClickCloudClutter.audioDirtHitKey, "audio/DirtHit_1.wav");
    }
}



