import
{
    ComponentAbleText, WinIfGreaterComponent,
    IWinCheckComponent, WinEquationComponent
} from "Components";
import { textStyleConfig } from "fonts";


export type WinSymbolText = ">" | "=";

export class WinSymbolObject extends ComponentAbleText
{
    private _winCheckComponent: IWinCheckComponent;
    public get winCheckComponent() { return this._winCheckComponent; }

    constructor(game: Phaser.Game, x, y, winType: WinSymbolText, numberValue: number)
    {
        super(game, x, y, "", textStyleConfig);
        switch (winType)
        {
            case ">":
                this._winCheckComponent = this.addComponent(WinIfGreaterComponent, numberValue);
                this.text = winType + " " + numberValue.toString();
                break;
            case "=":
                this._winCheckComponent = this.addComponent(WinEquationComponent);
                this.text = winType;
                break;
        }
        this.anchor.setTo(0.5);
    }
}


