import { IComponentAbleSprite, PhaserSpriteBehavior } from "Components/PhaserComponent/";
import { DragAndDropComponent } from "./dragAndDropComponent";
import { ResetAbleComponent, ResetBaseArgs } from "./resetAbleComponent";
import { SpawnerComponent } from "./spawnerComponent";
import { game } from "main";
import { MainState, events as stateEvent } from "States/";


namespace DadSpawnAnimation
{
    export type AnimationFunk = (callback: Function, animationArgs: IAnimationArgs) => void;
    export let animationFunk: AnimationFunk;
    export interface IAnimationArgs
    {
        draggedSprite: IComponentAbleSprite;
        spawnerOfSprite: IComponentAbleSprite;
    }

    function defaultSpawnAnimationFunk(callback: Function, animationArgs: IAnimationArgs)
    {
        callback();
    }

    export function setAnimationFunk(newAnimationFunk?: AnimationFunk)
    {
        if (newAnimationFunk)
        {
            animationFunk = newAnimationFunk;
        }
        else
        {
            animationFunk = defaultSpawnAnimationFunk;
        }
    }
    animationFunk = defaultSpawnAnimationFunk;
}

let currentState: MainState;

function onMainStateLoad(state: MainState)
{
    currentState = state;
}

stateEvent.onLoad.add(onMainStateLoad);

export class DadSpawnComponent extends DragAndDropComponent
{
    public static readonly componentName = "DadSpawnComponent";
    public static readonly inheritedComponentNames = [DragAndDropComponent];
    public static readonly animation = DadSpawnAnimation;

    public spawnerOfSprite: IComponentAbleSprite;
    private _state: MainState;
    private _hasDisableSpawn: boolean = false;

    public events: {
        onDragStart: Phaser.Signal;
        onDragStop: Phaser.Signal;
        onMoveTowardsDropArea: Phaser.Signal;
        onNotDropInArea: Phaser.Signal;
    };

    protected start()
    {
        let comp = this.sprite.getComponent(ResetAbleComponent);
        if (comp)
        {
            comp.events.onReset.add(this.onReset, this);
        }
    }

    protected onReset(resetArgs: ResetDadSpawnArgs)
    {
        let { dragAble } = resetArgs;

        if (dragAble !== undefined)
            this.SetDragAble(dragAble);
        else
            this.SetDragAble(true);
    }

    protected onMoveTowardsDropArea()
    {
        this._state.spawnerCompositeObjects.forEach((element) =>
        {
            if (element !== this.spawnerOfSprite)
                element.spawnerComponent.canSpawn(false);

        }, this);

        this._hasDisableSpawn = true;
    }

    protected onNotDropInAreaInAbleSpawn()
    {
        if (!this._hasDisableSpawn)
            return;

        this._state.spawnerCompositeObjects.forEach((element) =>
        {
            element.spawnerComponent.canSpawn(true);

        }, this);
    }

    protected notDropInArea()
    {
        this.SetDragAble(false);
        this.events.onNotDropInArea.dispatch(this);
        DadSpawnComponent.animation.animationFunk(animationFin.bind(this),
            { spawnerOfSprite: this.spawnerOfSprite, draggedSprite: this.sprite });
        function animationFin(this: DadSpawnComponent)
        {
            this.sprite.exists = false;
        }
    }

    public disappear()
    {
        this._dropAreaUsing = null;
        this.notDropInArea();
    }

    constructor(sprite: IComponentAbleSprite, layer: string | string[],
        overlapLayers: string[], dropHitBox?: Phaser.Rectangle)
    {
        super(sprite, layer, overlapLayers, dropHitBox);
        this.events.onNotDropInArea = new Phaser.Signal();
        this._state = currentState;
        this.events.onMoveTowardsDropArea.add(this.onMoveTowardsDropArea, this);
        this.events.onNotDropInArea.add(this.onNotDropInAreaInAbleSpawn, this);
    }
}


export interface IWithDadSpawnComponent
{
    readonly dadSpawnComponent: DadSpawnComponent;
}

export interface ResetDadSpawnArgs extends ResetBaseArgs
{
    dragAble?: boolean;
}


