import
{
    MultiplicationComponent, SymbolComponent,
    SymbolText, AdditionComponent,
    SubtractionComponent, DivisionComponent,
    ComponentAbleText, StateComponent
} from "Components/";
import { textStyleConfig } from "fonts";
import { IResetAbleSprite } from "../iResetAble";
import * as Libs from "Libs/";

const defaultSymbolObject = {
    tint: 16777215,
    alpha: 1,
    scale: { x: 1, y: 1 }
};

export class SymbolObject extends ComponentAbleText
    implements IResetAbleSprite
{
    public get symbolComponent(): SymbolComponent
    { return this._symbolStateComponent.currentComponent as SymbolComponent; }

    private _symbolStateComponent: StateComponent<SymbolComponent>;

    constructor(game: Phaser.Game, x: number, y: number, text: SymbolText, configObj: { style?: Phaser.PhaserTextStyle } = {})
    {
        super(game, x, y, text, configObj.style || textStyleConfig);

        this._symbolStateComponent = this.createStateComponent(SymbolComponent);
        this._symbolStateComponent.addComponent(AdditionComponent);
        this._symbolStateComponent.addComponent(SubtractionComponent);
        this._symbolStateComponent.addComponent(MultiplicationComponent);
        this._symbolStateComponent.addComponent(DivisionComponent);

        this.changeSymbol(text);

        this.anchor.set(0.5);
    }

    public resetSprite(x: number, y: number,
        text: SymbolText, configObj: { style?: Phaser.PhaserTextStyle } = {}): SymbolObject
    {

        let { alpha, scale, tint } = defaultSymbolObject;

        this.position.set(x, y);
        this.changeSymbol(text);
        this.setStyle(configObj.style || textStyleConfig);
        this.alpha = alpha;
        this.tint = tint;
        this.scale.set(scale.x, scale.y);
        return this;
    }

    public changeSymbol(value: SymbolText)
    {
        this.text = Libs.MathDAD.symbolTextToString(value);
        switch (value)
        {
            case "+":
                this._symbolStateComponent.SetState(AdditionComponent);
                break;
            case "-":
                this._symbolStateComponent.SetState(SubtractionComponent);
                break;
            case "*":
                this._symbolStateComponent.SetState(MultiplicationComponent);
                break;
            case "/":
                this._symbolStateComponent.SetState(DivisionComponent);
                break;
            default:
                console.warn(`symbolObject got ${value} which is not a one of the recognized symbol`);
                break;
        }
    }
}


