import { Parenthesis } from "./parenthesis";
import { DropAreaObject } from "../dropAreaObject";
import { game } from "main";

export class ParenthesisPair
{
    private _parenthesisRight: Parenthesis;
    private _parenthesisLeft: Parenthesis;

    public get parenthesisRight(): Parenthesis { return this._parenthesisRight; }
    public get parenthesisLeft(): Parenthesis { return this._parenthesisLeft; }

    public get startIndex() { return this.startBeforeDropArea.dropAreaIndex; }
    public get endIndex() { return this.endAfterDropArea.dropAreaIndex; }

    constructor(public startBeforeDropArea: DropAreaObject,
        public endAfterDropArea: DropAreaObject, public executionPriority: number,
        public defaultGroup: Phaser.Group)
    {

        this._parenthesisLeft = new Parenthesis(game, 0, 0, "(");
        defaultGroup.add(this._parenthesisLeft);

        this._parenthesisRight = new Parenthesis(game, 0, 0, ")");
        defaultGroup.add(this._parenthesisRight);
    }
}


