import { TreeParticleObject } from "GameObjects/Particles";
import { game } from "main";

export class TreeObject extends Phaser.Sprite
{

    public static readonly doodadName = "Tree";

    public treeShake: Phaser.Tween;
    public treeClickParticle: Phaser.Particles.Arcade.Emitter;


    public static loadAssets(game: Phaser.Game)
    {
        game.load.image("tree_1", "images/mathman/Objects/Tree_1.png");
        TreeParticleObject.loadAssets(game);
    }


    public treeClickShake()
    {
        if (this.treeClickParticle.parent !== this.parent)
        {
            this.parent.addChild(this.treeClickParticle);
        }

        this.treeShake = game.add.tween(this);

        this.treeShake.to({ angle: 3.5 }, 40, Phaser.Easing.Bounce.apply, false, 0, 0, true);
        this.treeShake.start();
        this.treeClickParticle.x = game.input.activePointer.x;
        this.treeClickParticle.y = game.input.activePointer.y;
        this.treeClickParticle.start(true, 2200, 200, 3);
    }

    constructor(game: Phaser.Game, x: number, y: number)
    {
        super(game, x, y, "tree_1");
        this.anchor.set(0.5, 1);
        this.treeClickParticle = new TreeParticleObject(game, 0, 0);
        this.inputEnabled = true;
        this.events.onInputDown.add(this.treeClickShake, this);
    }
}


