import { IComponentAbleSprite, PhaserSpriteBehavior } from "Components/PhaserComponent/";
import { IWithDadSpawnComponent, ResetDadSpawnArgs } from "./dadSpawnComponent";
import { ResetBaseArgs } from "./resetAbleComponent";
import { MathManger } from "Managers";
import { game } from "main";
import * as Libs from "Libs";

namespace SpawnerComponentAnimation
{
    export type SpawnAnimationFunk = (callback: Function, animationArgs: ISpawnAnimationArgs) => void;
    export type SpawnStateChangeAnimationFunk = (callback: Function,
        animationArgs: ISpawnStateChangeAnimationArgs) => void;

    export let spawnAnimationFunk: SpawnAnimationFunk;
    export let spawnStateChangeAnimation: SpawnStateChangeAnimationFunk;
    export interface ISpawnAnimationArgs
    {
        spawnerSprite: IComponentAbleSprite;
        spawnedSprite: IComponentAbleSprite;
    }

    export interface ISpawnStateChangeAnimationArgs
    {
        spawnerSprite: IComponentAbleSprite;
        canSpawn: boolean;
    }

    function defaultSpawnAnimationFunk(callback: Function, animationArgs: ISpawnAnimationArgs)
    {
        let { spawnerSprite } = animationArgs;
        let { x, y } = spawnerSprite.scale;
        x *= 1.5;
        y *= 1.5;
        game.add.tween(spawnerSprite.scale).
            to({ x, y }, 200, Phaser.Easing.Exponential.InOut, true, 0, undefined, true).
            onLoop.addOnce(callback);
    }

    function defaultSpawnStateChangeAnimation(
        callback: Function, animationArgs: ISpawnStateChangeAnimationArgs)
    {
        let { canSpawn, spawnerSprite } = animationArgs;

        let alpha = canSpawn ? 1 : 0.45;

        game.add.tween(spawnerSprite).
            to({ alpha }, 455, Phaser.Easing.Linear.None, true).
            onComplete.add(callback);
    }

    export function setSpawnAnimationFunk(newAnimationFunk?: SpawnAnimationFunk)
    {
        if (newAnimationFunk)
        {
            spawnAnimationFunk = newAnimationFunk;
        }
        else
        {
            spawnAnimationFunk = defaultSpawnAnimationFunk;
        }
    }

    export function setSpawnStateChangeAnimation(newAnimationFunk?: SpawnStateChangeAnimationFunk)
    {
        if (newAnimationFunk)
        {
            spawnStateChangeAnimation = newAnimationFunk;
        }
        else
        {
            spawnStateChangeAnimation = defaultSpawnStateChangeAnimation;
        }
    }

    spawnAnimationFunk = defaultSpawnAnimationFunk;
    spawnStateChangeAnimation = defaultSpawnStateChangeAnimation;
}

export class SpawnerComponent<T extends IComponentAbleSprite & IWithDadSpawnComponent =
    IComponentAbleSprite & IWithDadSpawnComponent> extends PhaserSpriteBehavior
{
    public static readonly componentName = "SpawnerComponent";

    public static readonly animation = SpawnerComponentAnimation;

    private _spawnSpriteArray: Libs.Phaser.SpriteArray<T>;
    private _canSpawn: boolean = true;

    private _onInputDown(eventSprite: IComponentAbleSprite, eventPointer: Phaser.Pointer)
    {
        if (!this._canSpawn)
            return;

        let { createArg, group, resetArgs } = this.defaultSpawnArgs;

        let spawnerSprite = this.sprite;
        let { x, y } = this.sprite;
        resetArgs.x = x;
        resetArgs.y = y;
        resetArgs.alpha = 1;
        resetArgs.exists = true;
        resetArgs.dragAble = true;
        let spawnedSprite = this._spawnSpriteArray.restElseCreate(group, resetArgs, x, y, ...createArg);
        spawnedSprite.dadSpawnComponent.spawnerOfSprite = this.sprite;
        Libs.Phaser.setToCurrentDrag(spawnedSprite, { x, y });
        // console.log(spawnedSprite);
        SpawnerComponent.animation.spawnAnimationFunk(animationDone.bind(this), { spawnedSprite, spawnerSprite });
        function animationDone(this: SpawnerComponent<T>)
        {
            console.log("Done");
        }
    }

    public canSpawn(canSpawn: boolean)
    {
        this._canSpawn = canSpawn;
        SpawnerComponentAnimation.
            spawnStateChangeAnimation(() => { }, { canSpawn, spawnerSprite: this.sprite });
    }

    constructor(sprite: IComponentAbleSprite, spawnSpriteArray: Libs.Phaser.SpriteArray<T>,
        public defaultSpawnArgs: { group?: Phaser.Group, resetArgs: ResetDadSpawnArgs, createArg: any[] })
    {
        super(sprite);
        sprite.inputEnabled = true;
        sprite.events.onInputDown.add(this._onInputDown, this);
        this._spawnSpriteArray = spawnSpriteArray;
        MathManger.onMathBegin.add(() => this._canSpawn = false, this);
        MathManger.onMathDoneLose.add(() => this._canSpawn = true, this);
    }

}




