import { SymbolText } from "Components/";
import
{
    DropAreaObject, DragAbleNumberObject, DragAbleSymbolObject,
    WinSymbolObject, WinSymbolText
} from "GameObjects/";
import { MainState } from "States/mainState";
import { events as mainStateEvent } from "States/Events/mainStateEvent";
import { LevelCreator, DoodadLayoutCreator, SettingsCreator } from "./SupCreators/";
import * as Libs from "Libs/";
import { defineRequire } from "outsideRequire";

export namespace GameCreator
{
    export type LevelCreatorCallback = (levelCreator: LevelCreator) => void;
    export type SettingCallback = (settingsCreator: SettingsCreator) => void;
    export type DoodadLayoutCallback = (doodadLayoutCreator: DoodadLayoutCreator) => void;

    export const orderGame: {
        onCreateLevel: LevelCreatorCallback[];
        onLoadGame: SettingCallback[];
    } = { onCreateLevel: [], onLoadGame: [] };

    export const equationGame: {
        onCreateLevel: LevelCreatorCallback[];
        onLoadGame: SettingCallback[];
    } = { onCreateLevel: [], onLoadGame: [] };

    export const onSettings: SettingCallback[] = [];
    export const doodadLayouts: ObjectDictionary<DoodadLayoutCallback> = Object.create(null);
}
export const currentDoodadLayouts = new Array<string>();

let settingSet = false;

let currentSettingsCreator: SettingsCreator;
let currentLevelCreator: LevelCreator;
let currentDoodadLayoutCreator: DoodadLayoutCreator;

mainStateEvent.onLoad.add((state) =>
{

    currentSettingsCreator = new SettingsCreator(state);
    if (!settingSet)
    {
        for (let index = 0; index < GameCreator.onSettings.length; index++)
        {
            let element = GameCreator.onSettings[index];
            element(currentSettingsCreator);
        }
        settingSet = true;
    }

    if (state.currentGameType === "equation")
    {
        for (let index = 0; index < GameCreator.equationGame.onLoadGame.length; index++)
        {
            let element = GameCreator.equationGame.onLoadGame[index];
            element(currentSettingsCreator);
        }
    }
    else if (state.currentGameType === "order")
    {
        for (let index = 0; index < GameCreator.orderGame.onLoadGame.length; index++)
        {
            let element = GameCreator.orderGame.onLoadGame[index];
            element(currentSettingsCreator);
        }
    }
});

mainStateEvent.onCreate.add((state) =>
{

    currentDoodadLayoutCreator = new DoodadLayoutCreator(state);
    currentLevelCreator = new LevelCreator(state.level, state);

    if (state.currentGameType === "equation")
    {
        GameCreator.equationGame.onCreateLevel[state.level](currentLevelCreator);
    }
    else if (state.currentGameType === "order")
    {
        GameCreator.orderGame.onCreateLevel[state.level](currentLevelCreator);
    }

    if (!Libs.stringArrayCompare(state.usedDoodadLayout, currentDoodadLayouts))
    {
        state.clearDoodads();
        for (let doodadLayoutName of currentDoodadLayouts)
            GameCreator.doodadLayouts[doodadLayoutName](currentDoodadLayoutCreator);
    }

    state.usedDoodadLayout = Libs.cloneArray(currentDoodadLayouts);

    state.dropAreaObjects.sort((a, b) => a.x - b.x);
});


