import { NumberObject, numberOverlapLayer } from "./numberObject";
import
{
    DADHomeComponent, makeComponentAbleSprite,
    IComponentAbleSprite, NumberComponent, startingObjectLayer,
} from "Components";
import { dropAreaOverlapLayer } from "../dropAreaObject";
import { NumberManager } from "Managers/";
import { textStyleConfig } from "fonts";

export class DragAbleNumberObject extends NumberObject
{
    public readonly dADHomeComponent: DADHomeComponent;

    public get homePosition(): NumberManager.HomePosition
    {
        return this.dADHomeComponent.currentHomePosition;
    }

    public set homePosition(value: NumberManager.HomePosition)
    {
        this.dADHomeComponent.currentHomePosition = value;
    }

    public resetSprite(x: number, y: number, numberValue: number,
        style: Phaser.PhaserTextStyle = textStyleConfig): NumberObject
    {
        super.resetSprite(x, y, numberValue, style);
        this.dADHomeComponent.moveHome();
        return this;
    }

    constructor(game: Phaser.Game, x: number, y: number, numberValue: number, configObj: { style?: Phaser.PhaserTextStyle } = {})
    {
        super(game, x, y, numberValue, configObj.style);
        this.dADHomeComponent = this.addComponent(DADHomeComponent,
            [numberOverlapLayer, startingObjectLayer], [dropAreaOverlapLayer]);
        this.anchor.set(0.5);
    }
}






