import { ISymbolObject, INumberObject } from "GameObjects/";

export class SymbolNumberGroup
{
    public rightNumberObject: INumberObject;
    public leftNumberObject: INumberObject;
    public symbolObject: ISymbolObject;

    public GetValue(): { numberValue: number, isX: boolean }
    {
        return this.symbolObject.symbolComponent.
            calc(this.leftNumberObject,
            this.rightNumberObject);
    }

    constructor(rightNumberObject, leftNumberObject, symbolObject)
    {
        this.rightNumberObject = rightNumberObject;
        this.leftNumberObject = leftNumberObject;
        this.symbolObject = symbolObject;
    }
}


export function isSymbolNumberGroup(obj): obj is SymbolNumberGroup
{
    return obj instanceof SymbolNumberGroup;
}


