import { ComponentAbleText } from "Components";
import { textStyleConfig } from "fonts";
export class Parenthesis extends ComponentAbleText
{
    constructor(game: Phaser.Game, x: number, y: number, symbol: "(" | ")")
    {
        super(game, x, y, symbol, textStyleConfig);
        this.anchor.setTo(0.5);
        this.fontSize = 80;

    }
}


