import
{
    DropAreaObject, INumberObject, DragAbleSymbolObject, WinSymbolObject
    , ParenthesisPair, ISymbolObject, CompositeObject
} from "GameObjects/";
import { game } from "main";
import
{
    beginAnimationFunc, processBeginAnimationFunc, processEndAnimationFunc,
    BaseAnimationArgs, ProcessBeginArgs, ProcessEndArgs,
    loseAnimationFunc, winAnimationFunc, parenthesisDoneAnimationFunc,
    splitAnimationFunc, SplitArgs, CompositeAndParts,
    loseEquationAnimationFunc, edgeCaseRemoveZeroAnima, EdgeCaseRemoveZeroArgs,
    edgeCaseSpawnSymbolAnima, EdgeCaseSpawnSymbolArgs
} from "./mathAnimation";
import * as Libs from "Libs/";
import * as Comps from "Components/";
import { SpriteArray } from "Libs/Phaser/";

export class MathProcess
{
    private _callbackAndContext: { callback: Function, context?: any };

    private _numberValues: Libs.ValueIndex<INumberObject>[];
    private _symbolValues: Libs.ValueIndex<ISymbolObject>[];
    private _compositeValues: Libs.ValueIndex<CompositeObject>[];
    private _intermittedNumberArray: SpriteArray<INumberObject>;
    private _intermittedSymbolArray: SpriteArray<ISymbolObject>;
    private _dropAreas: DropAreaObject[];
    private _winSymbol: WinSymbolObject;
    private _parenthesisPairs: ParenthesisPair[];
    private _parenthesisPairsCounter: number = 0;
    private _newestNumberValue: Libs.ValueIndex<INumberObject>;

    private _currentSymbol: Libs.ValueIndex<ISymbolObject>;
    private _currentLeftNumber?: Libs.ValueIndex<INumberObject>;
    private _currentRightNumber?: Libs.ValueIndex<INumberObject>;

    private _secondStageStartDropArea: DropAreaObject | null;
    private _isInSecondStage: boolean;
    private _currentSymbols: Libs.ValueIndex<ISymbolObject>[];


    constructor()
    {
    }

    public getSymbols(): Libs.ValueIndex<ISymbolObject>[]
    {
        return this._symbolValues;
    }

    public getNumbers(): Libs.ValueIndex<INumberObject>[]
    {
        return this._numberValues;
    }

    public getSecondStageStartDropArea(): DropAreaObject | null
    {
        return this._secondStageStartDropArea;
    }

    public getWinSymbol(): WinSymbolObject
    {
        return this._winSymbol;
    }

    public beginProcess(numberValues: Libs.ValueIndex<INumberObject>[], symbolValues: Libs.ValueIndex<ISymbolObject>[],
        compositeValues: Libs.ValueIndex<CompositeObject>[], intermittedNumberArray: SpriteArray<INumberObject>,
        intermittedSymbolArray: SpriteArray<ISymbolObject>,
        dropAreas: DropAreaObject[], winSymbol: WinSymbolObject,
        parenthesisPairs: ParenthesisPair[],
        secondStageStartDropArea: DropAreaObject | null, callback: (isAWin: boolean) => void, context?: any)
    {
        this._callbackAndContext = { callback, context };
        this._numberValues = numberValues;
        this._symbolValues = symbolValues;
        this._compositeValues = compositeValues;
        this._intermittedNumberArray = intermittedNumberArray;
        this._intermittedSymbolArray = intermittedSymbolArray;
        this._winSymbol = winSymbol;
        this._dropAreas = dropAreas;
        this._parenthesisPairs = parenthesisPairs;
        this._parenthesisPairs.sort((a, b) => a.executionPriority - b.executionPriority);
        this._secondStageStartDropArea = secondStageStartDropArea;
        this._isInSecondStage = false;

        this._parenthesisPairsCounter = 0;

        let upOne = true;
        while (this._parenthesisPairsCounter < this._parenthesisPairs.length && upOne)
        {
            upOne = !this._symbolValues.some((i) =>
            {
                let currentParenthesisPair = this._parenthesisPairs[this._parenthesisPairsCounter];
                return i.index >= currentParenthesisPair.startIndex && i.index <= currentParenthesisPair.endIndex;

            }, this);

            if (upOne)
            {
                this._parenthesisPairsCounter += 1;
            }
        }

        splitAnimationFunc(splitAnimationDone.bind(this), this.createSplitArgs());

        function splitAnimationDone(this: MathProcess)
        {
            for (let compositeValue of this._compositeValues)
            {
                let { numberObject, symbolObject } = compositeValue.value.split();
                let { index } = compositeValue;
                compositeValue.value.exists = false;
                this._numberValues.push(Libs.createValueIndex(numberObject, index + 0.5));
                this._symbolValues.push(Libs.createValueIndex(symbolObject, index - 0.5));
            }
            this._currentSymbols = this._getCurrentSymbols();
            beginAnimationFunc(this._process.bind(this), this.createBaseAnimationArgs());
        }
    }

    private _process()
    {
        if (this.isProcessDone())
        {
            if (!this._isInSecondStage)
            {
                this._isInSecondStage = true;
                this._currentSymbols = this._getCurrentSymbols();
                this._checkForEdgeCases(this._process.bind(this));
            }
            else
            {
                this._processEnd();
            }
            return;
        }

        this._currentSymbol = this._popNextSymbol();

        let { right, left } = this._currentSymbol.value.
            symbolComponent.getSymbolsNumbers(this._numberValues, this._currentSymbol.index);

        this._currentRightNumber = right;
        this._currentLeftNumber = left;
        let point = { x: this._currentSymbol.value.position.x, y: this._currentSymbol.value.position.y };

        processBeginAnimationFunc(animationFin.bind(this), this.createProcessBeginArgs());

        function animationFin(this: MathProcess)
        {
            this._currentSymbol.value.exists = false;
            Libs.removeFromArray(this._symbolValues, this._currentSymbol);
            if (left)
            {
                left.value.exists = false;
                Libs.removeFromArray(this._numberValues, left);
            }
            if (right)
            {
                right.value.exists = false;
                Libs.removeFromArray(this._numberValues, right);
            }

            let resultNumber = { numberValue: 0, isX: false };
            if (right && left)
            {
                resultNumber = this._currentSymbol.value.symbolComponent.
                    calc(left.value, right.value);
            }
            else if (right)
            {
                resultNumber = this._currentSymbol.value.symbolComponent.
                    calc(right.value);
            }
            else if (left)
            {
                resultNumber = this._currentSymbol.value.symbolComponent.
                    calc(left.value);
            }

            let { x, y } = this._currentSymbol.value.position;
            let newIntermittedNumber = this._intermittedNumberArray.find(i => !i.exists);
            if (!newIntermittedNumber)
                newIntermittedNumber = this._intermittedNumberArray.create(undefined, x, y, resultNumber.numberValue);
            else
                newIntermittedNumber.restNumberObject(x, y, resultNumber.numberValue);

            newIntermittedNumber.numberComponent.numberRepresenting = resultNumber.numberValue;
            newIntermittedNumber.numberComponent.isX = resultNumber.isX;

            this._newestNumberValue = { value: newIntermittedNumber, index: this._currentSymbol.index };
            this._numberValues.push(this._newestNumberValue);


            processEndAnimationFunc(animationFin.bind(this), this.createProcessEndArgs());
            function animationFin(this: MathProcess)
            {
                let upOne = false;
                if (this._parenthesisPairsCounter < this._parenthesisPairs.length)
                {
                    upOne = !this._symbolValues.some((i) =>
                    {
                        let currentParenthesisPair = this._parenthesisPairs[this._parenthesisPairsCounter];
                        return i.index >= currentParenthesisPair.startIndex && i.index <= currentParenthesisPair.endIndex;

                    }, this);
                }
                if (upOne)
                {
                    parenthesisDoneAnimationFunc(this._process.bind(this), this.createProcessEndArgs());
                    this._parenthesisPairsCounter += 1;
                }
                else
                {
                    this._process();
                }
            }
        }
    }

    private _processEnd()
    {
        let { callback, context } = this._callbackAndContext;
        let isAWin = this._winSymbol.winCheckComponent.checkForWin(this._numberValues, this._symbolValues);

        if (isAWin)
        {
            winAnimationFunc(callback.bind(context, isAWin), this.createProcessEndArgs());
        }
        else
        {
            if (this._winSymbol.winCheckComponent instanceof Comps.WinEquationComponent)
                loseEquationAnimationFunc(callback.bind(context, isAWin), this.createBaseAnimationArgs());
            else
                loseAnimationFunc(callback.bind(context, isAWin), this.createProcessEndArgs());
        }
    }

    private _checkForEdgeCases(callback: Function)
    {
        let secondStageStartDropArea = this._secondStageStartDropArea;
        if (secondStageStartDropArea !== null)
        {
            let rightNumberValues = this._numberValues.
                filter(i => i.index < (<DropAreaObject>secondStageStartDropArea).dropAreaIndex);
            let rightHasAnySymbol = this._symbolValues.
                some(i => i.index < (<DropAreaObject>secondStageStartDropArea).dropAreaIndex);
            if (rightNumberValues.length === 2 && !rightHasAnySymbol)
            {
                let secondNumber = rightNumberValues[0];
                if (secondNumber.index < rightNumberValues[1].index)
                {
                    secondNumber = rightNumberValues[1];
                }

                if (secondNumber.value.numberValue === 0)
                {
                    let args = this.createBaseAnimationArgs() as EdgeCaseRemoveZeroArgs;
                    args.numberObjectZero = secondNumber.value;
                    edgeCaseRemoveZeroAnima(zeroAnimationDone.bind(this, secondNumber), args);
                    return;
                }
                else if (secondNumber.value.numberValue > 0)
                {
                    let spawnedSymbol = this._intermittedSymbolArray.find(i => !i.exists);
                    if (spawnedSymbol === undefined)
                    {
                        spawnedSymbol = this._intermittedSymbolArray.create(undefined, 0, 0, "+");
                    }
                    else
                    {
                        if (spawnedSymbol.changeSymbol)
                            spawnedSymbol.changeSymbol("+");

                        spawnedSymbol.position.setTo(0, 0);
                        spawnedSymbol.exists = true;
                        spawnedSymbol.alpha = 1;
                    }
                    let animationArgs = this.createBaseAnimationArgs() as EdgeCaseSpawnSymbolArgs;
                    let spawnedSymbolIndex = (rightNumberValues[0].index + rightNumberValues[1].index) / 2;

                    this._symbolValues.push(Libs.createValueIndex(spawnedSymbol, spawnedSymbolIndex));
                    animationArgs.newSymbolObject = spawnedSymbol;
                    edgeCaseSpawnSymbolAnima(callback, animationArgs);
                    return;
                }
                else
                {
                    let spawnedSymbol = this._intermittedSymbolArray.find(i => !i.exists);
                    if (spawnedSymbol === undefined)
                    {
                        spawnedSymbol = this._intermittedSymbolArray.create(undefined, 0, 0, "-");
                    }
                    else
                    {
                        if (spawnedSymbol.changeSymbol)
                            spawnedSymbol.changeSymbol("-");

                        spawnedSymbol.position.setTo(0, 0);
                        spawnedSymbol.exists = true;
                        spawnedSymbol.alpha = 1;
                    }
                    secondNumber.value.numberValue *= -1;
                    let animationArgs = this.createBaseAnimationArgs() as EdgeCaseSpawnSymbolArgs;
                    animationArgs.newSymbolObject = spawnedSymbol;
                    edgeCaseSpawnSymbolAnima(callback, animationArgs);
                    return;
                }
            }
        }

        function zeroAnimationDone(this: MathProcess, zero: Libs.ValueIndex<INumberObject>)
        {
            zero.value.exists = false;
            Libs.removeFromArray(this._numberValues, zero);
            callback();
        }
        callback();
    }

    private _popNextSymbol(): Libs.ValueIndex<ISymbolObject>
    {
        let symbolsToUse: Libs.ValueIndex<ISymbolObject>[];

        let highestItem: Libs.ValueIndex<ISymbolObject> | null = null;

        if (this._parenthesisPairsCounter < this._parenthesisPairs.length)
        {
            symbolsToUse = this._currentSymbols.filter((i) =>
            {
                let currentParenthesisPair = this._parenthesisPairs[this._parenthesisPairsCounter];
                return i.index >= currentParenthesisPair.startIndex && i.index <= currentParenthesisPair.endIndex;

            }, this);
        }
        else
        {
            symbolsToUse = this._currentSymbols;
        }

        for (let index = 0; index < symbolsToUse.length; index++)
        {
            let newItem = symbolsToUse[index];

            if (!isCalcValid(newItem, this._numberValues))
                continue;

            if (highestItem === null || newItem.value.symbolComponent.priority > highestItem.value.symbolComponent.priority
                || newItem.value.symbolComponent.priority === highestItem.value.symbolComponent.priority
                && newItem.index < highestItem.index)
            {
                highestItem = newItem;
            }
        }
        Libs.removeFromArray(this._currentSymbols, highestItem);

        if (highestItem === null)
            throw "no valid Symbol";

        return highestItem;
    }

    private _getCurrentSymbols(): Libs.ValueIndex<ISymbolObject>[]
    {
        let secondStageStartDropArea = this._secondStageStartDropArea;
        if (secondStageStartDropArea !== null)
        {
            if (this._isInSecondStage)
                return this._symbolValues.filter(i => i.index >= (<any>secondStageStartDropArea).dropAreaIndex);
            else
                return this._symbolValues.filter(i => i.index < (<any>secondStageStartDropArea).dropAreaIndex);
        }

        return this._symbolValues;
    }

    public createBaseAnimationArgs(): BaseAnimationArgs
    {
        let otherParenthesisPair = this._parenthesisPairs.filter((e, i) => i >= this._parenthesisPairsCounter, this);
        return {
            dropAreas: this._dropAreas, otherNumberValues: this._numberValues,
            otherSymbolValues: this._symbolValues, winSymbol: this._winSymbol, otherParenthesisPair,
            rightSideStartIndex: this._secondStageStartDropArea ? this._secondStageStartDropArea.dropAreaIndex : undefined
        };
    }

    public createProcessBeginArgs(): ProcessBeginArgs
    {
        let baseValue: ProcessBeginArgs = this.createBaseAnimationArgs() as ProcessBeginArgs;
        baseValue.currentSymbol = this._currentSymbol;
        baseValue.left = this._currentLeftNumber;

        baseValue.currentParenthesisPair = this._parenthesisPairs.length > this._parenthesisPairsCounter ?
            this._parenthesisPairs[this._parenthesisPairsCounter] : null;

        baseValue.right = this._currentRightNumber;

        Libs.removeFromArray(baseValue.otherParenthesisPair, baseValue.currentParenthesisPair);

        return baseValue;
    }

    public createProcessEndArgs(): ProcessEndArgs
    {
        let baseValue: ProcessEndArgs = this.createBaseAnimationArgs() as ProcessEndArgs;
        baseValue.newNumber = this._newestNumberValue;
        baseValue.currentParenthesisPair = this._parenthesisPairs.length > this._parenthesisPairsCounter ?
            this._parenthesisPairs[this._parenthesisPairsCounter] : null;

        Libs.removeFromArray(baseValue.otherParenthesisPair, baseValue.currentParenthesisPair);

        return baseValue;
    }

    public createSplitArgs(): SplitArgs
    {
        let returnValue: SplitArgs;
        let compositesAndPartsIndex: Libs.ValueIndex<CompositeAndParts>[] = [];

        for (let compositeObjectIndex of this._compositeValues)
        {
            let { numberObject, symbolObject } = compositeObjectIndex.value.split();
            let compositeObject = compositeObjectIndex.value;
            let { index } = compositeObjectIndex;
            let compositeAndParts: CompositeAndParts = { numberObject, symbolObject, compositeObject };
            compositesAndPartsIndex.push(Libs.createValueIndex(compositeAndParts, index));
        }
        returnValue = { compositesAndPartsIndex };
        return returnValue;
    }

    public isProcessDone(): boolean
    {
        for (let symbolValue of this._currentSymbols)
        {
            if (isCalcValid(symbolValue, this._numberValues))
            {
                return false;
            }
        }
        return true;
    }
}




function isCalcValid(symbolValue: Libs.ValueIndex<ISymbolObject>,
    numberValues: Libs.ValueIndex<INumberObject>[]): boolean
{
    let symbolIndex = symbolValue.index;
    let symbolComp = symbolValue.value.symbolComponent;
    let { left, right } = symbolComp.getSymbolsNumbers(numberValues, symbolIndex);
    if (right && left)
    {
        return symbolComp.
            isCalcValid(left.value, right.value);
    }
    else if (right)
    {
        return symbolComp.isCalcValid(right.value);
    }
    else if (left)
    {
        return symbolComp.isCalcValid(left.value);
    }

    return false;
}


