import { addDoodadClass } from "GameCreator/SupCreators/doodadLayoutCreator";
import { IComponentAbleSprite } from "Components/";


export function registerDoodad(name: string)
{
    return function (target: new (...args) => IComponentAbleSprite)
    {
        // console.log(name);
        addDoodadClass(name, target);
    };
}


