import
{
    IComponentAbleSprite, PhaserSpriteBehavior,
    InheritableComponentType
} from "./PhaserComponent/";
import { SymbolComponent } from "Components/SymbolComponents/";
import { ICheckNextAble, ICheckNextAbleType } from "./iCheckNextAble";


export class MathOwnedByComponent extends PhaserSpriteBehavior
{
    public static readonly componentName: string = "MathOwnedByComponent";
    public static readonly inheritedComponentNames: InheritableComponentType[];

    public ownedBy: IMathOwner | null;

    constructor(sprite: Phaser.Text & IComponentAbleSprite,
        owner: IMathOwner | null = null)
    {
        super(sprite);
        this.ownedBy = owner;
    }
}

export interface IMathOwner
{
    getMathObjectOwned(sprites: IComponentAbleSprite[]);
}


