import { NumberComponent, SymbolComponent } from "./symbolComponent";
import { IComponentAbleSprite } from "Components/PhaserComponent/";
import { MathDAD } from "Libs";

export class AdditionComponent extends SymbolComponent
{
    public static readonly componentName: string = "SymbolAdditionComponet";
    public static readonly inheritedComponetNames = [SymbolComponent];

    protected _symbolName = "Addition";

    public priority = 1;

    public calc(left: MathDAD.XNumberValue, right: MathDAD.XNumberValue): { numberValue: number, isX: boolean }
    {
        let numberValue = left.numberValue + right.numberValue;
        let isX = left.isX || right.isX;
        return { numberValue, isX };
    }

    public isCalcValid(left: MathDAD.XNumberValue, right: MathDAD.XNumberValue): boolean
    {
        return left.isX === right.isX || right.numberValue === 0 || left.numberValue === 0;
    }
}


