import
{
    IComponentAbleSprite, PhaserSpriteBehavior,
    InheritableComponentType
} from "./PhaserComponent/";
import { SymbolComponent } from "Components/SymbolComponents/";
import { ICheckNextAble, ICheckNextAbleType } from "./iCheckNextAble";


export class NumberComponent extends PhaserSpriteBehavior implements ICheckNextAble
{

    public static readonly componentName: string = "NumberComponent";
    public static readonly inheritedComponentNames: InheritableComponentType[] = [ICheckNextAbleType];

    private _numberRepresenting: number;
    private _divisionStyling = false;
    private _isX = false;
    public get isX() { return this._isX; }
    public set isX(value: boolean)
    {
        if (value !== this._isX)
        {
            this.events.onNumberChange.dispatch(this._numberRepresenting, value, this._divisionStyling);
            this._isX = value;
        }
    }

    public readonly sprite: Phaser.Text & IComponentAbleSprite;
    public events: { onNumberChange: Phaser.Signal };

    public get divisionStyling() { return this._divisionStyling; }
    public set divisionStyling(value)
    {
        if (value !== this._divisionStyling)
        {
            this.events.onNumberChange.dispatch(this._numberRepresenting, this._isX, value);
            this._divisionStyling = value;
        }
    }

    public get numberRepresenting()
    {
        return this._numberRepresenting;
    }

    public set numberRepresenting(value: number)
    {
        if (value !== this._numberRepresenting)
        {
            this.events.onNumberChange.dispatch(value, this._isX, this._divisionStyling);
            this._numberRepresenting = value;
        }
    }

    public checkNext(next: IComponentAbleSprite | undefined | null): boolean
    {
        if (!next)
            return true;

        let comp = next.getComponent(SymbolComponent);
        if (comp)
            return true;

        if (next.getComponent("CompositeComponent"))
            return true;

        return false;
    }

    constructor(sprite: Phaser.Text & IComponentAbleSprite, numberRepresenting: number)
    {
        super(sprite);
        this.events = { onNumberChange: new Phaser.Signal() };
        this.numberRepresenting = numberRepresenting;
    }
}


