import { game } from "main";

export class TreeParticleObject extends Phaser.Particles.Arcade.Emitter
{

    constructor(game: Phaser.Game, x: number, y: number)
    {
        super(game, x, y);
        this.makeParticles("spr_leaf_1");
        this.minParticleScale = 0.2;
        this.maxParticleScale = 0.4;
        this.setAlpha(1, 0, 3200, Phaser.Easing.Sinusoidal.apply);
        this.maxParticleSpeed.set(220, 0);
        this.minParticleSpeed.set(-220, -100);
        this.particleDrag.x = 800;
        this.particleDrag.y = 150;
    }


    public static loadAssets(game: Phaser.Game)
    {
        game.load.image("spr_leaf_1", "images/mathman/Objects/leaf_1.png");
    }
}



