import { IComponentAbleSprite, PhaserSpriteBehavior } from "Components/PhaserComponent/";
import { SymbolComponent, NumberComponent } from "Components/SymbolComponents/symbolComponent";
import * as Libs from "Libs/";
import { INumberObject } from "GameObjects";
import { MathDAD } from "Libs/";


export class RootComponent extends SymbolComponent
{
    public static readonly componentName = "RootComponent";
    public static readonly inheritedComponentNames = [SymbolComponent];
    public priority = 3;

    protected _symbolName = "exponentiation";

    public calc(right: MathDAD.XNumberValue): { numberValue: number, isX: boolean }
    {

        let numberValue = 0;

        if (right.numberValue < 0)
        {
            numberValue = NaN;
        }

        if (this.rootValue === 2)
        {
            numberValue = Math.sqrt(right.numberValue);
        }
        else
        {
            numberValue = right.numberValue ** (1 / this.rootValue);
        }

        let isX = right.isX;

        return { numberValue, isX };
    }

    public getSymbolsNumbers(numberValues: Libs.ValueIndex<INumberObject>[], symbolIndex: number):
        { right?: Libs.ValueIndex<INumberObject>, left?: Libs.ValueIndex<INumberObject> }
    {
        let returnValues: { right?, left?} = {};

        returnValues.right = Libs.
            findMinElement(numberValues.filter(vi => vi.index > symbolIndex),
            i => i.index);

        return returnValues;
    }

    constructor(sprite: IComponentAbleSprite,
        public rootValue = 2)
    {
        super(sprite);
    }
}


