import { CloudBurstComponent, IComponentAbleSprite, ComponentAbleSprite } from "Components/";
import { registerDoodad } from "./decorators";
import { assetLoadMethod, StateEnum } from "assetsLoader";


export const World2HillSprites = ["world_2_hill_5", "world_2_hill_4",
    "world_2_hill_3", "world_2_hill_2", "world_2_hill_1"];


@registerDoodad("HillsWorld2")
export class HillScene2 extends ComponentAbleSprite
{
    public readonly cloudBurstComponent: CloudBurstComponent;

    public setCloudBurst(distanceMod: number, pixelPerfectClick = false)
    {
        this.cloudBurstComponent.distanceMod = distanceMod;
        this.cloudBurstComponent.pixelPerfectClick = pixelPerfectClick;
    }

    constructor(game: Phaser.Game, x: number, y: number, hillKey: number, distanceMod: number, pixelPerfectClick = false)
    {
        super(game, x, y, World2HillSprites[hillKey]);
        this.cloudBurstComponent = this.addComponent(CloudBurstComponent, distanceMod, pixelPerfectClick);
    }

    @assetLoadMethod(StateEnum.Main)
    private static loadAssetsHill(game: Phaser.Game)
    {
        game.load.image(World2HillSprites[4], "images/mathman/World2/bg5.png");
        game.load.image(World2HillSprites[3], "images/mathman/World2/bg4.png");
        game.load.image(World2HillSprites[2], "images/mathman/World2/bg3.png");
        game.load.image(World2HillSprites[1], "images/mathman/World2/bg2.png");
        game.load.image(World2HillSprites[0], "images/mathman/World2/bg1.png");

    }
}




