import { IComponentAbleSprite, PhaserSpriteBehavior } from "Components/PhaserComponent/";


export class ResetAbleComponent extends PhaserSpriteBehavior
{
    public static readonly componentName = "ResetAbleComponent";
    public events = { onReset: new Phaser.Signal };

    public resetObject(resetArgs: ResetBaseArgs)
    {
        let { x, y, scale, exists, alpha, tint } = resetArgs;

        if (x)
            this.sprite.position.x = x;

        if (y)
            this.sprite.position.y = y;

        if (scale)
            this.sprite.scale.setTo(scale.x, scale.y);

        if (exists !== undefined)
            this.sprite.exists = exists;
        else
            this.sprite.exists = true;

        if (alpha)
            this.sprite.alpha = alpha;

        if (tint)
            this.sprite.tint = tint;

        this.events.onReset.dispatch(resetArgs);
    }
}



export interface ResetBaseArgs
{
    x?: number;
    y?: number;
    scale?: { x, y };
    exists?: boolean;
    tint?: number;
    alpha?: number;
}


