import { MainState } from "States/mainState";
import { LevelSelectState } from "States/LevelSelect/levelSelectState";
import { PreloadState } from "States/preloadState";
import { defineRequire } from "outsideRequire";
import { levelSelectInfo } from "States/LevelSelect/levelSelectInfo";
import "LevelCreation/test";

export let game: Phaser.Game;

let preloadState;
let levelSelectState;
let mainState;
let setupDone = false;

// window.onload = () =>
// {
//     const preloadState = new PreloadState("levelSelect");
//     const levelSelectState = new LevelSelectState();
//     const mainState = new MainState();

//     game = new Phaser.Game(800, 600, Phaser.AUTO, "content", preloadState);
//     game.state.add("levelSelect", levelSelectState);
//     game.state.add("main", mainState);

// };

(window as any).startGameOrderOrEquation = function (gameToUse: Phaser.Game, gameToPlay: GameTypes)
{
    levelSelectInfo.gameBeginPlayed = gameToPlay;
    game = gameToUse;

    if (!setupDone)
    {

        preloadState = new PreloadState("levelSelect");
        game.state.add("preloadState", preloadState);
        levelSelectState = new LevelSelectState();
        game.state.add("levelSelect", levelSelectState);
        mainState = new MainState();
        game.state.add("main", mainState);
        setupDone = true;
        game.state.start("preloadState");

    }
    else
    {
        game.state.start("levelSelect");
    }
};

defineRequire("code/main", this);


