import * as stateEvent from "./Events/mainStateEvent";
import { textStyleConfig } from "fonts";
import
{
    DropAreaObject, DragAbleNumberObject,
    NumberObject, DragAbleSymbolObject,
    WinSymbolObject, SymbolObject, makeParenthesisPairArray,
    ParenthesisPairArray, RootObject, ExponentiationObject,
    CompositeObject, CompositeSpawner, SpawnCompositeObject,
    TemporaryNumberObject
} from "GameObjects";
import { makeSpriteArray, SpriteArray } from "Libs/Phaser";
import { jsonGlobalObject as jGlobal } from "Managers/globalSettingsManger";
import { loadMainSceneAsset, StateEnum } from "assetsLoader";
import * as Libs from "Libs/";
import "GameCreator/";
import "Managers/";

export class MainState extends Phaser.State
{
    private _level: number = 0;
    private _firstCreateCalled = false;
    private _debugKeySet = false;

    public get level()
    {
        return this._level;
    }

    public set level(level)
    {
        this._level = level;
    }

    public currentGameType: GameTypes = "order";

    public genericGroups: Phaser.Group[] = new Array(6);
    public dropSquareGroup: Phaser.Group;
    public winSymbolGroup: Phaser.Group;
    public numberObjectGroup: Phaser.Group;
    public symbolObjectGroup: Phaser.Group;
    public compositeGroup: Phaser.Group;


    public dropAreaObjects: SpriteArray<DropAreaObject>;
    public intermittedNumberObjects: SpriteArray<TemporaryNumberObject>;
    public fixedNumberObjects: SpriteArray<NumberObject>;
    public dragAbleNumberObjects: SpriteArray<DragAbleNumberObject>;
    public fixedSymbolObjects: SpriteArray<SymbolObject>;
    public dragAbleSymbolObjects: SpriteArray<DragAbleSymbolObject>;
    public winSymbolObjects: SpriteArray<WinSymbolObject>;
    public rootSymbolObjects: SpriteArray<RootObject>;
    public exponentiationSymbolObjects: SpriteArray<ExponentiationObject>;
    public parenthesisPairObjects: ParenthesisPairArray;
    public compositeObjects: SpriteArray<CompositeObject>;
    public compositeSpawnObject: SpriteArray<SpawnCompositeObject>;
    public spawnerCompositeObjects: SpriteArray<CompositeSpawner>;
    public intermittedSymbolsObjects: SpriteArray<SymbolObject>;


    public secondStageStartDropArea: DropAreaObject | null = null;


    public usedDoodadLayout: string[] = [];

    public customDoodadsSpriteArrays: SpriteArrayDictionary = Object.create(null);

    public init(level?: number, gameType?: GameTypes)
    {
        if (level)
            this.level = level;

        this._firstCreateCalled = false;
        this.usedDoodadLayout.length = 0;
        if (gameType)
            this.currentGameType = gameType;

        this.input.onDown.add(() =>
        {
            if (!this.scale.isFullScreen && jGlobal.fullScreen)
            {
                this.scale.startFullScreen(true, true);
            }
        });

        if (window.__Game__DEBUG)
        {
            this.input.keyboard.addKey(Phaser.Keyboard.R).onDown.add(() =>
            {
                this.game.state.restart(true);
            }, this);

            this.input.keyboard.addKey(Phaser.Keyboard.ZERO).onDown.add(() =>
            {
                this._level = 0;
            }, this);

            this.input.keyboard.addKey(Phaser.Keyboard.ONE).onDown.add(() =>
            {
                this._level = 1;
            }, this);

            this.input.keyboard.addKey(Phaser.Keyboard.TWO).onDown.add(() =>
            {
                this._level = 2;
            }, this);

            this.input.keyboard.addKey(Phaser.Keyboard.THREE).onDown.add(() =>
            {
                this._level = 3;
            }, this);

            // level skip
            this.input.keyboard.addKey(Phaser.Keyboard.SPACEBAR).onDown.add(() =>
            {
                if (this._level < 39)
                {
                    this.softCutNextLevel();
                    console.log("Skipped to level: " + (this._level + 1));
                }

            }, this);
            // level return
            this.input.keyboard.addKey(Phaser.Keyboard.BACKSPACE).onDown.add(() =>
            {
                if (this._level > 0)
                {
                    this.softCutNextLevel(-1);
                    console.log("Returned to level: " + (this._level + 1));
                }

            }, this);

            this.input.keyboard.addKey(Phaser.Keyboard.L).onDown.add(() =>
            {
                this.state.start("levelSelect");
            });

            this._debugKeySet = true;
        }
    }

    public clearSpriteArrays(withDoodads: boolean = true)
    {
        if (withDoodads)
        {
            for (let name in this.customDoodadsSpriteArrays)
            {
                this.customDoodadsSpriteArrays[name].length = 0;
            }
        }

        this.dropAreaObjects.length = 0;
        this.intermittedNumberObjects.length = 0;
        this.fixedNumberObjects.length = 0;
        this.dragAbleNumberObjects.length = 0;
        this.fixedSymbolObjects.length = 0;
        this.dragAbleSymbolObjects.length = 0;
        this.winSymbolObjects.length = 0;
        this.rootSymbolObjects.length = 0;
        this.exponentiationSymbolObjects.length = 0;
        this.parenthesisPairObjects.length = 0;
        this.compositeObjects.length = 0;
        this.compositeSpawnObject.length = 0;
        this.spawnerCompositeObjects.length = 0;
    }

    public preload()
    {
        this.load.baseURL = "./assets/";
        loadMainSceneAsset(StateEnum.Main);
        stateEvent.runOnLoad(this);
    }

    public create()
    {

        if (!this._firstCreateCalled)
        {
            for (let index = 0; index < this.genericGroups.length; index++)
                this.genericGroups[index] = this.add.group();

            this.dropSquareGroup = this.add.group();
            this.winSymbolGroup = this.add.group();
            this.symbolObjectGroup = this.add.group();
            this.numberObjectGroup = this.add.group();
            this.compositeGroup = this.add.group();

            this.dropAreaObjects = makeSpriteArray(this, DropAreaObject, this.dropSquareGroup);
            this.intermittedNumberObjects = makeSpriteArray(this, TemporaryNumberObject, this.numberObjectGroup);
            this.fixedNumberObjects = makeSpriteArray(this, NumberObject, this.numberObjectGroup);
            this.dragAbleNumberObjects = makeSpriteArray(this, DragAbleNumberObject, this.numberObjectGroup);
            this.fixedSymbolObjects = makeSpriteArray(this, SymbolObject, this.symbolObjectGroup);
            this.dragAbleSymbolObjects = makeSpriteArray(this, DragAbleSymbolObject, this.symbolObjectGroup);
            this.winSymbolObjects = makeSpriteArray(this, WinSymbolObject, this.winSymbolGroup);
            this.rootSymbolObjects = makeSpriteArray(this, RootObject, this.symbolObjectGroup);
            this.exponentiationSymbolObjects = makeSpriteArray(this, ExponentiationObject, this.symbolObjectGroup);
            this.parenthesisPairObjects = makeParenthesisPairArray(this.symbolObjectGroup);
            this.compositeObjects = makeSpriteArray(this, CompositeObject, this.compositeGroup);
            this.compositeObjects.defaultCreateArgs = [0, 0, "+", -1, this.symbolObjectGroup, this.numberObjectGroup];
            this.compositeSpawnObject = makeSpriteArray(this, SpawnCompositeObject, this.compositeGroup);
            this.compositeSpawnObject.defaultCreateArgs = [0, 0, "+", -1, this.symbolObjectGroup, this.numberObjectGroup];
            this.spawnerCompositeObjects = makeSpriteArray(this, CompositeSpawner, this.numberObjectGroup);
            this.spawnerCompositeObjects.defaultCreateArgs = [0, 0, "+", -1, this.compositeSpawnObject];

            this.intermittedSymbolsObjects = makeSpriteArray(this, SymbolObject, this.symbolObjectGroup);

            this.physics.startSystem(Phaser.Physics.ARCADE);
            this._firstCreateCalled = true;
        }

        stateEvent.runOnCreate(this);

        this.dropAreaObjects.sort((a, b) => a.x - b.x);
        for (let index = 0; index < this.dropAreaObjects.length; index++)
        {
            let element = this.dropAreaObjects[index];
            element.dropAreaIndex = index;
        }
    }

    public update()
    {
        stateEvent.runOnUpdate(this);
    }

    public render()
    {
        stateEvent.runOnRender(this);
    }

    public shutdown()
    {
        this.clearSpriteArrays();
        this.winSymbolGroup.removeAll(true);
        this.numberObjectGroup.removeAll(true);
        this.symbolObjectGroup.removeAll(true);
        for (let genericGroup of this.genericGroups)
        {
            genericGroup.removeAll(true);
        }
        this.dropSquareGroup.removeAll(true);
        this.compositeGroup.removeAll(true);
        stateEvent.runOnShutdown(this);
    }

    /**
     * hardCutNextLevel
     */
    public hardCutNextLevel()
    {
        this._level += 1;
        this.game.state.restart(true);
    }

    public softCutNextLevel(levelChangeNumber: number = 1)
    {
        this._level += levelChangeNumber;
        this.clearSpriteArrays(false);
        this.winSymbolGroup.removeAll(true);
        this.numberObjectGroup.removeAll(true);
        this.symbolObjectGroup.removeAll(true);
        this.dropSquareGroup.removeAll(true);
        this.compositeGroup.removeAll(true);
        stateEvent.runOnShutdown(this);
        this.create();
    }

    public clearDoodads()
    {
        for (let name in this.customDoodadsSpriteArrays)
        {
            this.customDoodadsSpriteArrays[name].length = 0;
        }
        for (let genericGroup of this.genericGroups)
        {
            genericGroup.removeAll(true);
        }
    }
}

export interface SpriteArrayDictionary
{
    [name: string]: SpriteArray;
}




