import { DropAreaObject } from "GameObjects";
import { ParenthesisPair } from "./parenthesisPair";
import { improveArray, ImprovedArray } from "Libs";

export interface ParenthesisPairArray extends ImprovedArray<ParenthesisPair>
{
    create(startBeforeDropArea: DropAreaObject, endAfterDropArea: DropAreaObject,
        executionPriority: number): ParenthesisPair;
    defaultGroup: Phaser.Group;
}

export function makeParenthesisPairArray(group: Phaser.Group)
{
    let newArray = improveArray<ParenthesisPair>() as ParenthesisPairArray;
    newArray.create = ParenthesisPairArrayCreate;
    newArray.defaultGroup = group;
    return newArray;
}

function ParenthesisPairArrayCreate(this: ParenthesisPairArray, startBeforeDropArea: DropAreaObject,
    endAfterDropArea: DropAreaObject, executionPriority: number): ParenthesisPair
{
    let parenthesisPair = new ParenthesisPair(startBeforeDropArea,
        endAfterDropArea, executionPriority, this.defaultGroup);

    this.push(parenthesisPair);
    return parenthesisPair;
}





