import { game } from "main";

const spr_mist_keys = ["spr_mist_1", "spr_mist_2"];

export class MistObject extends Phaser.Sprite
{

    private xSpeed;
    private yOffset;

    public static readonly doodadName = "Mist";


/*     public get getBody(): Phaser.Physics.Arcade.Body
    {
        return this.body;
    }
*/

    public update()
    {
        this.position.x += this.xSpeed;
        this.position.y = this.game.height - this.height + (Math.sin(this.game.time.now / 2200) * 15) + this.yOffset;

        if (this.position.x  + this.width  < 0)
            {
                this.position.x = this.width;
            }
    }

    public setParallax(useParallax, xAcceleration, yAcceleration)
    {
        if (useParallax)
        {
            this.checkWorldBounds = true;
            this.events.onOutOfBounds.add(this._eventOutOfBounds, this);
            // game.physics.enable(this, Phaser.Physics.ARCADE);
            // this.getBody.velocity.set(xAcceleration, yAcceleration);
        }
    }

    private _eventOutOfBounds()
    {
        // this.position.set(game.width + this.width, this.position.y);
    }

    constructor(game: Phaser.Game, x, y, key: number, useParallax = true,
        xAcceleration = -0.5, yAcceleration = 0)
    {
        super(game, x, y, spr_mist_keys[key]);
        this.xSpeed = xAcceleration;
        this.yOffset = yAcceleration;
        // this.setParallax(true, xAcceleration, yAcceleration);
    }

    public static loadAssets(game: Phaser.Game)
    {
        game.load.image(spr_mist_keys[0], "images/mathman/World1/mist.png");
        game.load.image(spr_mist_keys[1], "images/mathman/World2/mist.png");
    }
}


