import { SymbolComponent, NumberComponent } from "./symbolComponent";
import { MathDAD } from "Libs";
import { INumberObject } from "GameObjects";
import * as Libs from "Libs/";

export class DivisionComponent extends SymbolComponent
{
    public static readonly componentName = "SymbolDivisionComponet";
    public static readonly inheritedComponentNames = [SymbolComponent];

    protected _symbolName = "Division";

    public priority: number = 2;

    public calc(left: MathDAD.XNumberValue, right: MathDAD.XNumberValue): { numberValue: number, isX: boolean }
    {
        let numberValue = left.numberValue / right.numberValue;
        let isX = left.isX || right.isX;
        return { numberValue, isX };
    }

    public isCalcValid(left: MathDAD.XNumberValue, right: MathDAD.XNumberValue): boolean
    {
        return true;
    }
}


