import { game } from "main";

export function tweenTint(objects: Phaser.Sprite[], startColor: number, endColor: number, time: number): Phaser.Tween
{    // create an object to tween with our step value at 0
    let colorBlend = { step: 0 };
    // create the tween on this object and tween its step property to 100
    let colorTween = game.add.tween(colorBlend).to({ step: 100 }, time);
    // run the interpolateColor function every time the tween updates, feeding it the
    // updated value of our tween each time, and set the result as our tint
    colorTween.onUpdateCallback(function ()
    {
        for (let obj of objects)
            obj.tint = Phaser.Color.interpolateColor(startColor, endColor, 100, colorBlend.step);
    });
    // set the object to the start color straight away    obj.tint = startColor;
    // start the tween
    return colorTween;
}


