import { game } from "main";
import { ClickCloudClutter } from "GameObjects/Particles/";
import { IComponentAbleSprite, PhaserSpriteBehavior } from "./PhaserComponent/";
import { events } from "States/Events/mainStateEvent";

export class CloudBurstComponent extends PhaserSpriteBehavior
{
    public static readonly componentName = "CloudBurstComponent";

    public get pixelPerfectClick()
    {
        return this.sprite.input.pixelPerfectClick;
    }

    public set pixelPerfectClick(value: boolean)
    {
        this.sprite.input.pixelPerfectClick = value;
    }

    public start()
    {

        this.sprite.events.onInputDown.add(this._eventOnInputDown, this);
    }

    protected _eventOnInputDown()
    {
        let { x, y } = game.input.activePointer;
        clickCloudClutterParticleEffect.PlayParticle(x, y, this.distanceMod);
    }

    constructor(sprite: IComponentAbleSprite, public distanceMod, pixelPerfectClick)
    {
        super(sprite);
        this.sprite.inputEnabled = true;
        this.sprite.input.pixelPerfectClick = pixelPerfectClick;
    }
}

let clickCloudClutterParticleEffect: ClickCloudClutter;
function createParticleEffect()
{
    clickCloudClutterParticleEffect = new ClickCloudClutter(game, 0, 0);
}

events.onCreate.add(createParticleEffect);


