import { ComponentAbleSprite, RootComponent } from "Components";
import { ISymbolObject } from "../iSymbolObject";

export class RootObject extends
    ComponentAbleSprite implements ISymbolObject
{
    private _symbolStateComponent: RootComponent;
    public get symbolComponent() { return this._symbolStateComponent; }

    constructor(game: Phaser.Game, x: number, y: number, rootValue = 2)
    {
        super(game, x, y, "sqrRoot");
        this._symbolStateComponent = this.addComponent(RootComponent, rootValue);
        this.anchor.setTo(0.29, 0.55);
    }

    public static loadAssets(game: Phaser.Game) {
        // the assets to load for this actor like
        game.load.image("sqrRoot", "images/sqrRoot.png");
    }

}




