import { fonts } from "fonts";
import * as globalSettingsManger from "Managers/globalSettingsManger";

export class PreloadState extends Phaser.State
{
    private _jsonLoaded = false;
    private _fontLoaded = false;

    public preload()
    {
        setupWebFontConfig(this.game, this.onFontLoaded, this);


        this.game.load.script("webfont", "//ajax.googleapis.com/ajax/libs/webfont/1.4.7/webfont.js");
        this.scale.fullScreenScaleMode = Phaser.ScaleManager.SHOW_ALL;
        this.scale.pageAlignHorizontally = true;
        addScript("htmlSites/test.js");
        // this.load.script("gameCreator", "htmlSites/test.js");
        globalSettingsManger.loadJson("json/global.json");
        globalSettingsManger.addOnJsonLoad(this.onJsonLoaded, this);
    }

    public onFontLoaded()
    {
        this._fontLoaded = true;
        if (this._jsonLoaded)
        {
            if (window.DebugLevelSkip && window.DebugLevelSkip.skipLevel)
            {
                this.state.start("main", undefined, undefined,
                    window.DebugLevelSkip.levelIndex, window.DebugLevelSkip.gameMode);
            }
            else
            {
                this.state.start(this.nextStage);
            }
        }
    }

    public onJsonLoaded()
    {
        this._jsonLoaded = true;
        if (this._fontLoaded)
        {
            this.state.start(this.nextStage);
        }
    }


    constructor(private nextStage: string)
    {
        super();
    }

}

function setupWebFontConfig(game: Phaser.Game, callBack: Function, callbackContext: object)
{
    let fontArray = Array<string>();
    for (let index in fonts)
    {
        fontArray.push(fonts[index]);
    }

    (<any>window).WebFontConfig = {
        active: function () { game.time.events.add(Phaser.Timer.SECOND, callBack, callbackContext); },
        google: {
            families: fontArray
        }
    };
}

function addScript(src, callback?)
{
    let s = document.createElement("script");
    s.setAttribute("src", src);
    s.onload = callback;
    document.body.appendChild(s);
}


