import { CloudBurstComponent, IComponentAbleSprite, ComponentAbleSprite } from "Components/";
import { registerDoodad } from "./decorators";
import { assetLoadMethod, StateEnum } from "assetsLoader";


export const World1HillSprites = ["world_1_hill_6", "world_1_hill_5", "world_1_hill_4",
    "world_1_hill_3", "world_1_hill_2", "world_1_hill_1"];

export const WindSound = "wind1";

@registerDoodad("HillsWorld1")
export class HillScene1 extends ComponentAbleSprite
{
    public readonly cloudBurstComponent: CloudBurstComponent;

    public setCloudBurst(distanceMod: number, pixelPerfectClick = false)
    {
        this.cloudBurstComponent.distanceMod = distanceMod;
        this.cloudBurstComponent.pixelPerfectClick = pixelPerfectClick;
    }

    constructor(game: Phaser.Game, x: number, y: number, hillKey: number, distanceMod: number, pixelPerfectClick = false)
    {
        super(game, x, y, World1HillSprites[hillKey]);
        this.cloudBurstComponent = this.addComponent(CloudBurstComponent, distanceMod, pixelPerfectClick);
    }

    @assetLoadMethod(StateEnum.Main)
    private static loadAssetsHill(game: Phaser.Game)
    {
        game.load.image(World1HillSprites[5], "images/mathman/World1/bg1.png");
        game.load.image(World1HillSprites[4], "images/mathman/World1/bg2.png");
        game.load.image(World1HillSprites[3], "images/mathman/World1/bg3.png");
        game.load.image(World1HillSprites[2], "images/mathman/World1/bg4.png");
        game.load.image(World1HillSprites[1], "images/mathman/World1/bg5.png");
        game.load.image(World1HillSprites[0], "images/mathman/World1/bg6.png");

        game.load.audio(WindSound, "audio/Wind1.wav");
    }
}




