import { GameCreator } from "../gameCreator";
import { MainState } from "States/mainState";
import { makeSpriteArray, SpriteArray } from "Libs/Phaser/";
import { IComponentAbleSprite } from "Components/";
import * as DDA from "GameObjects/Doodads";



export function addDoodadClass(name: string, doodadClass: new (...args: any[]) => IComponentAbleSprite)
{
    name = name.toLowerCase();
    if (!doodadsClasses[name])
        doodadsClasses[name] = doodadClass;
}

const doodadsClasses: ObjectDictionary<Constructor<IComponentAbleSprite>> = Object.create(null);

export class DoodadLayoutCreator
{
    private _state: MainState;
    public get state() { return this.state; }

    public create(doodadType: string, groupNumber: number, x: number, y: number, ...args: any[]): Phaser.Sprite
    {
        return this._state.customDoodadsSpriteArrays[doodadType.toLowerCase()].
            create(this._state.genericGroups[groupNumber], x, y, ...args);
    }

    constructor(state: MainState)
    {
        this._state = state;
        for (let namesDDA in DDA)
        {
            let element = DDA[namesDDA];
            if (typeof element.doodadName === "string")
            {
                addDoodadClass(element.doodadName, element);
            }
        }

        for (let name in doodadsClasses)
        {
            if (!this._state.customDoodadsSpriteArrays[name])
                this._state.customDoodadsSpriteArrays[name] = makeSpriteArray(state, doodadsClasses[name]);
        }
    }
}


