import { IComponentAbleSprite } from "./types";

export abstract class PhaserSpriteBehavior
{
    public readonly sprite: IComponentAbleSprite;

    constructor(sprite: IComponentAbleSprite)
    {
        this.sprite = sprite;
    }
}



