import { ComponentAbleText, SymbolText, SpawnerComponent, CompositeResetArg } from "Components/";
import { SpawnCompositeObject } from "GameObjects/";
import { textStyleConfig } from "fonts";
import * as Libs from "Libs/";

export class CompositeSpawner extends ComponentAbleText
{
    private _spawnerComponent: SpawnerComponent;
    public get spawnerComponent() { return this._spawnerComponent; }

    constructor(game: Phaser.Game, x: number, y: number, symbolText: SymbolText
        , numberValue: number, spawnSpriteArray: Libs.Phaser.SpriteArray<SpawnCompositeObject>)
    {
        super(game, x, y, "", textStyleConfig);
        this.text = Libs.MathDAD.symbolTextToString(symbolText) + " " +
            Libs.MathDAD.numberToString(numberValue, false);
        this.anchor.setTo(0.5);

        let resetArgs: CompositeResetArg = { symbolText, numberValue };
        let createArg: any[] = [symbolText, numberValue];

        this._spawnerComponent = this.addComponent(SpawnerComponent,
            spawnSpriteArray, { resetArgs, createArg });
    }
}


