import { PhaserSpriteBehavior, IComponentAbleSprite } from "Components/PhaserComponent/";
import { IWinCheckComponent } from "Components/WinCheckComponents/iWinCheckComponent";
import { IMathOwner } from "Components/mathOwnedByComponent";
import { INumberObject, ISymbolObject, DropAreaObject } from "GameObjects/";
import * as Libs from "Libs/";

export class WinEquationComponent extends PhaserSpriteBehavior implements IMathOwner, IWinCheckComponent
{
    public sprite: IComponentAbleSprite;
    private _ownedSprites: IComponentAbleSprite[];

    public static readonly componentName = "WinEquationComponent";
    public numberValue: number;

    public getMathObjectOwned(sprites: IComponentAbleSprite[])
    {
        this._ownedSprites = sprites;
    }

    public checkForWin(numberValues: Libs.ValueIndex<INumberObject>[],
        symbolValues: Libs.ValueIndex<ISymbolObject>[]): boolean
    {
        if (numberValues.length !== 2 || symbolValues.length !== 0)
            return false;

        let firstNumber = numberValues[0];
        let secondNumber = numberValues[1];
        if (secondNumber.index < firstNumber.index)
        {
            let temp = firstNumber;
            firstNumber = secondNumber;
            secondNumber = temp;
        }

        return firstNumber.value.numberComponent.isX
            && firstNumber.value.numberValue === 1 && !secondNumber.value.numberComponent.isX;

    }


    constructor(sprite: IComponentAbleSprite, numberValue: number)
    {
        super(sprite);
        this.numberValue = numberValue;
    }

}


