import { closeToNumber } from "../Common/closeToNumber";

export function numberToString(numberValue: number, isX: boolean, divisionStyling?: boolean): string
{
    let stringValue: string | null = null;

    if (divisionStyling)
    {
        stringValue = applyDivisionStyling(numberValue);
        divisionStyling = stringValue !== null;
    }

    if (stringValue === null)
    {

        let newStrings = (Math.round(numberValue * 100) / 100).toString().split(".");

        if (newStrings.length >= 2)
            stringValue = `${newStrings[0]},${newStrings[1].slice(0, 2)}`;
        else
            stringValue = newStrings[0];
    }

    if (isX)
    {
        if (divisionStyling)
        {
            if (stringValue.slice(0, 2) === "1/")
            {
                stringValue = "X " + stringValue.slice(1);
            }
            else
            {
                stringValue += "X";
            }
        }
        else
        {
            if (stringValue === "1")
            {
                stringValue = "X";
            }
            else
            {
                stringValue += "X";
            }
        }
    }

    return stringValue;
}

const marginOfError = 0.001;
let divisionStylingTable: { closeToNumber: number, text: string }[] = [];

function applyDivisionStyling(numberValue: number): string | null
{
    for (let entry of divisionStylingTable)
    {
        if (closeToNumber(numberValue, entry.closeToNumber, marginOfError))
        {
            return entry.text;
        }
    }
    return null;
}

export function addDivisionStylingTable(closeToNumber: number, text: string)
{
    divisionStylingTable.push({ closeToNumber, text });
}

for (let index = 2; index < 21; index++)
{
    addDivisionStylingTable(1 / index, "1/ " + index.toString());
}


