import { NumberComponent, SymbolComponent } from "./symbolComponent";
import { MathDAD } from "Libs";

export class MultiplicationComponent extends SymbolComponent
{
    public static readonly componentName = "SymbolMultiplicationComponet";
    public static readonly inheritedComponentNames = [SymbolComponent];

    protected _symbolName = "Multiplication";

    public priority: number = 2;

    public calc(left: MathDAD.XNumberValue, right: MathDAD.XNumberValue): { numberValue: number, isX: boolean }
    {
        let numberValue = left.numberValue * right.numberValue;
        let isX = left.isX || right.isX;

        return { numberValue, isX };
    }

}


