import { LevelSelectButton } from "./levelButton";
import { SaveHandler } from "./saveHandler";
import { BackButtonGameSelect } from "./backButtonGameSelect";
import { levelSelectInfo } from "./levelSelectInfo";


export class LevelSelectState extends Phaser.State
{
    private amountOfLevels: number;

    private levelPage: number;
    private maxLevelPage: number;

    private bars: Phaser.Group[];
    private fullBarWidth: number;

    private currentBars: Phaser.Group[];

    private arrowButtonLeft: Phaser.Button;
    private arrowButtonRight: Phaser.Button;

    private tweenDuration: number = 500;

    private levelSelectBackground: Phaser.Sprite;


    constructor()
    {
        super();
    }

    public preload(): void
    {

        this.load.baseURL = "./assets/levelSelect/";

        this.game.load.image(levelSelectInfo.backButtonSprite.key,
            levelSelectInfo.backButtonSprite.spriteUrl);

        this.game.load.image(levelSelectInfo.buttonArrowSprite.key,
            levelSelectInfo.buttonArrowSprite.spriteUrl);

        this.game.load.image(levelSelectInfo.buttonBackgroundGreySprite.key,
            levelSelectInfo.buttonBackgroundGreySprite.spriteUrl);

        this.game.load.image(levelSelectInfo.buttonBackgroundSprite.key,
            levelSelectInfo.buttonBackgroundSprite.spriteUrl);

        this.game.load.image(levelSelectInfo.levelSelectBackgroundSprite.key,
            levelSelectInfo.levelSelectBackgroundSprite.spriteUrl);

        this.game.load.image(levelSelectInfo.loadScreenSprite.key,
            levelSelectInfo.loadScreenSprite.spriteUrl);
    }

    public create(): void
    {
        SaveHandler.loadGame();
        this.levelSelectBackground = this.game.add.sprite(0, 0,
            levelSelectInfo.levelSelectBackgroundSprite.key);

        this.amountOfLevels = levelSelectInfo.amountOfLevels;

        this.currentBars = new Array<Phaser.Group>();

        this.bars = this.CreateButtonBars(5, 80, 80, 0);

        this.CreateArrowButtons();

        this.maxLevelPage = Math.floor(this.bars.length / 2) + ((this.bars.length % 2 > 0) ? 1 : 0);

        // Last level completed on level screen
        if ((LevelTracker.GetCurrentLevelIndex() + 1) % 10 === 0 && !LevelTracker.FiveLevelsCompleted())
        {
            if (LevelTracker.GetCurrentLevelIndex() >= levelSelectInfo.amountOfLevels - 1)
            {
                this.SetLevelPage(LevelTracker.GetCurrentLevelPage(), true, true);
            }
            else
            {
                this.SetLevelPage((LevelTracker.GetCurrentLevelPage() + 1), true, true);
            }

            LevelTracker.ResetLevelsCompleted();
        }
        else if (LevelTracker.FiveLevelsCompleted() === true)
        {
            this.SetLevelPage(LevelTracker.GetCurrentLevelPage(), true, true);

            LevelTracker.ResetLevelsCompleted();
        }
        else
        {
            this.SetLevelPage(LevelTracker.GetCurrentLevelPage(), true, true);
        }

        this.UpdateArrowButtons();

        let backButton = new BackButtonGameSelect(this.game, this.game.width - 100,
            this.game.height - 100, levelSelectInfo.backButtonSprite.key);

        this.add.existing(backButton);

        let loadScreen: Phaser.Sprite = this.game.add.sprite(this.game.width * 0.5,
            this.game.height * 0.5, levelSelectInfo.loadScreenSprite.key);
        loadScreen.anchor = new Phaser.Point(0.5, 0.5);

        this.game.add.tween(loadScreen).to({ alpha: 0 }, 1250, Phaser.Easing.Linear.None).start();
        this.game.add.tween(loadScreen.scale).to({ x: 1.75, y: 1.75 }, 1250, Phaser.Easing.Linear.None).start();
    }

    private CreateButtonBars(buttonsPerBar: number, buttonWidth: number, buttonHeight: number, buttonRightMargin: number)
    {
        this.fullBarWidth = buttonsPerBar * (buttonWidth + buttonRightMargin) - buttonRightMargin;

        // Figure out how many bars we need
        let amountOfBarsNeeded = Math.floor(this.amountOfLevels / 5) + ((this.amountOfLevels % 5 > 0) ? 1 : 0);

        // Create the bars array
        let tempBars: Phaser.Group[] = new Array<Phaser.Group>();

        // Populate the empty bars array
        for (let i = 0; i < amountOfBarsNeeded; i++)
        {
            tempBars[i] = this.game.add.group();
        }

        // Populate the groups in the bar array with button children
        let btnIndex = 0;
        let lvlSelectButton: LevelSelectButton | null = null;

        for (let i = 0; i < amountOfBarsNeeded; i++)
        {
            for (let k = 0; k < 5; k++)
            {
                if (btnIndex < this.amountOfLevels)
                {
                    lvlSelectButton = null;

                    let levelData = SaveHandler.getLevelData();

                    if (!levelData)
                    {
                        throw Error("no level Data");
                    }

                    if (levelData[btnIndex].completed)
                    {
                        lvlSelectButton = new LevelSelectButton(this.game, 0, 0,
                            buttonWidth, buttonHeight, levelSelectInfo.buttonBackgroundSprite.key, btnIndex, k);
                    }
                    else
                    {
                        lvlSelectButton = new LevelSelectButton(this.game, 0, 0, buttonWidth,
                            buttonHeight, levelSelectInfo.buttonBackgroundGreySprite.key, btnIndex, k);
                    }

                    tempBars[i].add(lvlSelectButton);

                    btnIndex++;
                }
            }

            tempBars[i].align(5, 1, buttonWidth + buttonRightMargin, buttonHeight);
        }

        // Move the groups of bounds
        for (let i = 0; i < tempBars.length; i++)
        {
            tempBars[i].position = new Phaser.Point(this.game.width, this.game.height * 0.5);
        }

        return tempBars;
    }

    private CreateArrowButtons()
    {
        let arrowButtonScreenMargin = 20;
        let arrowButtonWidth = 80;
        let arrowButtonHeight = 80;

        this.arrowButtonLeft = this.game.add.button(arrowButtonWidth + arrowButtonScreenMargin,
            this.game.height * 0.5, levelSelectInfo.buttonArrowSprite.key, this.DecrementPage, this);
        this.arrowButtonLeft.anchor = new Phaser.Point(0.5, 0.5);
        this.arrowButtonLeft.angle = 180;

        this.arrowButtonRight = this.game.add.button(this.game.width - arrowButtonWidth - arrowButtonScreenMargin,
            this.game.height * 0.5, levelSelectInfo.buttonArrowSprite.key, this.IncrementPage, this);
        this.arrowButtonRight.anchor = new Phaser.Point(0.5, 0.5);
    }

    private UpdateArrowButtons()
    {
        if (this.levelPage <= 0)
        {
            this.arrowButtonLeft.visible = false;
        }
        else
        {
            if (this.arrowButtonLeft.visible === false)
            {
                this.arrowButtonLeft.visible = true;
            }
        }

        if (this.IsAtTheEndOfLevelPage())
        {
            this.arrowButtonRight.visible = false;
        }
        else
        {
            if (this.arrowButtonRight.visible === false)
            {
                this.arrowButtonRight.visible = true;
            }
        }
    }

    private IncrementPage()
    {
        if (this.HasMoreThanOneLevelPage() && this.IsNotAtTheEndOfLevelPage())
        {
            this.SetLevelPage((this.levelPage + 1), true);
        }
    }

    private DecrementPage()
    {
        if (this.levelPage > 0)
        {
            this.SetLevelPage((this.levelPage - 1), false);
        }
    }

    private SetLevelPage(index: number, incrementing: boolean, firstTime?: boolean)
    {
        this.levelPage = index;

        LevelTracker.SetCurrentLevelPage(this.levelPage);

        this.UpdateArrowButtons();

        if (firstTime === undefined)
        {
            this.ExitTweenBars(incrementing);
        }

        this.EntryTweenBars(incrementing);
    }

    private EntryTweenBars(incrementing: boolean) // Set transitions in future
    {
        // Reset current bars array
        this.currentBars = [];

        // If we're not at the start page, we need to add 1 to levelpage to get the correct set of pages.
        let topPageIndex: number = (this.levelPage > 0) ? 2 * this.levelPage : this.levelPage;
        let durationOfTween = this.tweenDuration;

        // Set top tween position
        let tweenPointTop: Phaser.Point = this.GetUpperGroupPosition(80, 80);

        // Position the bar depending on whether the page is incrementing or decrementing
        this.bars[topPageIndex].position = new Phaser.Point((incrementing === true) ? this.game.width : -this.fullBarWidth, tweenPointTop.y);

        // Add a tween from the current position to the tween top point position
        this.game.add.tween(this.bars[topPageIndex].position).to(tweenPointTop, durationOfTween, Phaser.Easing.Back.In).start();

        // Add the bar to the array of currently displayed bars
        this.currentBars.push(this.bars[topPageIndex]);

        // Is there another bar after the top bar?
        if (topPageIndex + 1 < this.bars.length)
        {


            // Increment because we need the second row of bars
            topPageIndex += 1;

            // Set bottom tween position
            let tweenPointBottom: Phaser.Point = this.GetLowerGroupPosition(80, 80);

            // Position the bar depending on whether the page is incrementing or decrementing
            this.bars[topPageIndex].position = new Phaser.Point((incrementing === true) ? this.game.width : -this.fullBarWidth, tweenPointBottom.y);

            // Add a tween from the current position to the tween bottom point position
            this.game.add.tween(this.bars[topPageIndex].position).to(tweenPointBottom, durationOfTween, Phaser.Easing.Back.In).start();

            // Add the bar to the array of currently displayed bars
            this.currentBars.push(this.bars[topPageIndex]);
        }
    }
    // Set transitions in future
    private ExitTweenBars(incrementing: boolean)
    {
        // Set exit top point
        let tweenExitTop: Phaser.Point = this.GetUpperGroupPosition(80, 80);

        // Modify the x coordinate of exit top point depending on whether the page is incrementing or decrementing
        tweenExitTop.x = (incrementing === true) ? -this.fullBarWidth : this.game.width;

        // Add a tween from the current position (middle of screen) to the exit top point
        this.game.add.tween(this.currentBars[0].position).to(tweenExitTop, 450, Phaser.Easing.Back.In).start();

        // Is there another bar after the top bar?
        if (this.currentBars[1] !== undefined)
        {
            // Set exit bottom point
            let tweenExitBottom: Phaser.Point = this.GetLowerGroupPosition(80, 80);

            // Modify the x coordinate of exit bottom point depending on whether the page is incrementing or decrementing
            tweenExitBottom.x = (incrementing === true) ? -this.fullBarWidth : this.game.width;

            // Add a tween from the current position (middle of screen) to the exit bottom point
            this.game.add.tween(this.currentBars[1].position).to(tweenExitBottom, 500, Phaser.Easing.Back.In).start();
        }
    }


    private GetUpperGroupPosition(buttonWidth: number, buttonHeight: number): Phaser.Point
    {
        return new Phaser.Point((this.game.width * 0.5) - (this.fullBarWidth * 0.5), 150);
    }

    private GetLowerGroupPosition(buttonWidth: number, buttonHeight: number): Phaser.Point
    {
        return new Phaser.Point((this.game.width * 0.5) - (this.fullBarWidth * 0.5), this.game.height - 150 - buttonHeight);
    }

    private MaxPagesIsUnevenNumber(): boolean
    {
        return (this.maxLevelPage % 2 > 0) ? true : false;
    }

    private IsAtTheEndOfLevelPage(): boolean
    {
        return (this.levelPage === this.maxLevelPage - 1);
    }

    private HasMoreThanOneLevelPage(): boolean
    {
        return this.maxLevelPage !== 1;
    }

    private IsNotAtTheEndOfLevelPage(): boolean
    {
        return this.levelPage < this.maxLevelPage - 1;
    }
}


export class LevelTracker
{
    private static levelsCompletedInARow = 0;
    private static lastLevelPage = 0;
    private static lastLevelIndex = 0;
    private static backgroundMistLastPosition: Phaser.Point;


    public static IncrementCompletedLevels(): void
    {
        this.levelsCompletedInARow++;
    }

    public static FiveLevelsCompleted(): boolean
    {
        return this.levelsCompletedInARow >= 5;
    }

    public static ResetLevelsCompleted(): void
    {
        this.levelsCompletedInARow = 0;
    }

    public static SetCurrentLevelPage(index: number): void
    {
        this.lastLevelPage = index;
    }

    public static GetCurrentLevelPage(): number
    {
        return this.lastLevelPage;
    }

    public static SetCurrentLevelIndex(index: number): void
    {
        this.lastLevelIndex = index;
    }

    public static GetCurrentLevelIndex(): number
    {
        return this.lastLevelIndex;
    }
    public static GetLevelsCompletedInARow(): number
    {
        return this.levelsCompletedInARow;
    }
    public static SetBackgroundMistLastXPosition(position: Phaser.Point)
    {
        this.backgroundMistLastPosition = position;
    }
    public static GetBackgroundMistLastXPosition(): Phaser.Point
    {
        if (this.backgroundMistLastPosition !== undefined)
            return this.backgroundMistLastPosition;
        else
            return new Phaser.Point(0, 0);
    }
}





