import { SpriteArray } from "Libs/Phaser";
import { MathProcess } from "./MathProcess/";
import * as mathAnimation from "./MathProcess/mathAnimation";
import * as loseAnimating from "./LoseProcess/loseAnimation";
import { LoseProcess } from "./LoseProcess/";
import
{
    NumberComponent, SymbolComponent, ICheckNextAbleType,
    RootComponent, DragAndDropComponent, CompositeComponent
} from "Components";
import { MainState } from "States/mainState";
import { events as stateEvents, runOnLevelWon } from "States/Events/mainStateEvent";
import
{
    DropAreaObject, NumberObject, DragAbleNumberObject,
    DragAbleSymbolObject, WinSymbolObject, SymbolObject,
    INumberObject, ISymbolObject, CompositeObject
} from "GameObjects/";
import * as Libs from "Libs/";

export namespace MathManger
{
    let currentState: MainState;
    let currentMathProcess: MathProcess = new MathProcess();
    let currentLoseProcess: LoseProcess;
    let processRunning = false;
    export let onMathBegin = new Phaser.Signal;
    export let onMathDoneLose = new Phaser.Signal;
    export let onMathDoneWin = new Phaser.Signal;

    function mangerUpdate(state: MainState)
    {
        currentState = state;
        if (!processRunning && state.dropAreaObjects.every(i => i.full))
        {
            checkMath(state.dropAreaObjects, state.intermittedNumberObjects,
                state.intermittedSymbolsObjects, state.winSymbolObjects, state.secondStageStartDropArea);
        }
    }

    function managerShutdown(state: MainState)
    {
        currentMathProcess = new MathProcess();
        processRunning = false;
    }

    function checkMath(dropAreaArray: SpriteArray<DropAreaObject>,
        intermittedNumberObjectArray: SpriteArray<NumberObject>,
        intermittedSymbolArray: SpriteArray<SymbolObject>,
        winSymbolArray: SpriteArray<WinSymbolObject>,
        secondStageStartDropArea: DropAreaObject | null)
    {

        let firstDropArea = dropAreaArray[0];

        if (dropAreaArray.length < 1 || !firstDropArea.objectInArea)
            return;

        let firstObjectInArea = firstDropArea.objectInArea;

        if (!firstObjectInArea.getComponent(NumberComponent) &&
            !firstObjectInArea.getComponent(RootComponent))
            return;

        let numberValues: Libs.ValueIndex<INumberObject>[] = [];
        let symbolValues: Libs.ValueIndex<ISymbolObject>[] = [];
        let compositeValue: Libs.ValueIndex<CompositeObject>[] = [];

        // for (let index = 0; index < dropAreaArray.length; index++)
        // {
        //     let currentDropArea = dropAreaArray[index];
        //     let nextDropArea = dropAreaArray[index + 1];

        //     if (!currentDropArea.objectInArea)
        //         return;

        //     let comp = currentDropArea.objectInArea.getComponent(ICheckNextAbleType);
        //     if (comp)
        //     {
        //         if (nextDropArea)
        //         {
        //             if (!comp.checkNext(nextDropArea.objectInArea))
        //                 return;
        //         }
        //         else
        //         {
        //             if (!comp.checkNext(null))
        //                 return;
        //         }
        //     }
        //     else
        //     {
        //         return;
        //     }
        // }

        for (let index = 0; index < dropAreaArray.length; index++)
        {
            let currentDropArea = dropAreaArray[index];
            if (!currentDropArea.objectInArea)
                continue;

            let currentObjectInArea = currentDropArea.objectInArea;

            let dragAbleComp = currentObjectInArea.getComponent(DragAndDropComponent);
            if (dragAbleComp)
                dragAbleComp.SetDragAble(false);

            let symbolComp = currentObjectInArea.getComponent(SymbolComponent);
            if (symbolComp)
                symbolValues.push(Libs.createValueIndex(currentObjectInArea as SymbolObject, index));

            let numberComp = currentObjectInArea.getComponent(NumberComponent);
            if (numberComp)
                numberValues.push(Libs.createValueIndex(currentObjectInArea as NumberObject, index));

            let compositeComp = currentObjectInArea.getComponent(CompositeComponent);
            if (compositeComp)
            {
                compositeValue.push(Libs.createValueIndex(currentObjectInArea as CompositeObject, index));
            }
        }

        processRunning = true;
        onMathBegin.dispatch();
        currentMathProcess.beginProcess(numberValues, symbolValues, compositeValue, intermittedNumberObjectArray,
            intermittedSymbolArray, dropAreaArray.clone(), winSymbolArray[0],
            currentState.parenthesisPairObjects.clone(), secondStageStartDropArea, processDone);
    }

    function processDone(isAWin: boolean)
    {
        if (isAWin)
        {
            onMathDoneWin.dispatch();
            runOnLevelWon(currentState);
            currentState.softCutNextLevel();
        }
        else
        {
            currentState.dropAreaObjects.forEach((da) =>
            {
                if (da.objectInArea != null)
                {
                    da.objectInArea.exists = true;
                    da.objectInArea.position.copyFrom(da.position);
                }
            });
            currentLoseProcess = new LoseProcess();
            currentLoseProcess.beginProcess(() => { processRunning = false; onMathDoneLose.dispatch(); }, currentState);
        }

    }
    stateEvents.onUpdate.add(mangerUpdate);
    stateEvents.onShutdown.add(managerShutdown);

    export const setBeginAnimationFunc = mathAnimation.setBeginAnimationFunc;
    export const setProcessBeginAnimationFunc = mathAnimation.setProcessBeginAnimationFunc;
    export const setProcessEndAnimationFunc = mathAnimation.setProcessEndAnimationFunc;
    export const setLoseAnimationFunc = mathAnimation.setLoseAnimationFunc;
    export const setWinAnimationFunc = mathAnimation.setWinAnimationFunc;
    export const setLoseObjectAppearAnimationFunc = loseAnimating.setLoseObjectAppearAnimationFunc;
}


