import { IComponentAbleSprite, PhaserSpriteBehavior } from "./PhaserComponent/";

export class NewLitUpComponent extends PhaserSpriteBehavior
{
    public static readonly componentName = "NewLitUpComponent";
    private isLit: boolean = false;
    public spriteKeyLitUp: string;
    public spriteKeyLitDown: string;

    public LitUp()
    {
        if (!this.isLit)
        {
            this.sprite.loadTexture(this.spriteKeyLitUp);
            this.isLit = true;
        }
    }

    public LitDown()
    {
        if (this.isLit)
        {
            this.sprite.loadTexture(this.spriteKeyLitDown);
            this.isLit = false;
        }
    }

    constructor(sprite: IComponentAbleSprite, spriteKeyLitUp: string, spriteKeyLitDown: string)
    {
        super(sprite);
        this.spriteKeyLitUp = spriteKeyLitUp;
        this.spriteKeyLitDown = spriteKeyLitDown;
    }
}


