import * as Libs from "Libs/";
import { PhaserSpriteBehavior } from "./phaserSpriteBehavior";
import
{
    PhaserSpriteBehaviorWithEvents, IPhaserSpriteBehaviorConstructor,
    InterfaceComponentAbleType, StateComponent
} from "./types";

export class StateComponentClass<T extends PhaserSpriteBehaviorWithEvents> extends
    PhaserSpriteBehavior implements StateComponent<T>
{
    private _phaserComponents: StateComponentDirectory<T> = {};
    private _components: T[] = [];
    private _startQueue: T[] = [];
    private _currentState?: string;
    private _currentComponent?: T;

    public get currentComponent(): T | undefined
    {
        return this._currentComponent;
    }

    public addComponent<L extends T>(
        componentClassOrNames: IPhaserSpriteBehaviorConstructor<L> | string,
        component: PhaserSpriteBehaviorWithEvents | any, ...args: any[]): L
    {
        let newComponent;
        let name: string;
        if (Libs.isString(componentClassOrNames))
        {
            name = componentClassOrNames;
            this._phaserComponents[componentClassOrNames] = component;
        }
        else
        {
            name = componentClassOrNames.componentName;
            newComponent = new componentClassOrNames(this.sprite, ...args);
            this._phaserComponents[componentClassOrNames.componentName] = newComponent;
        }

        this._startQueue.push(newComponent);
        this._components.push(newComponent);
        if (this._currentComponent)
        {
            this._currentState = name;
            this._currentComponent = newComponent;
        }

        return newComponent;
    }

    public getAllComponent(): T[]
    {
        return this._components.slice(0);
    }

    public SetState<L extends T>(
        componentIdentifier: IPhaserSpriteBehaviorConstructor<L> |
            InterfaceComponentAbleType<L> | string): L | false
    {
        let componentName;
        if (Libs.isString(componentIdentifier))
        {
            componentName = componentIdentifier;
        }
        else
        {
            componentName = componentIdentifier.componentName;
        }

        let comps = this._phaserComponents[componentName];

        if (comps)
        {
            this._components.forEach((i) =>
            {
                if (i.OnComponetStateChange)
                {
                    i.OnComponetStateChange(this._currentState, componentName);
                }
            });

            this._currentState = componentName;
            this._currentComponent = comps;
            return comps as L;
        }

        return false;
    }

    public forEach<Z>(callbackfn: (...args: any[]) => void, thisArg?: Z): void
    {
        this._components.forEach(callbackfn, thisArg);
    }

    private update()
    {
        this._startQueue.forEach((c) =>
        {
            if (c.start)
                c.start();
        });

        this._components.forEach((c) =>
        {
            if (c.update)
                c.update();
        });

        this._startQueue.length = 0;
    }

}
interface StateComponentDirectory<T extends PhaserSpriteBehaviorWithEvents>
{
    [index: string]: T;
}



