import { IComponentAbleSprite } from "Components/";
import { SpriteArray } from "Libs/Phaser/";
import { DropAreaObject, WinSymbolObject, ParenthesisPair } from "GameObjects";
import * as Libs from "Libs";
import { game } from "main";

export type LoseObjectAppearAnimationFunc = (callback: Function, animationArgs: LoseAppearAnimationArgs) => void;

export let loseObjectAppearAnimationFunc: LoseObjectAppearAnimationFunc;

export function setLoseObjectAppearAnimationFunc(newAnimationFunc?: LoseObjectAppearAnimationFunc)
{
    if (newAnimationFunc)
    {
        loseObjectAppearAnimationFunc = newAnimationFunc;
    }
    else
    {
        loseObjectAppearAnimationFunc = defaultLoseObjectAppearAnimationFunc;
    }
}

function defaultLoseObjectAppearAnimationFunc(callback: Function, animationArgs: LoseAppearAnimationArgs)
{
    let { objectsInDropAreas, parenthesisPairs } = animationArgs;
    let callbackCounter = new Libs.CallbackCounter(objectsInDropAreas.length + parenthesisPairs.length,
        callback);

    objectsInDropAreas.forEach((obj) =>
    {
        let tween = game.add.tween(obj).
            to({ alpha: 1 }, 1000, Phaser.Easing.Exponential.InOut, true);
        tween.onComplete.addOnce(callbackCounter.CountUp, callbackCounter);
    });

    parenthesisPairs.forEach((obj) =>
    {
        let tween = game.add.tween(obj.parenthesisLeft).
            to({ alpha: 1 }, 1000, Phaser.Easing.Exponential.InOut, true);
        tween.onComplete.addOnce(callbackCounter.CountUp, callbackCounter);

        tween = game.add.tween(obj.parenthesisRight).
            to({ alpha: 1 }, 1000, Phaser.Easing.Exponential.InOut, true);
        tween.onComplete.addOnce(callbackCounter.CountUp, callbackCounter);
    });
}

loseObjectAppearAnimationFunc = defaultLoseObjectAppearAnimationFunc;


export interface LoseAppearAnimationArgs
{
    objectsInDropAreas: IComponentAbleSprite[];
    dropAreas: DropAreaObject[];
    parenthesisPairs: ParenthesisPair[];
    winSymbol: WinSymbolObject;
}


