GameCreator.doodadLayouts["MistLayout1"] = function (layoutCreator) {
    let mist1 = layoutCreator.create("Mist", 3, 0, 0, 0, true, -0.2, -50);
    mist1.position.set(0, gameHeight - mist1.height);
    mist1.alpha = 0.5;

    let mist2 = layoutCreator.create("Mist", 3, 0, 0, 0, true, -0.2, -50);
    mist2.position.set(mist1.width, gameHeight - mist2.height);
    mist2.alpha = 0.5;


    let mist3 = layoutCreator.create("Mist", 5, 0, 0, 0, true, -0.25, 50);
    mist3.position.set(-100, gameHeight - mist1.height);
    mist3.alpha = 0.3;

    let mist4 = layoutCreator.create("Mist", 5, 0, 0, 0, true, -0.25, 50);
    mist4.position.set(mist1.width - 100, gameHeight - mist2.height);
    mist4.alpha = 0.3;

};

