function endAnimationFunc(callback: Function, animationArgs: ProcessEndArgs) {
    let { newNumber, winSymbol, dropAreas } = animationArgs;

    let originalWinSymbolPosition = winSymbol.position.clone();
    let originalWinSymbolTint = winSymbol.tint;
    let originalNewNumberTint = newNumber.value.tint;

    let tintColor = 15073280;

    let centerX = game.world.centerX;
    let moveTween = game.add.tween(newNumber.value).
        to({ x: centerX }, 600, Phaser.Easing.Elastic.Out);

    let moveTweenNumberValue = game.add.tween(winSymbol).to(
        { left: newNumber.value.right - 50 }, 600, Phaser.Easing.Exponential.In);

    // let tintTween = tweenTint([newNumber.value, winSymbol], winSymbol.tint, tintColor, 500);

    moveTweenNumberValue.onComplete.addOnce(secondAnimationStage);

    moveTween.chain(moveTweenNumberValue).start();

    function secondAnimationStage() {
        let callBackCounter = new Lib.CallbackCounter(2, thirdAnimationStage);

        let moveBackTween = game.add.tween(winSymbol).
            to(originalWinSymbolPosition, 600, Phaser.Easing.Exponential.In);

        let numberFadeOut = game.add.tween(newNumber.value).
            to({ alpha: 0 }, 400);

        moveBackTween.start().onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
        numberFadeOut.start().onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
    }

    function thirdAnimationStage() {
        let callBackCounter = new Lib.CallbackCounter(dropAreas.length + 1, animationFin);

        newNumber.value.tint = originalNewNumberTint;

        callBackCounter.CountUp();

        dropAreas.forEach((dropArea) => {
            let tween = dropArea.game.add.tween(dropArea).
                to({ alpha: 1 }, 1000, Phaser.Easing.Linear.None, true);
            tween.onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
        });
    }

    function animationFin() {

    }
}

function BeginAnimationFunc(callback: Function, animationArgs: BaseAnimationArgs): void {

    let { dropAreas } = animationArgs;

    dropAreas.forEach((dropArea) => {
        let tween = dropArea.game.add.tween(dropArea).
            to({ alpha: 0 }, 1000, Phaser.Easing.Exponential.Out, true);
        tween.onComplete.addOnce(animationFin);
    });

    let count = 0;
    function animationFin() {
        count++;
        if (count < dropAreas.length)
            return;
        game.time.events.add(20, callback);
    }
}

function beginProcessAnimationFunc(callback: Function, animationArgs: ProcessBeginArgs): void {

    let { currentSymbol, right, left } = animationArgs;
    let { x, y } = currentSymbol.value;
    let scaleSize;
    let count = 0;

    if (isExponential(currentSymbol.value.symbolComponent)) {
        this.moveEquation = false;

        if (left) {
            animationFin();
            animationFin();
            let tween = game.add.tween(currentSymbol.value.position).to({ x: left.value.position.x, y: left.value.position.y }, 300, Phaser.Easing.Cubic.apply, false, 0);

            game.add.tween(currentSymbol.value).to({ alpha: 0 }, 200, Phaser.Easing.Cubic.In, true);

            let tween2 = game.add.tween(left.value.position).to({x: left.value.position.x, y: left.value.position.y - left.value.height / 4}, 250, Phaser.Easing.Exponential.In, false, 0);

            let tween3 = game.add.tween(left.value).to({ alpha: 0 }, 300, Phaser.Easing.Cubic.In, true, 200);
                tween3.onComplete.addOnce(animationFin);

                tween.chain(tween3);

                tween.start();

            }
    }
    else if (isSqrRoot(currentSymbol.value.symbolComponent)) {
        {
        this.moveEquation = true;
        this.moveEquationMod = 0.35;

        if (right) {

            let distance = right.value.x - currentSymbol.value.x;

            let moveTween1 = game.add.tween(currentSymbol.value.position).to({ x: currentSymbol.value.x + (distance / 2)}, 250, Phaser.Easing.Cubic.apply, true, 200).onComplete.addOnce(animationFin);
            let moveTween2 = game.add.tween(right.value.position).to({x: right.value.position.x - (distance / 2)}, 250, Phaser.Easing.Exponential.In, true, 200).onComplete.addOnce(animationFin);

            let alphaTween1 = game.add.tween(currentSymbol.value).to({ alpha: 0 }, 250, Phaser.Easing.Cubic.In, true, 200);
            let alphaTween2 = game.add.tween(right.value).to({ alpha: 0 }, 250, Phaser.Easing.Cubic.In, true, 200);

            animationFin();

            }
        }
    }
    else {
        this.moveEquation = true;
        this.moveEquationMod = 0.75;
        game.add.tween(currentSymbol.value).
            to({ alpha: 0 }, 400, Phaser.Easing.Circular.In, true, 820).
            onComplete.addOnce(animationFin);

        // number Scale

        if (right)
            game.add.tween(right.value.scale).
                to({ x: 1.5, y: 1.5 }, 250, Phaser.Easing.Cubic.apply, true, 0).yoyo(true).
                onComplete.addOnce(animationFin);
        else
            count++;

        if (left)
            game.add.tween(left.value.scale).
                to({ x: 1.5, y: 1.5 }, 250, Phaser.Easing.Cubic.apply, true, 0).yoyo(true).
                onComplete.addOnce(animationFin);
        else
            count++;

        // number fades
        if (right)
            game.add.tween(right.value).
                to({ alpha: 0 }, 400, Phaser.Easing.Cubic.In, true, 420);
        if (left)
            game.add.tween(left.value).
                to({ alpha: 0 }, 400, Phaser.Easing.Cubic.In, true, 420);

        // number movement
        if (right)
            game.add.tween(right.value).
                to({ x, y }, 300, Phaser.Easing.Cubic.In, true, 520).onStart.addOnce(playWindSound);
        if (left)
            game.add.tween(left.value).
                to({ x, y }, 300, Phaser.Easing.Cubic.In, true, 520);

    }

    function playWindSound() {
        windSound.play();
    }

    function animationFin() {
        count++;
        if (count < 3)
            return;
        harshNumberMoveSound.play();
        callback();
    }
}

function ProcessEndAnimationFunc(callback: Function, animationArgs: ProcessEndArgs): void {

    let { newNumber } = animationArgs;
    let { dropAreas } = animationArgs;
    let id = newNumber.index;
    let count = 0;
    let posX = animationArgs.newNumber.value.position.x;
    newNumber.value.alpha = 0;

    game.add.tween(newNumber.value).
        to({ alpha: 1 }, 300, Phaser.Easing.Default, true);

    game.add.tween(newNumber.value.scale).
        to({ x: 1.5, y: 1.5 }, 350, Phaser.Easing.Circular.In, true, 0, undefined, true);

    if (this.moveEquation === true) {
    if (parenthesisPair1 !== undefined) {
        if (parenthesisPair1.parenthesisLeft.position.x < posX) {
            game.add.tween(parenthesisPair1.parenthesisLeft.position).to({ x: parenthesisPair1.parenthesisLeft.position.x + dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }

        else if (parenthesisPair1.parenthesisLeft.position.x > posX) {
            game.add.tween(parenthesisPair1.parenthesisLeft.position).to({ x: parenthesisPair1.parenthesisLeft.position.x - dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }

        if (parenthesisPair1.parenthesisRight.position.x < posX) {
            game.add.tween(parenthesisPair1.parenthesisRight.position).to({ x: parenthesisPair1.parenthesisRight.position.x + dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }

        else if (parenthesisPair1.parenthesisRight.position.x > posX) {
            game.add.tween(parenthesisPair1.parenthesisRight.position).to({ x: parenthesisPair1.parenthesisRight.position.x - dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }
    }

    if (parenthesisPair2 !== undefined) {
        if (parenthesisPair2.parenthesisLeft.position.x < posX) {
            game.add.tween(parenthesisPair2.parenthesisLeft.position).to({ x: parenthesisPair2.parenthesisLeft.position.x + dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }

        else if (parenthesisPair2.parenthesisLeft.position.x > posX) {
            game.add.tween(parenthesisPair2.parenthesisLeft.position).to({ x: parenthesisPair2.parenthesisLeft.position.x - dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }

        if (parenthesisPair2.parenthesisRight.position.x < posX) {
            game.add.tween(parenthesisPair2.parenthesisRight.position).to({ x: parenthesisPair2.parenthesisRight.position.x + dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }

        else if (parenthesisPair2.parenthesisRight.position.x > posX) {
            game.add.tween(parenthesisPair2.parenthesisRight.position).to({ x: parenthesisPair2.parenthesisRight.position.x - dropAreas[0].width * this.moveEquationMod }, 500, Phaser.Easing.Cubic.In, true);
        }
    }

        animationArgs.otherNumberValues.forEach(numb => {
                if (numb){

                if (numb.value.position.x > posX){
                    game.add.tween(numb.value.position).to({ x: numb.value.position.x - animationArgs.dropAreas[0].width * this.moveEquationMod}, 500, Phaser.Easing.Cubic.In, true);
                    }

                else if (numb.value.position.x < posX)
                    game.add.tween(numb.value.position).to({  x: numb.value.position.x + animationArgs.dropAreas[0].width * this.moveEquationMod}, 500, Phaser.Easing.Cubic.In, true);
                }

                countUp();
            });


        animationArgs.otherSymbolValues.forEach(symb => {
                if (symb){

                if (symb.value.position.x > posX){
                    game.add.tween(symb.value.position).to({ x: symb.value.position.x - animationArgs.dropAreas[0].width * this.moveEquationMod}, 500, Phaser.Easing.Cubic.In, true);
                    }

                else if (symb.value.position.x < posX)
                    game.add.tween(symb.value.position).to({  x: symb.value.position.x + animationArgs.dropAreas[0].width * this.moveEquationMod}, 500, Phaser.Easing.Cubic.In, true);
                }

                countUp();
            });

            this.moveEquationMod = 1;
        }

    else {
        this.moveEquation = true;
        this.moveEquationMod = 1;
        game.time.events.add(700, animationFin);
    }

    function countUp()
        {
            count++;
            if (count < animationArgs.otherNumberValues.length + animationArgs.otherSymbolValues.length)
                return;
            animationFin();
        }

    function animationFin() {
        game.time.events.add(1200, callback);
    }
}

function LoseAnimationFunc(callback: Function, animationArgs: ProcessEndArgs) {
    let { newNumber, winSymbol, dropAreas } = animationArgs;

    if (parenthesisPair1){
    parenthesisPair1.parenthesisLeft.position.set(parenthesis1XPos[0],  parenthesisPair1.parenthesisRight.position.y);
    parenthesisPair1.parenthesisRight.position.set(parenthesis1XPos[1], parenthesisPair1.parenthesisLeft.position.y);
    }

    if (parenthesisPair2){
    parenthesisPair2.parenthesisLeft.position.set(parenthesis2XPos[0], parenthesisPair2.parenthesisRight.position.y);
    parenthesisPair2.parenthesisRight.position.set(parenthesis2XPos[1], parenthesisPair2.parenthesisLeft.position.y);
    }

    let originalWinSymbolPosition = winSymbol.position.clone();
    let originalWinSymbolTint = winSymbol.tint;
    let originalNewNumberTint = newNumber.value.tint;

    let tintColor = 15073280;

    let centerX = game.world.centerX;

    let moveTween = game.add.tween(newNumber.value).
        to({ x: centerX - newNumber.value.width / 2 }, 600, Phaser.Easing.Elastic.Out);

    let moveTweenNumberValue = game.add.tween(winSymbol.position).to(
        { x: centerX + newNumber.value.width / 2 + 60 }, 600, Phaser.Easing.Exponential.In);

    // let tintTween = tweenTint([newNumber.value, winSymbol], winSymbol.tint, tintColor, 500);

    moveTweenNumberValue.onComplete.addOnce(secondAnimationStage);

    moveTween.chain(moveTweenNumberValue).start();

    function secondAnimationStage() {
        let callBackCounter = new Lib.CallbackCounter(2, thirdAnimationStage);

        let moveBackTween = game.add.tween(winSymbol).
            to(originalWinSymbolPosition, 600, Phaser.Easing.Exponential.In);

        let numberFadeOut = game.add.tween(newNumber.value).
            to({ alpha: 0 }, 400);

        moveBackTween.start().onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
        numberFadeOut.start().onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
    }

    function thirdAnimationStage() {
        let callBackCounter = new Lib.CallbackCounter(dropAreas.length + 1, animationFin);

        newNumber.value.tint = originalNewNumberTint;

        // tweenTint([winSymbol], winSymbol.tint, originalWinSymbolTint, 1000).start().onComplete.addOnce(, callBackCounter);

    callBackCounter.CountUp();

        dropAreas.forEach((dropArea) => {
            if (dropArea.objectInArea && dropArea.objectInArea.getComponent(Comps.DragAndDropComponent)) {
                let tween = dropArea.game.add.tween(dropArea).
                    to({ alpha: 1 }, 1000, Phaser.Easing.Linear.None, true);
                tween.onComplete.addOnce(callBackCounter.CountUp, callBackCounter);
            }
            else
                callBackCounter.CountUp();

        });
    }

    function animationFin() {
        mathMan1.scratch();
        game.time.events.add(300, callback);
    }
}

function ParenthesisDoneAnimationFunc(callback: Function, animationArgs: ProcessEndArgs)
{

    let { newNumber, dropAreas, currentParenthesisPair, otherParenthesisPair } = animationArgs;
    let id = newNumber.index;
    let count = 0;
    let posX = animationArgs.newNumber.value.position.x;
    let pareMoveMod = 1.5;
    if (animationArgs.currentParenthesisPair)
    {
        let { parenthesisLeft, parenthesisRight } = animationArgs.currentParenthesisPair;
        let callBackCounter = new Lib.CallbackCounter(2, callback);

        game.add.tween(parenthesisLeft).to({ alpha: 0 }, 333, Phaser.Easing.Exponential.In).start().
            onComplete.addOnce(countUp);

        game.add.tween(parenthesisRight).to({ alpha: 0 }, 333, Phaser.Easing.Exponential.In).start().
            onComplete.addOnce(countUp);

        if (this.moveEquation === true) {
            if (otherParenthesisPair.length > 0)
                {
                    if (parenthesisPair2.parenthesisLeft.position.x < parenthesisPair1.parenthesisLeft.position.x)
                        {
                            game.add.tween(parenthesisPair2.parenthesisLeft.position).to({ x: parenthesisPair2.parenthesisLeft.position.x + dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);
                            game.add.tween(parenthesisPair2.parenthesisRight.position).to({ x: parenthesisPair2.parenthesisRight.position.x - dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);
                        }
                    else
                        {
                            game.add.tween(parenthesisPair2.parenthesisLeft.position).to({ x: parenthesisPair2.parenthesisLeft.position.x - dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);
                            game.add.tween(parenthesisPair2.parenthesisRight.position).to({ x: parenthesisPair2.parenthesisRight.position.x - dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);
                        }
                }

            animationArgs.otherNumberValues.forEach(numb => {

                if (numb){

                if (numb.value.position.x < parenthesisLeft.position.x){
                    game.add.tween(numb.value.position).to({ x: numb.value.position.x + dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);
                    }

                else if (numb.value.position.x > parenthesisRight.position.x)
                    game.add.tween(numb.value.position).to({  x: numb.value.position.x - dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);
                }

                countUp();

            });

            animationArgs.otherSymbolValues.forEach(symb => {
                if (symb){

                if (symb.value.position.x < parenthesisLeft.position.x){
                    game.add.tween(symb.value.position).to({ x: symb.value.position.x + dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);
                    }

                else if (symb.value.position.x > parenthesisRight.position.x)
                    game.add.tween(symb.value.position).to({  x: symb.value.position.x - dropAreas[0].width / pareMoveMod}, 500, Phaser.Easing.Cubic.In, true);

                countUp();

            }
            });
        }
    }
    else
    {
        this.moveEquation = true;
        callback();
    }

        function countUp()
        {
            count++;
            if (count < animationArgs.otherNumberValues.length + animationArgs.otherSymbolValues.length + 2)
                return;
            animationFin();
        }

        function animationFin() {
            game.time.events.add(1200, callback);
    }
}

function DropInAreaTween(spriteToMove: Phaser.Sprite,
    endDropArea: DropAreaObject): Phaser.Tween {

        return spriteToMove.game.add.tween(spriteToMove).
            to({ x: endDropArea.position.x, y: endDropArea.position.y },
            333, Phaser.Easing.Elastic.Out);
}


function WinAnimationFunc(callback: Function, animationArgs: ProcessEndArgs) {
    let { newNumber, winSymbol } = animationArgs;

    let tintColor = 531890;

    let centerX = game.world.centerX;

    let moveTween = game.add.tween(newNumber.value).
        to({ x: centerX - newNumber.value.width / 2 }, 600, Phaser.Easing.Elastic.Out);

    let moveTweenNumberValue = game.add.tween(winSymbol.position).to(
        { x: centerX + newNumber.value.width / 2 + 60 }, 600, Phaser.Easing.Exponential.In);

    // let tintTween = tweenTint([newNumber.value, winSymbol], winSymbol.tint, tintColor, 500);

    let scaleTween = game.add.tween(newNumber.value.scale).
        to({ x: 2, y: 2 }, 600, Phaser.Easing.Exponential.InOut, false, 30).yoyo(true);

    scaleTween.onComplete.addOnce(animationFin);
    moveTween.chain(moveTweenNumberValue, scaleTween).start();
    mathMan1.castMagic();

    function animationFin() {

        switch (worldProgress) {
            case 0:
                GrowObject1.growPlant(4, callback);
                break;
            case 1:
                GrowObject1.growPlant(4, callback);
                break;
            case 2:
                GrowObject1.growPlant(3, callback);
                break;
            case 3:
                GrowObject1.growPlant(3, callback);
                break;
            case 4:
                GrowObject1.growPlant(1, callback);
                break;
            case 5:
                GrowObject2.growPlant(2);
                GrowObject3.growPlant(3, callback);
                break;
            case 6:
                GrowObject2.growPlant(3, callback);
                GrowObject3.growPlant(3);
                break;
            case 7:
                GrowObject2.growPlant(2);
                GrowObject3.growPlant(2, callback);
                break;
            case 8:
                GrowObject2.growPlant(2, callback);
                GrowObject3.growPlant(2);
                break;
            case 9:
                GrowObject2.growPlant(3);
                GrowObject3.growPlant(2, callback);
                break;
            default:
                break;
        }
    }
}

function tiltScaleRight() {
    scaleObject.tiltScale(false);
}

function tiltScaleLeft() {
    scaleObject.tiltScale(true);
}
