function addSpace(space: number) {
    if (newLevel) {
        spaceToAdd = 0;
        newLevel = false;
    }

    spaceToAdd += space;
    return spaceToAdd;
}

function isExponential(obj: Comps.SymbolComponent): obj is Comps.ExponentiationComponent {
    return obj instanceof Comps.ExponentiationComponent;
}

function isSqrRoot(obj: Comps.SymbolComponent): obj is Comps.RootComponent {
    return obj instanceof Comps.RootComponent;
}

function playNumberMoveSound(distance: number) {

    if (distance < 10)
        return;

    else if (distance > 10 && distance < 100)
        smallNumberMoveSound.play();

    else if (distance > 100)
        harshNumberMoveSound.play();

}

function buildWorld(spritesToBuildFromBottom?: Phaser.Sprite[], spritesToBuildFromRight?: Phaser.Sprite[], spritesToBuildFromLeft?: Phaser.Sprite[], spritesToBuildFromAbove?: Phaser.Sprite[]) {
    let delayMod = 400;
    let reductionMod = 25;
    let reductionFactor = 0;
    let delay = 0;

    let pareStart1;
    let pareEnd1;
    let pareStart2;
    let pareEnd2;

    if (spritesToBuildFromBottom) {
        spritesToBuildFromBottom.forEach(sprite => {
            let oldPos = sprite.position.y;

            sprite.position.set(sprite.position.x, gameHeight);

            let spriteTween = sprite.game.add.tween(sprite).
                to({ y: oldPos }, 300, Phaser.Easing.Circular.Out, true, delay).onStart.addOnce(playSound);

            delay += delayMod - reductionFactor;
            reductionFactor += reductionMod;
        });
    }

    delay += delayMod - reductionFactor;
    reductionFactor += reductionMod;

    if (spritesToBuildFromRight) {
        spritesToBuildFromRight.forEach(sprite => {
            let oldPos = sprite.position.x;

            sprite.position.set(gameWidth, sprite.position.y);

            let spriteTween = sprite.game.add.tween(sprite).
                to({ x: oldPos }, 300, Phaser.Easing.Circular.Out, true, delay).onStart.addOnce(playSound);

            delay += delayMod - reductionFactor;
            reductionFactor += reductionMod;
        });
    }

    delay += delayMod - reductionFactor;
    reductionFactor += reductionMod;

    if (spritesToBuildFromLeft) {
        spritesToBuildFromLeft.forEach(sprite => {
            let oldPos = sprite.position.x;

            sprite.position.set(-gameWidth - sprite.width, sprite.position.y);

            let spriteTween = sprite.game.add.tween(sprite).
                to({ x: oldPos }, 300, Phaser.Easing.Circular.Out, true, delay).onStart.addOnce(playSound);

            delay += delayMod - reductionFactor;
            reductionFactor += reductionMod;
        });
    }

    delay += delayMod - reductionFactor;
    reductionFactor += reductionMod;

    if (spritesToBuildFromAbove) {
        spritesToBuildFromAbove.forEach(sprite => {
            let oldPos = sprite.position.y;

            sprite.position.set(sprite.position.x, gameHeight);

            let spriteTween = sprite.game.add.tween(sprite).
                to({ y: oldPos }, 300, Phaser.Easing.Circular.Out, true, delay).onStart.addOnce(playSound);

            delay += delayMod - reductionFactor;
            reductionFactor += reductionMod;
        });
    }

    function playSound() {
        harshNumberMoveSound.play();
    }

    newWorld = false;
}

function createLevel(levelCreator: LevelCreator, dropAreas: string[], numbers: number[], winconType: string, winconAmount: number, dropAreaScaleModifier?: number,
    spaceBetweenNumbers?: number, firstDropAreaXOffset?: number) {

    if (dropAreaScaleModifier)
        levelCreator.state.intermittedNumberObjects.onCreate = f => {
            f.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
            return f;
        };
        else{
        levelCreator.state.intermittedNumberObjects.onCreate = undefined;
        }

    worldProgress = (levelCreator.level % 10);

    newLevel = true;
    let dropAreasX = 50;
    let dropAreasY = 120;
    let _spaceBetweenNumbers = 100;
    let xOffset = 0;

    let pareStart1;
    let pareEnd1;
    let pareStart2;
    let pareEnd2;

    levelDropPositions = [];
    let dropAreaIndex = 0;

    if (dropAreaScaleModifier)
        _spaceBetweenNumbers = _spaceBetweenNumbers * dropAreaScaleModifier;
    if (firstDropAreaXOffset)
        xOffset += firstDropAreaXOffset;

    if (spaceBetweenNumbers)
        _spaceBetweenNumbers = spaceBetweenNumbers;

    dropAreas.forEach(str => {
        if (str === "+" || str === "-" || str === "*" || str === "/") {
            let fixedArea: DropAreaObject = levelCreator.create.dropArea(addSpace(_spaceBetweenNumbers), dropAreasY);
            fixedArea.addObjectFixed(levelCreator.create.fixed.symbol(str));
            fixedArea.alpha = 0;

            if (dropAreaScaleModifier) {
                fixedArea.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
                let object = fixedArea.objectInArea;

                if (object)
                    object.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
            }
            dropAreaIndex++;
            levelDropPositions.push(fixedArea);
        }

        else if (str === "empty") {
            let area: DropAreaObject = levelCreator.create.dropArea(addSpace(_spaceBetweenNumbers), dropAreasY);
            area.alpha = 0.8;
            if (dropAreaScaleModifier)
                area.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);

            dropAreaIndex++;
            levelDropPositions.push(area);

        }

        else if (str === "^") {
            let xOffset = 85;
            let yOffset = 80;

            if (dropAreaScaleModifier)
                {
                    xOffset = xOffset * dropAreaScaleModifier;
                    yOffset = yOffset * dropAreaScaleModifier;
                }

            let fixedArea: DropAreaObject = levelCreator.create.dropArea(levelDropPositions[dropAreaIndex - 1].position.x + xOffset, dropAreasY - yOffset);
            fixedArea.alpha = 0;
            levelCreator.create.complexSymbol.exponentiation(fixedArea, 2);

            if (fixedArea.objectInArea){
                fixedArea.objectInArea.scale.multiply(0.8, 0.8);

                if (dropAreaScaleModifier)
                    fixedArea.objectInArea.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
            }
            dropAreaIndex++;
            levelDropPositions.push(fixedArea);

        }

        else if (str === "sqr"){

            let fixedArea: DropAreaObject = levelCreator.create.dropArea(addSpace(_spaceBetweenNumbers), dropAreasY);
            levelCreator.create.complexSymbol.root(fixedArea, 2);
            fixedArea.alpha = 0;
            if (dropAreaScaleModifier){
                fixedArea.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
                if (fixedArea.objectInArea)
                    fixedArea.objectInArea.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
            }
            dropAreaIndex++;
            levelDropPositions.push(fixedArea);

        }

        else if (str.charAt(0) === "(" || str.charAt(0) === ")") {

            if (str.charAt(0) === "(") {
                if (str.charAt(1) === "1")
                    pareStart1 = dropAreaIndex;
                if (str.charAt(1) === "2")
                    pareStart2 = dropAreaIndex;
            }

            else if (str.charAt(0) === ")") {
                if (str.charAt(1) === "1")
                    pareEnd1 = dropAreaIndex - 1;

                if (str.charAt(1) === "2")
                    pareEnd2 = dropAreaIndex - 1;

                if (dropAreas[dropAreaIndex] === "^")
                    {
                        pareEnd1 --;
                        pareEnd2 --;
                    }
            }
                addSpace(_spaceBetweenNumbers / 1.5);
        }

        else {
            console.log("input " + str + " is not valid. Try a symbol: '+, -, *, /,' or 'empty'");
        }


    });

    // set paranthesis pairs
    if (pareStart1 !== undefined && pareEnd1 !== undefined) {
        parenthesis1XPos = [levelDropPositions[pareStart1].position.x - (_spaceBetweenNumbers), levelDropPositions[pareEnd1].position.x + (_spaceBetweenNumbers)];

        parenthesisPair1 = levelCreator.create.parenthesisPair(levelDropPositions[pareStart1], levelDropPositions[pareEnd1], 1);

        parenthesisPair1.parenthesisLeft.position.setTo(parenthesis1XPos[0], dropAreasY);
        parenthesisPair1.parenthesisRight.position.setTo(parenthesis1XPos[1], dropAreasY);
        if (dropAreaScaleModifier){
             parenthesisPair1.parenthesisLeft.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
             parenthesisPair1.parenthesisRight.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
        }
    }
    if (pareStart2 !== undefined && pareEnd2 !== undefined) {
        parenthesis2XPos = [levelDropPositions[pareStart2].position.x - (_spaceBetweenNumbers), levelDropPositions[pareEnd2].position.x + (_spaceBetweenNumbers)];

        parenthesisPair2 = levelCreator.create.parenthesisPair(levelDropPositions[pareStart2], levelDropPositions[pareEnd2], 2);

        parenthesisPair2.parenthesisLeft.position.setTo(parenthesis2XPos[0], dropAreasY);
        parenthesisPair2.parenthesisRight.position.setTo(parenthesis2XPos[1], dropAreasY);

        if (dropAreaScaleModifier){
             parenthesisPair2.parenthesisLeft.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
             parenthesisPair2.parenthesisRight.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
        }
        if (pareStart1 === pareStart2) {
            parenthesisPair2.parenthesisLeft.position.setTo(levelDropPositions[pareStart2].position.x - (_spaceBetweenNumbers / 1.1), dropAreasY);
        }

        if (pareEnd1 === pareEnd2) {
            parenthesisPair2.parenthesisRight.position.setTo(levelDropPositions[pareEnd2].position.x + (_spaceBetweenNumbers / 1.1), dropAreasY);
        }
    }

    setBottomNumberPositions([homePos1, homePos2, homePos3, homePos4, homePos5, homePos6, homePos7], numbers);

    numbers.forEach(num => {
        let n = levelCreator.create.number(num);
        if (dropAreaScaleModifier)
            n.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
    });

    let pos = addSpace(_spaceBetweenNumbers) + 50;

    if (pos < game.world.centerX){
            pos = game.world.centerX + 50;
        }
    let endArea = levelCreator.create.winCheck(winconType, winconAmount, pos, dropAreasY);

    if (dropAreaScaleModifier){
        endArea.scale.multiply(dropAreaScaleModifier, dropAreaScaleModifier);
    }

    function addSpace(space: number) {
        if (newLevel) {
            spaceToAdd = 0;
            newLevel = false;
        }

        spaceToAdd += space;
        return spaceToAdd;
    }

    function setBottomNumberPositions(homes: NumberManager.HomePosition[], numbersInUse: number[]) {

        let xIncrease = 100;
        let xStartPos = (870 - (numbersInUse.length * 100)) / 2;

        homes.forEach(element => {
            element.x = xStartPos + xIncrease;
            xIncrease += 100;
        });
    }

}

function createLevelFunc(dropAreas: string[], numbers: number[], winconType: string, winconAmount: number, dropAreaScaleModifier?: number,
    spaceBetweenNumbers?: number, firstDropAreaXOffset?: number): (LevelCreator) => void
    {
       return function(LevelCreator)
        {
            createLevel(LevelCreator, dropAreas, numbers, winconType, winconAmount, dropAreaScaleModifier, spaceBetweenNumbers, firstDropAreaXOffset);
        };
    }

