function createEquationLevel(levelCreator: LevelCreator, objects: string[], dragables: string[], dropAreaScaleModifier?: number)
    {
        newLevel = true;
        let _spaceBetweenNumbers = 70;
        let yCord = 100;
        let currentObjectIndex = 0;
        let leftSide = true;
        let secondStatePosition;
        let xIsDefined;
        let xVar = 1;
        let objectsBeforeMiddle = 1;
        let objectsAfterMiddle = 0;
        let beforeMiddle = true;


        objects.forEach(str => {

    if (beforeMiddle)
        {
            if (str !== "="){
                objectsBeforeMiddle ++;

                if (str === "empty")
                    objectsBeforeMiddle += 0.3;
            }

            else
                beforeMiddle = false;

        }
    else
            objectsAfterMiddle ++;
        });

        objects.forEach(str => {

            if (Number(str))
                {
                    if (objects[currentObjectIndex + 1] === "X")
                        {
                            xVar = Number(str);
                        }
                    else if (objects[currentObjectIndex - 1] === "/" && objects[currentObjectIndex - 2] === "X")
                        {
                            xVar = Number(str);
                        }
                    else
                        {
                            let area = levelCreator.create.dropArea(gameWidth / 2 + addSpace(_spaceBetweenNumbers) - (_spaceBetweenNumbers * objectsBeforeMiddle), yCord);
                            area.addObjectFixed(levelCreator.create.fixed.number(Number(str)));
                            area.alpha = 0;
                            if (Number(str) > 10)
                                {
                                    addSpace(_spaceBetweenNumbers * 0.2);
                                }
                        }
                    currentObjectIndex ++;
                }

            else if (str === "X")
                {
                    if (objects[currentObjectIndex + 1] === "/")
                        {
                            objectsBeforeMiddle -= 1;

                            let area = levelCreator.create.dropArea(gameWidth / 2 + addSpace(_spaceBetweenNumbers) - (_spaceBetweenNumbers * objectsBeforeMiddle), yCord);
                            let nmbr = levelCreator.create.fixed.number(xVar / Number(objects[currentObjectIndex + 2]));
                            nmbr.isX = true;
                            nmbr.numberComponent.divisionStyling = true;
                            area.alpha = 0;
                            area.addObjectFixed(nmbr);
                            addSpace(_spaceBetweenNumbers);

                        }
                    else
                        {
                             if (xVar > 1)
                                {
                                    objectsBeforeMiddle += 0.5;
                                }

                            let area = levelCreator.create.dropArea(gameWidth / 2 + addSpace(_spaceBetweenNumbers) - (_spaceBetweenNumbers * objectsBeforeMiddle), yCord);
                            let nmbr = levelCreator.create.fixed.number(xVar);
                            area.alpha = 0;
                            nmbr.isX = true;
                            area.addObjectFixed(nmbr);

                        }
                    if (xVar > 1)
                        addSpace(_spaceBetweenNumbers / 2);

                        currentObjectIndex ++;
                }

            else if (str === "+" || str === "-" || str === "/" || str === "*")
                {
                    if (str !== "/" && objects[currentObjectIndex] !== "X")
                        {
                            let area = levelCreator.create.dropArea(gameWidth / 2 + addSpace(_spaceBetweenNumbers) - (_spaceBetweenNumbers * objectsBeforeMiddle), yCord);
                            area.addObjectFixed(levelCreator.create.fixed.symbol(str));
                            area.alpha = 0;

                        }
                        currentObjectIndex ++;
                }

            else if (str === "=")
                {
                    addSpace(_spaceBetweenNumbers * 1.5);
                    let winSymbol = levelCreator.create.winCheck("=", 0, 0, yCord);
                    winSymbol.position.set(gameWidth / 2 - winSymbol.width / 2, yCord);
                    leftSide = false;

                    currentObjectIndex ++;
                    secondStatePosition = levelCreator.state.dropAreaObjects.length;

                }

            else if (str === "empty")
                {
                    let area: DropAreaObject = levelCreator.create.dropArea(gameWidth / 2 + addSpace(_spaceBetweenNumbers * 1.5) - (_spaceBetweenNumbers * objectsBeforeMiddle), yCord);

                    if (leftSide) {
                            area.dropArea.events.onNoObjectInArea.add(tiltScaleRight);
                            area.dropArea.events.onAreaReserved.add(tiltScaleLeft);
                        }
                    else
                        {
                            area.dropArea.events.onNoObjectInArea.add(tiltScaleLeft);
                            area.dropArea.events.onAreaReserved.add(tiltScaleRight);
                        }


                        currentObjectIndex ++;
                }
        });
/*
        dragables.forEach(str => {
            levelCreator.create.compositeSpawner(str.charAt[0], str.charAt[1], addSpace(yCord) - 400, 500);
        }); */

        let xIncrease = 100;
        let xStartPos = (870 - (dragables.length * 150)) / 2;
        dragables.forEach(spawner => {
            let symbol = spawner.charAt(0);

            if (symbol === "+" || symbol === "-" || symbol === "/" || symbol === "*") {
                levelCreator.create.compositeSpawner(symbol, Number(spawner.charAt(1)), xStartPos + xIncrease, 500);
                xIncrease += 150;
            }
        });
    /*
        levelCreator.create.compositeSpawner("*", 7, 350, 500);
        levelCreator.create.compositeSpawner("/", 7, 450, 500);
        levelCreator.create.compositeSpawner("+", 7, 550, 500);
        levelCreator.create.compositeSpawner("-", 7, 650, 500);
    */

        console.log(secondStatePosition);
        levelCreator.set.secondStageStartDropArea(levelCreator.state.dropAreaObjects[secondStatePosition]);

        function addSpace(space: number) {
            if (newLevel) {
                spaceToAdd = 0;
                newLevel = false;
            }

            spaceToAdd += space;
            return spaceToAdd;
        }
    }


