import { PhaserSpriteBehavior, IComponentAbleSprite } from "./PhaserComponent/";
import { RectangleOverlapManger } from "Managers/";

export abstract class RectangleOverlapComponent extends PhaserSpriteBehavior
    implements RectangleOverlapManger.IRectangleOverlapItem
{

    public static readonly componentName: string = "RectangleOverlapComponent";
    public receiveOverlapEvent: boolean;
    protected overlapRectangle: Phaser.Rectangle;
    protected overlapRectangleOffSet: Phaser.Point;
    public getOverlapRectangle(): Phaser.Rectangle | PIXI.Rectangle
    {
        if (this.overlapRectangle)
        {
            this.overlapRectangle.x = this.sprite.position.x + this.overlapRectangleOffSet.x;
            this.overlapRectangle.y = this.sprite.position.y + this.overlapRectangleOffSet.y;
            return this.overlapRectangle;
        }
        return this.sprite.getBounds();
    }

    constructor(sprite: IComponentAbleSprite, layer: string | string[], overlapLayers: string[] = [], overlapRectangle?: Phaser.Rectangle)
    {
        super(sprite);
        if (overlapRectangle)
        {
            this.overlapRectangleOffSet = new Phaser.Point(overlapRectangle.x, overlapRectangle.y);
            this.overlapRectangle = overlapRectangle;
        }
        RectangleOverlapManger.addItem(this, layer, overlapLayers);
    }

    protected onRemove()
    {
        RectangleOverlapManger.removeItem(this);
    }
}


