import { PhaserSpriteBehavior, InterfaceComponentAbleType } from "Components/PhaserComponent/";
import { INumberObject, ISymbolObject, DropAreaObject } from "GameObjects/";
import * as Libs from "Libs/";

export interface IWinCheckComponent extends PhaserSpriteBehavior
{
    checkForWin(numberValues: Libs.ValueIndex<INumberObject>[],
        symbolValues: Libs.ValueIndex<ISymbolObject>[]): boolean;
}

export const IWinCheckComponentType: InterfaceComponentAbleType<IWinCheckComponent> = {
    componentName: "IWinCheckComponent"
};


