import { dropInAreaTween, goHomeTween } from "Tweens/numberObjectTweens";
import { DropAreaComponent, ObjectInAreaStatusEnum } from "./dropAreaComponent";
import { IComponentAbleSprite, InheritableComponentType, InterfaceComponentAbleType } from "./PhaserComponent/";
import { RectangleOverlapComponent } from "./rectangleOverlapComponent";
import { DropAreaObject } from "GameObjects";

export class DragAndDropComponent extends RectangleOverlapComponent
{
    public static readonly componentName: string = "DragAndDropComponent";
    public static readonly inheritedComponentNames: InheritableComponentType[] = [RectangleOverlapComponent];

    protected _moveTween: Phaser.Tween;
    protected _moveToPoint: { x: number, y: number } | Phaser.Point;
    protected _dropAreaUsing: DropAreaComponent | null = null;
    protected _isBeginMove: boolean = false;

    public get getIsBeginDragged(): boolean
    {
        return this._isBeginMove;
    }

    public events: {
        onDragStart: Phaser.Signal;
        onDragStop: Phaser.Signal;
        onMoveTowardsDropArea: Phaser.Signal;
    };

    private _eventDragstart()
    {
        this._isBeginMove = true;
        if (this._moveTween)
            this._moveTween.stop();
        if (this._dropAreaUsing)
        {
            this._dropAreaUsing.SetObjectInArea(this.sprite, ObjectInAreaStatusEnum.reservedArea);
        }
        this.receiveOverlapEvent = true;
    }

    private _eventDragEnd()
    {
        this._isBeginMove = false;
        if (this._dropAreaUsing)
        {
            this._dropAreaUsing.SetObjectInArea(this.sprite, ObjectInAreaStatusEnum.movingTowardsArea);
            this._moveToPoint = this._dropAreaUsing.sprite.position;
            this.events.onMoveTowardsDropArea.dispatch(this._dropAreaUsing);
            let endDropArea: DropAreaObject = this._dropAreaUsing.sprite as DropAreaObject;
            this._beginTweenMoveDropArea(endDropArea, (() =>
            {
                if (this._dropAreaUsing)
                    this._dropAreaUsing.SetObjectInArea(this.sprite, ObjectInAreaStatusEnum.inArea);

            }), this);
        }
        else
        {
            this.notDropInArea();
        }
        this.receiveOverlapEvent = false;
    }

    protected notDropInArea()
    {
        throw "can not use base class : DragAndDropComponent";
    }


    private _beginTweenMoveDropArea(dropAreaObject: DropAreaObject, tweenCompleteCallback?: Function, listenerContext?: any): void
    {
        if (this._moveTween)
            this._moveTween.stop();
        if (!this._moveTween)
            this._moveTween = dropInAreaTween(this.sprite, dropAreaObject);
        else
        {
            let oldTween = this._moveTween;
            this._moveTween = dropInAreaTween(this.sprite, dropAreaObject);
            this._moveTween.onChildComplete = oldTween.onChildComplete;
            this._moveTween.onComplete = oldTween.onComplete;
            this._moveTween.onLoop = oldTween.onLoop;
            this._moveTween.onRepeat = oldTween.onRepeat;
            this._moveTween.onStart = oldTween.onStart;
        }
        if (tweenCompleteCallback)
        {
            this._moveTween.onComplete.addOnce(tweenCompleteCallback, listenerContext);
        }

        this._moveTween.start();
    }

    public onOverlapStay(others: IComponentAbleSprite[])
    {
        if (!this._dropAreaUsing)
        {
            others = others.
                filter(o => (o.getComponent<DropAreaComponent>(DropAreaComponent) as
                    DropAreaComponent).getObjectInAreaStatus === ObjectInAreaStatusEnum.noObject);

            if (others.length > 0)
            {
                this._dropAreaUsing = <DropAreaComponent>others[0].getComponent(DropAreaComponent);
                this._dropAreaUsing.SetObjectInArea(this.sprite, ObjectInAreaStatusEnum.reservedArea);
            }

        }
    }

    public onOverlapExit(others: IComponentAbleSprite[])
    {
        if (this._dropAreaUsing
            && this._dropAreaUsing.getObjectInAreaStatus !== ObjectInAreaStatusEnum.inArea
            && this._dropAreaUsing.getObjectInAreaStatus !== ObjectInAreaStatusEnum.movingTowardsArea)
        {
            if (others.indexOf(this._dropAreaUsing.sprite) !== -1)
            {
                this._dropAreaUsing.SetObjectInArea(this.sprite, ObjectInAreaStatusEnum.noObject);
                this._dropAreaUsing = null;
            }
        }
    }

    protected onRemove()
    {
        super.onRemove();
        this.events.onDragStart.remove(this._eventDragstart, this);
        this.events.onDragStop.remove(this._eventDragEnd, this);
    }

    public SetDragAble(isDragAble: boolean)
    {
        if (isDragAble)
        {
            this.sprite.input.enableDrag();
        }
        else
        {
            this.sprite.input.disableDrag();
            this.receiveOverlapEvent = false;
        }
    }

    constructor(sprite: IComponentAbleSprite,
        layer: string | string[], overlapLayers: string[], dropHitBox?: Phaser.Rectangle)
    {
        super(sprite, layer, overlapLayers, dropHitBox);
        this.events = {
            get onDragStart(): Phaser.Signal
            {
                return sprite.events.onDragStart;
            },
            get onDragStop(): Phaser.Signal
            {
                return sprite.events.onDragStop;
            },
            onMoveTowardsDropArea: new Phaser.Signal()
        };
        this.sprite.inputEnabled = true;
        this.events.onDragStart.add(this._eventDragstart, this);
        this.events.onDragStop.add(this._eventDragEnd, this);
        this.sprite.input.enableDrag();
    }
}


