import { MainState } from "States/mainState";
import
{
    DADHomeComponent, IComponentAbleSprite,
    ObjectInAreaStatusEnum, DadSpawnComponent, DragAndDropComponent
} from "Components/";
import { loseObjectAppearAnimationFunc } from "./loseAnimation";
import { MathProcess } from "../MathProcess/";
import * as Libs from "Libs/";

export class LoseProcess
{
    private _state: MainState;
    private _callback: Function;
    private _objects: IComponentAbleSprite[];

    public beginProcess(callback: Function, state: MainState)
    {
        this._state = state;
        this._callback = callback;
        this._objects = [];

        for (let index = 0; index < state.dropAreaObjects.length; index++)
        {
            let element = state.dropAreaObjects[index];
            if (element.objectInArea)
            {
                this._objects.push(element.objectInArea);
            }
        }

        for (let compositeObject of this._state.compositeSpawnObject)
        {
            if (!compositeObject.compositeComponent.isMerged)
            {
                compositeObject.compositeComponent.merge();
            }
        }


        loseObjectAppearAnimationFunc(this._start.bind(this),
            {
                objectsInDropAreas: Libs.cloneArray(this._objects),
                dropAreas: state.dropAreaObjects.clone(),
                winSymbol: state.winSymbolObjects[0],
                parenthesisPairs: state.parenthesisPairObjects.clone()
            });
    }

    private _start()
    {
        let callbackCounter = new Libs.CallbackCounter(this._state.dropAreaObjects.length
            , this._whenObjectAreHome, this);

        for (let index = 0; index < this._state.dropAreaObjects.length; index++)
        {
            let dropArea = this._state.dropAreaObjects[index];
            let numberInArea = dropArea.objectInArea;

            if (numberInArea)
            {
                this._objects.push(numberInArea);

                let dadComp = numberInArea.getComponent(DADHomeComponent);

                if (dadComp)
                {
                    dadComp.moveHome(callbackCounter.CountUp, callbackCounter);
                }
                else
                {
                    let dadSpawn = numberInArea.getComponent(DadSpawnComponent);
                    if (dadSpawn)
                    {
                        dadSpawn.disappear();
                    }
                    callbackCounter.amountToReach--;
                }

            }
            else
            {
                callbackCounter.amountToReach--;
            }
        }
    }

    private _whenObjectAreHome()
    {
        this._state.dragAbleNumberObjects.forEach(i => i.dADHomeComponent.SetDragAble(true));
        this._state.dragAbleSymbolObjects.forEach(i => i.dADHomeComponent.SetDragAble(true));

        this._state.intermittedNumberObjects.forEach(i => i.exists = false);
        this._state.intermittedSymbolsObjects.forEach(i => i.exists = false);

        this._state.dropAreaObjects.forEach((i) =>
        {
            if (!i.objectInArea || i.objectInArea.getComponent(DragAndDropComponent))
            {
                i.dropArea.SetObjectInArea(null, ObjectInAreaStatusEnum.noObject);
            }
        });

        this._callback();
    }

}


