import * as GO from "GameObjects/";
import { game } from "main";
import { exitsInArray } from "Libs/";


type AssetLoadCallback = (game: Phaser.Game) => void;
let assetLoadFuncs: AssetLoadCallback[][] = [[]];
let callBackIdentifiers: string[] = [];

export enum StateEnum { Preload, Main }


export function loadMainSceneAsset(stateEnum: StateEnum)
{
    if (stateEnum === StateEnum.Main)
    {
        Tools.loadAllAssetsImports(GO);
    }
    assetLoadFuncs[stateEnum].forEach(i => i(game));
}

export function assetLoadMethod(stateEnum: StateEnum, identifier?: string)
{
    return function (target: any, propertyKey: string, descriptor: PropertyDescriptor)
    {
        if (identifier !== undefined)
        {
            if (exitsInArray(callBackIdentifiers, identifier))
            {
                return;
            }
            else
            {
                callBackIdentifiers.push(identifier);
            }
        }

        if (!assetLoadFuncs[stateEnum])
        {
            assetLoadFuncs[stateEnum] = [];
        }
        assetLoadFuncs[stateEnum].push(descriptor.value);
    };
}

namespace Tools
{
    type LoadFunction = (game: Phaser.Game) => void;

    interface IObjectWithLoadAssets
    {
        loadAssets: (game: Phaser.Game) => void;
    }

    function IObjectWithLoadAssetsCheck(obj: any): obj is IObjectWithLoadAssets
    {
        return typeof obj.loadAssets === "function";
    }

    export function loadAllAssets(...loadFunctions: Array<IObjectWithLoadAssets | LoadFunction>)
    {
        for (let loadAsset of loadFunctions)
        {
            if (IObjectWithLoadAssetsCheck(loadAsset))
            {
                loadAsset.loadAssets(game);
            }
            else
            {
                loadAsset(game);
            }
        }
    }

    export function loadAllAssetsImports(gameObjectsImport: any)
    {
        for (let gameObjectClassName in gameObjectsImport)
        {
            let element = gameObjectsImport[gameObjectClassName];
            if (IObjectWithLoadAssetsCheck(element) &&
                !exitsInArray(assetLoadFuncs[StateEnum.Main], element.loadAssets))
                element.loadAssets(game);

        }
    }
}


