import { TreeParticleObject } from "GameObjects/Particles";
import { game } from "main";

export class ScaleObject extends Phaser.Sprite
{

    public static readonly doodadName = "Scale";

    public scaleBalance: Phaser.Tween;
    public scaleShake: Phaser.Tween;
    private scaleWeight: Phaser.Sprite;
    private idealAngle;
    private oldAngle = 0;
    private testTilt = true;
    private testDirection = 0;
    private moneyBag;
    private fruitBasket;

    public static loadAssets(game: Phaser.Game)
    {
        game.load.image("scaleBase", "images/mathman/World2/scale/base.png");
        game.load.image("scaleWeight", "images/mathman/World2/scale/scale.png");
        game.load.image("scaleMoney", "images/mathman/World2/scale/money.png");
        game.load.image("scaleBasket", "images/mathman/World2/scale/basket.png");
    }

    constructor(game: Phaser.Game, x: number, y: number)
    {
        super(game, x, y, "scaleBase");
        this.anchor.set(0.5, 0.5);
        this.scaleWeight = this.createWeight();
        this.fruitBasket = this.createFruitBasket(this.scaleWeight);
        this.moneyBag = this.createMoneyBag(this.scaleWeight);
        this.inputEnabled = true;
        this.events.onInputDown.add(this.shakeScale, this);
    }

    private createWeight()
    {

        let weight = new Phaser.Sprite(game, -22, -53, "scaleWeight");
        this.addChild(weight);
        weight.anchor.set(0.5, 0.5);

        return weight;
    }

    private createMoneyBag(parent: Phaser.Sprite)
    {
        let bag = new Phaser.Sprite(game, -this.width, -this.height / 3, "scaleMoney");
        parent.addChild(bag);
        bag.anchor.set(0.5, 0.5);
        bag.scale.multiply(0.7, 0.7);
        bag.alpha = 0;
        return bag;
    }

    private createFruitBasket(parent: Phaser.Sprite)
    {
        let basket = new Phaser.Sprite(game, this.width, -this.height / 2.1, "scaleBasket");
        parent.addChild(basket);
        basket.anchor.set(0.5, 0.5);
        basket.alpha = 0;
        return basket;
    }

    public setBasePosition(x, y)
    {
        this.scaleWeight.position.set(x, y);
    }

    public update()
    {

    }
    public tiltScale(directionIsLeft: boolean)
    {

        if (this.scaleBalance)
            this.scaleBalance.stop();
        if (this.scaleShake)
            this.scaleShake.stop();

        this.scaleWeight.angle = this.oldAngle;

        let targetAngle: number = directionIsLeft ? this.scaleWeight.angle - 15 : this.scaleWeight.angle + 15;

        if (targetAngle > 15)
            targetAngle = 15;

        if (targetAngle < -15)
            targetAngle = -15;

        if (targetAngle === 0)
            this.scaleBalance = game.add.tween(this.scaleWeight).to({angle: targetAngle}, 1500, Phaser.Easing.Elastic.Out, true);

        else
            this.scaleBalance = game.add.tween(this.scaleWeight).to({angle: targetAngle}, 500, Phaser.Easing.Bounce.Out, true);

        this.oldAngle = targetAngle;

    }

    public shakeScale()
    {
        let rnd = (game.rnd.between(-4, 4));
        if (this.scaleBalance){
            if (!this.scaleBalance.isRunning)
            {
                if (this.scaleShake)
                    if (this.scaleShake.isRunning) {
                        this.scaleShake.stop();
                        this.scaleWeight.angle = this.oldAngle;
                }

                this.scaleShake = game.add.tween(this.scaleWeight).to({angle: this.scaleWeight.angle + rnd}, 120, Phaser.Easing.Circular.Out, true, 0, undefined, true);
            }
        }
        else
            {
                if (this.scaleShake)
                    if (this.scaleShake.isRunning) {
                        this.scaleShake.stop();
                        this.scaleWeight.angle = this.oldAngle;
                }

                this.scaleShake = game.add.tween(this.scaleWeight).to({angle: this.scaleWeight.angle + rnd}, 120, Phaser.Easing.Circular.Out, true, 0, undefined, true);

            }

    }

    public showBasket(visible: boolean)
    {
        if (visible)
            game.add.tween(this.fruitBasket).to({alpha: 1}, 500, Phaser.Easing.Exponential.Out, true);

        else
            game.add.tween(this.fruitBasket).to({alpha: 0}, 500, Phaser.Easing.Exponential.Out, true);
    }

    public showMoney(visible: boolean)
    {
        if (visible)
            game.add.tween(this.moneyBag).to({alpha: 1}, 500, Phaser.Easing.Exponential.Out, true);
        else
            game.add.tween(this.moneyBag).to({alpha: 0}, 500, Phaser.Easing.Exponential.Out, true);
    }


}


