import { NumberComponent } from "../numberComponent";
import { PhaserSpriteBehavior, IComponentAbleSprite, InheritableComponentType } from "../PhaserComponent/";
import * as Libs from "Libs/";
import * as MathDAD from "Libs/MathDAD/";
import { INumberObject } from "GameObjects";
import { ExponentiationComponent } from "Components/SymbolComponents/ComplexSymbol/";
import { ICheckNextAble, ICheckNextAbleType } from "../iCheckNextAble";
export { NumberComponent } from "../numberComponent";

export type SymbolText = "+" | "-" | "*" | "/";

export class SymbolComponent extends PhaserSpriteBehavior implements ICheckNextAble
{
    public static readonly componentName: string = "SymbolComponet";
    public static readonly inheritedComponentNames: InheritableComponentType[] = [ICheckNextAbleType];
    public priority = 0;

    protected _symbolName: string = "none";
    public get symbolName() { return this._symbolName; }

    public calc(left?: MathDAD.XNumberValue, right?: MathDAD.XNumberValue): { numberValue: number, isX: boolean }
    { throw "can not use base class : SymbolComponent"; }

    public isCalcValid(left?: MathDAD.XNumberValue, right?: MathDAD.XNumberValue): boolean
    { return true; }

    public getSymbolsNumbers(numberValues: Libs.ValueIndex<INumberObject>[], symbolIndex: number):
        { right?: Libs.ValueIndex<INumberObject>, left?: Libs.ValueIndex<INumberObject> }
    {
        let returnValues: { right?, left?} = {};

        returnValues.right = Libs.
            findMinElement(numberValues.filter(vi => vi.index > symbolIndex),
            i => i.index);

        returnValues.left = Libs.
            findMaxElement(numberValues.filter(vi => vi.index < symbolIndex),
            i => i.index);

        return returnValues;
    }

    public makeGroup?(numberValues: Libs.ValueIndex<INumberObject>[],
        symbolIndex: number): MathDAD.SymbolNumberGroup | void;

    public checkNext(next: IComponentAbleSprite | undefined | null): boolean
    {
        if (!next)
            return false;

        if (next.getComponent(NumberComponent))
        {
            return true;
        }

        if (next.getComponent("RootComponent"))
        {
            return true;
        }

        return false;
    }

    public constructor(sprite: IComponentAbleSprite)
    {
        super(sprite);
    }

}


