import { LevelTracker } from "./levelSelectState";
import { LevelHandler } from "./levelHandler";


export class LevelSelectButton extends Phaser.TileSprite
{
    private index: number;
    private btnText: Phaser.Text;

    constructor(game: Phaser.Game, x: number, y: number, width: number, height: number, key: string, lvlIndex: number, tileIndex: number)
    {
        super(game, x, y, width, height, key);

        this.index = lvlIndex;

        this.inputEnabled = true;

        this.events.onInputDown.add(this.OnButtonPress, this);
        this.events.onInputOver.add(this.OnButtonHover, this);
        this.events.onInputOut.add(this.OnButtonHoverEnd, this);

        this.tilePosition = new Phaser.Point(-width * tileIndex, 0);

        this.btnText = this.game.add.text(this.width * 0.5, this.height * 0.5, (this.index + 1).toString(), {
            font: "32px Arial",
            fill: "#FFFFFF",
            wordWrap: true,
            wordWrapWidth: this.width,
            align: "center",
        });
        this.btnText.anchor.set(0.5);

        this.addChild(this.btnText);
    }

    public OnButtonPress()
    {
        LevelTracker.SetCurrentLevelIndex(this.index);

        // Do something level related here
        LevelHandler.LoadLevel(this.index);
    }

    public OnButtonHover()
    {
        this.tint = 0xD1D1D1;
    }

    public OnButtonHoverEnd()
    {
        this.tint = 0xFFFFFF;
    }
}




