import { IComponentAbleSprite } from "./PhaserComponent/";
import { RectangleOverlapComponent } from "./rectangleOverlapComponent";

export class DropAreaComponent extends RectangleOverlapComponent
{
    public static readonly componentName = "dropAreaComponent";

    private objectInArea: IComponentAbleSprite | null = null;
    private objectInAreaStatus: ObjectInAreaStatusEnum = ObjectInAreaStatusEnum.noObject;

    public readonly events = {
        onNoObjectInArea: new Phaser.Signal(),
        onAreaReserved: new Phaser.Signal(),
        onObjectMovingTowardsArea: new Phaser.Signal(),
        onObjectInArea: new Phaser.Signal(),
        onObjectStatusChange: new Phaser.Signal()
    };

    public get getObjectInArea(): IComponentAbleSprite | null
    {
        return this.objectInArea;
    }
    public get getObjectInAreaStatus(): ObjectInAreaStatusEnum
    {
        return this.objectInAreaStatus;
    }

    public SetObjectInArea(objectInArea: IComponentAbleSprite | null,
        objectInAreaStatus: ObjectInAreaStatusEnum): boolean
    {

        if (this.objectInArea !== null && this.objectInArea !== objectInArea
            && objectInAreaStatus !== ObjectInAreaStatusEnum.noObject)
        {
            return false;
        }

        let oldObjectInAreaStatus = this.objectInAreaStatus;

        switch (objectInAreaStatus)
        {
            case ObjectInAreaStatusEnum.noObject:
                this.objectInArea = null;
                this.objectInAreaStatus = objectInAreaStatus;
                this.events.onNoObjectInArea.dispatch();
                break;

            case ObjectInAreaStatusEnum.reservedArea:
                this.objectInArea = objectInArea;
                this.objectInAreaStatus = objectInAreaStatus;
                this.events.onAreaReserved.dispatch(objectInArea);
                break;

            case ObjectInAreaStatusEnum.movingTowardsArea:
                this.objectInArea = objectInArea;
                this.objectInAreaStatus = objectInAreaStatus;
                this.events.onObjectMovingTowardsArea.dispatch(objectInArea);
                break;

            case ObjectInAreaStatusEnum.inArea:
                this.objectInArea = objectInArea;
                this.objectInAreaStatus = objectInAreaStatus;
                this.events.onObjectInArea.dispatch(objectInArea);
                break;
        }

        this.events.onObjectStatusChange.
            dispatch(objectInArea, this.objectInAreaStatus, oldObjectInAreaStatus);

        return true;

    }

}

export enum ObjectInAreaStatusEnum
{
    noObject = 0, reservedArea = 1, movingTowardsArea = 2, inArea = 3
}


