import { DragAndDropComponent } from "./dragAndDropComponent";
import { IComponentAbleSprite } from "./PhaserComponent/";
import { switchHomePointTween, goHomeTween } from "Tweens/numberObjectTweens";
import { NumberManager } from "Managers/";

export { DragAndDropComponent } from "./dragAndDropComponent";

export class DADHomeComponent extends DragAndDropComponent
    implements NumberManager.INumberMangerComponent
{
    public static readonly componentName: string = "DadHomeComponent";
    public static readonly inheritedComponentNames = [
        DragAndDropComponent, NumberManager.INumberMangerComponentType];

    private _currentHomeNumber: number = 0;
    private _useHomePoint: boolean = true;
    private _movePoint: { x: number, y: number };
    private _currentHomePosition: NumberManager.HomePosition;

    public get getMovePoint(): { x: number, y: number }
    {
        return this._movePoint;
    }

    public get getCurrentHomeNumber(): number
    {
        return this._currentHomeNumber;
    }

    public get getUseHomePoint(): boolean
    {
        return this._useHomePoint;
    }

    private _getHomePoint(): { x: number, y: number }
    {
        if (!this._useHomePoint)
        {
            let { x, y, index } = NumberManager.registerNumberObjectComponent(this);
            this._useHomePoint = true;
            this.changeHomeNumber(index, x, y, false);
        }
        return this.getMovePoint;
    }

    protected notDropInArea()
    {
        this._moveToPoint = this._getHomePoint();
        this._beginTweenMoveHome();
    }

    public moveHome(tweenCompleteCallback?: Function, listenerContext?: any)
    {
        this._dropAreaUsing = null;
        this._moveToPoint = this._getHomePoint();
        this._beginTweenMoveHome(tweenCompleteCallback, listenerContext);
    }

    private _beginTweenMoveHome(tweenCompleteCallback?: Function, listenerContext?: any): void
    {
        if (this._moveTween)
            this._moveTween.stop();
        if (!this._moveTween)
            this._moveTween = goHomeTween(this.sprite, this._moveToPoint);
        else
        {
            let oldTween = this._moveTween;
            this._moveTween = goHomeTween(this.sprite, this._moveToPoint);
            this._moveTween.onChildComplete = oldTween.onChildComplete;
            this._moveTween.onComplete = oldTween.onComplete;
            this._moveTween.onLoop = oldTween.onLoop;
            this._moveTween.onRepeat = oldTween.onRepeat;
            this._moveTween.onStart = oldTween.onStart;
        }
        if (tweenCompleteCallback)
        {
            this._moveTween.onComplete.addOnce(tweenCompleteCallback, listenerContext);
        }

        this._moveTween.start();
    }

    public get currentHomePosition()
    {
        return this._currentHomePosition;
    }

    public set currentHomePosition(value: NumberManager.HomePosition)
    {
        this._currentHomePosition = value;
        let { x, y, index } = NumberManager.registerNumberObjectComponentAt(this, value.index);
        this._currentHomeNumber = index;
        this._movePoint = { x, y };
    }

    public changeHomeNumber(newNumber: number, newPositionX: number, newPositionY, move: boolean = true): void
    {
        this._currentHomeNumber = newNumber;
        this._movePoint = { x: newPositionX, y: newPositionY };
        if (move && !this.getIsBeginDragged && this._useHomePoint)
            this.moveToNewPos(newPositionX, newPositionY);
    }

    protected moveToNewPos(x: number, y: number): void
    {
        if (this._moveTween)
            this._moveTween.stop(false);
        if (!this._moveTween)
            this._moveTween = switchHomePointTween(this.sprite, { x, y });
        else
        {
            let oldTween = this._moveTween;
            this._moveTween = switchHomePointTween(this.sprite, { x, y });
            this._moveTween.onChildComplete = oldTween.onChildComplete;
            this._moveTween.onComplete = oldTween.onComplete;
            this._moveTween.onLoop = oldTween.onLoop;
            this._moveTween.onRepeat = oldTween.onRepeat;
            this._moveTween.onStart = oldTween.onStart;
        }

        this._moveTween.start();
    }

    private _eventOnMoveTowardsDropArea()
    {
        this._useHomePoint = false;

        NumberManager.removeNumberObjectComponent(this);
    }


    constructor(sprite: IComponentAbleSprite, layer: string | string[],
        overlapLayers: string[], dropHitBox?: Phaser.Rectangle)
    {
        super(sprite, layer, overlapLayers, dropHitBox);
        this.events.onMoveTowardsDropArea.add(this._eventOnMoveTowardsDropArea, this);
        this._currentHomePosition = NumberManager.registerNumberObjectComponent(this);
        let { x, y, index } = this._currentHomePosition;
        this._currentHomeNumber = index;
        this._movePoint = { x, y };
        this.sprite.position.set(x, y);
    }
}


