
let defaultDivId = "content";

export function setDefaultCustomCursorDivId(newDefaultDivId: string)
{
    defaultDivId = newDefaultDivId;
}

export function setCustomCursor(cursorUrl: string, divId: string)
{
    let div = document.getElementById(divId);
    if (div)
    {
        div.style.cursor = `url(${cursorUrl}), auto`;
    }
}


