import { IComponentAbleSprite } from "./PhaserComponent/";
import { addOnJsonUpdate, jsonGlobalObject, removeOnJsonUpdate } from "Managers/globalSettingsManger";
import { RectangleOverlapComponent } from "./rectangleOverlapComponent";
import { events as stateEvent } from "States/Events/mainStateEvent";
import { MainState } from "States/mainState";

export const startingAreaLayer = "StartingAreaLayer";

export const startingObjectLayer = "StartingAreaObjectsLayer";


export class StartingAreaComponent extends RectangleOverlapComponent
{
    public static readonly componentName = "StartingAreaComponent";
    public events = {
        numberObjectExit: new Phaser.Signal
    };

    public onOverlapExit(others: IComponentAbleSprite[])
    {
        this.events.numberObjectExit.dispatch(others);
    }

    public render(state: MainState)
    {
        if (jsonGlobalObject.debugDraw)
            state.game.debug.geom(this.getOverlapRectangle(), "red", false);
    }

    private _updateRectangle(jsonGlobalObject: globalJson.RootObject)
    {
        this.overlapRectangleOffSet.set(jsonGlobalObject.startingArea.x, jsonGlobalObject.startingArea.y);
        this.overlapRectangle.width = jsonGlobalObject.startingArea.width;
        this.overlapRectangle.height = jsonGlobalObject.startingArea.height;
    }


    public onRemove()
    {
        removeOnJsonUpdate(this);
    }

    constructor(sprite: IComponentAbleSprite)
    {
        super(sprite, startingAreaLayer, [startingObjectLayer],
            new Phaser.Rectangle(jsonGlobalObject.startingArea.x, jsonGlobalObject.startingArea.y,
                jsonGlobalObject.startingArea.width, jsonGlobalObject.startingArea.height));

        this.receiveOverlapEvent = true;

        stateEvent.onRender.add(this.render, this);
        addOnJsonUpdate(this, this._updateRectangle, this);
    }
}


