import { MainState } from "../mainState";

type StateFunctionEvent = (state: MainState) => void;

export const events = {
    onLoad:
    {
        add: (listener: StateFunctionEvent, context?: object) =>
        {
            funcsOnLoad.push({ listener, context });
        },
        remove: (listener: StateFunctionEvent) =>
        {
            funcsOnLoad = funcsOnLoad.filter(a => a.listener !== listener);
        }
    },
    onCreate:
    {
        add: (listener: StateFunctionEvent, context?: object) =>
        {
            funcsOnCreate.push({ listener, context });
        },
        remove: (listener: StateFunctionEvent) =>
        {
            funcsOnCreate = funcsOnCreate.filter(a => a.listener !== listener);
        }
    },
    onUpdate:
    {
        add: (listener: StateFunctionEvent, context?: object) =>
        {
            funcsOnUpdate.push({ listener, context });
        },
        remove: (listener: StateFunctionEvent) =>
        {
            funcsOnUpdate = funcsOnUpdate.filter(a => a.listener !== listener);
        }
    },
    onShutdown:
    {
        add: (listener: StateFunctionEvent, context?: object) =>
        {
            funcsOnShutdown.push({ listener, context });
        },
        remove: (listener: StateFunctionEvent) =>
        {
            funcsOnShutdown = funcsOnShutdown.filter(a => a.listener !== listener);
        }
    },
    onRender:
    {
        add: (listener: StateFunctionEvent, context?: object) =>
        {
            funcsOnRender.push({ listener, context });
        },
        remove: (listener: StateFunctionEvent) =>
        {
            funcsOnRender = funcsOnShutdown.filter(a => a.listener !== listener);
        }
    },
    onLevelWon:
    {
        add: (listener: StateFunctionEvent, context?: object) =>
        {
            funcsOnLevelWon.push({ listener, context });
        },
        remove: (listener: StateFunctionEvent) =>
        {
            funcsOnLevelWon = funcsOnLevelWon.filter(a => a.listener !== listener);
        }
    }
};

let funcsOnLoad = new Array<ListenerAndContext>();
let funcsOnCreate = new Array<ListenerAndContext>();
let funcsOnUpdate = new Array<ListenerAndContext>();
let funcsOnShutdown = new Array<ListenerAndContext>();
let funcsOnRender = new Array<ListenerAndContext>();
let funcsOnLevelWon = new Array<ListenerAndContext>();


type ListenerAndContext = { listener: StateFunctionEvent, context?: object };


export function runOnLoad(state: MainState)
{
    funcsOnLoad.forEach(f => f.listener.call(f.context, state));
}

export function runOnCreate(state: MainState)
{
    funcsOnCreate.forEach(f => f.listener.call(f.context, state));
}

export function runOnUpdate(state: MainState)
{
    funcsOnUpdate.forEach(f => f.listener.call(f.context, state));
}

export function runOnShutdown(state: MainState)
{
    funcsOnShutdown.forEach(f => f.listener.call(f.context, state));
}

export function runOnRender(state: MainState)
{
    funcsOnRender.forEach(f => f.listener.call(f.context, state));
}

export function runOnLevelWon(state: MainState)
{
    funcsOnLevelWon.forEach(f => f.listener.call(f.context, state));
}


